<template>
    <div class="boot-page" >
        <!--著作权合同备案-->
        <div class="contractRecord">
            <div class="div-dataList">
              <table>
                <tr>
                    <th class="firstTh">序号</th>
                    <th class="secondTh">版权作品标题</th>
                    <th class="thirdTh">版本号</th>
                    <th class="fourthTh">作品分类</th>
                    <th class="fifthTh">登记日期</th>
                </tr>
                <tr v-for="(item,index) in contractRecord">
                    <td class="firstTd">{{index+1}}</td>
                    <td  class="secondTd" @click="showPopup(item.id)">
                            {{item.worksTitle}}
                    </td>
                    <td>
                            {{item.versionNum}}
                    </td>
                    <td>
                            {{item.worksType}}
                    </td>
                    <td  class="lastTd">
                            {{item.regDate}}
                    </td>
                </tr>
              </table>
            </div>
                 <div class="div-pager" v-if="this.totalRecord>this.pageSize">
                     <el-pagination
                             @size-change="handleSizeChange"
                             @current-change="handleCurrentChange"
                             :current-page.sync="this.currentPage"
                             :page-size="this.pageSize"
                             layout="prev, pager, next, jumper"
                             :total="this.totalRecord">
                     </el-pagination>
                 </div>
        </div>

        <!--弹框-->
        <el-dialog  :visible.sync="dialogVisible">
            <p class="title">著作权合同备案</p>
            <!--这里写自己的内容-->
            <div class="dialog-con">
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">作品标题：</span>
                    </h4>
                    <p class="item-value"><span>{{contractList.worksTitle}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">作品分类：</span>
                    </h4>
                    <p class="item-value"><span>{{contractList.worksType}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">版本号：</span>
                    </h4>
                    <p class="item-value"><span>{{contractList.versionNum}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">权利人：</span>
                    </h4>
                    <p class="item-value"><span>{{contractList.obligee}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">地域范围：</span>
                    </h4>
                    <p class="item-value"><span>{{contractList.palceRange}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">合同开始时间：</span>
                    </h4>
                    <p class="item-value"><span>{{contractList.startDate}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">合同到期时间：</span>
                    </h4>
                    <p class="item-value"><span>{{contractList.expirationDate}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">永久转让或授权：</span>
                    </h4>
                    <p class="item-value"><span>{{contractList.authorization}} </span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">所属类：</span>
                    </h4>
                    <p class="item-value"><span>{{contractList.genericClass}} </span></p>
                </div>
            </div>
        </el-dialog>
    </div>
</template>

<script>
    import * as api from '@/assets/js/api.js'
    export default {
        name: 'contractRecordList',
        data () {
            return {
                dialogVisible: false,
                totalRecord:100,
                pageSize:50,
                currentPage:1,
                contractRecord:[
//                    {
//                        "worksTitle":"资产管理平台—资产目录子系统",
//                        "versionNumber":"V1.0",
//                        "worksType":"软件",
//                        "regDate":"2011-11-11"
//                    },
//                    {
//                        "worksTitle":"资产管理平台—资产目录子系统",
//                        "versionNumber":"V1.0",
//                        "worksType":"软件",
//                        "regDate":"2011-11-11"
//                    },
                ],
                contractList:{
//                    "worksTitle":"资产管理平台-资源目录子系统",
//                    "worksType" :"软件",
//                    "versionNum":"V1.0",
//                    "obligee":"北京XX科技有限责任公司",
//                    "palceRange":"2010-7-1",
//                    "startDate":"2010-7-1",
//                    "expirationDate":"2010-7-1",
//                    "authorization":"转让",
//                    "genericClass":"软件"
                }
            }
        },
        mounted(){
            api.Get('/cloudMap/inAssetsInfo/getContractRecordList',{page:1,pageSize:this.pageSize,entId:1,entName:this.$router.currentRoute.params.entName}).then(res=>{
                this.contractRecord=res.data;
                this.totalRecord=res.totalRecord;
                //console.log(res);
            });
        },
        methods:{
            showPopup(id){
                this.dialogVisible = true;
                   api.Get('/cloudMap/inAssetsInfo/getContractRecordInfo', {id: id, entId: 1, entName:this.$router.currentRoute.params.entName}).then(res => {
                       this.contractList = res.data;
                       //console.log(id);
                       //console.log(res);
                   });
            },
            handleSizeChange(val) {
                console.log(`每页 ${val} 条`);
            },
            handleCurrentChange(val) {
                console.log(`当前页: ${val}`);
                api.Get('/cloudMap/inAssetsInfo/getContractRecordList',{entId:1,entName:this.$router.currentRoute.params.entName,page:val,pageSize:this.pageSize}).then(res=>{
                    this.contractRecord=res.data;
                    this.totalRecord=res.totalRecord;
                    console.log(res);
                });
            },
        },
        components:{

        }
    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    @import 'assets/css/common.css';
    @import 'assets/css/tableStyle.css';
    .contractRecord .firstTh{
        width:10%;
    }
    .contractRecord .secondTh{
        width:40%;
    }
    .contractRecord .thirdTh{
        width:15%;
    }
    .contractRecord .fourthTh{
        width:20%;
    }
    .contractRecord .fifthTh{
        width:15%;
    }
    .secondTd{
        text-decoration: underline;
        cursor: pointer;
    }
</style>



