<template>
    <div id="nav" class="nav">
        <h3>{{title}}</h3>
        <div class="nav-list" ref='wrap'>
            <span class="move-ico aleft" @click="animT(-1)">&lt;</span>
            <ul ref="ul">
                <li v-for="(item,index) in list" key='index' @click='liClick(cMenu,item,index)'
                    ref="index">
                    {{item}}
                </li>
            </ul>
            <span class="move-ico aright" @click="animT(1)">&gt;</span>
        </div>
        <!--<div class="border-wrap"></div>-->
    </div>
</template>

<script>
    export default {
        name: 'infoList',
        props: ['cMenu','list','currentTitle'],
        data(){
            return {
                title: '',
                currentIndex: 0,
                initIndex: 3,
                width: '',
                arrow: -1
            }
        },
        mounted(){
            console.log(this.$router.name)
//            this.title = this.currentTitle? this.currentTitle: (this.list.length > 0 ? this.list[0] : '')
            const div = this.$refs.index[0];
            this.width = div.style.width || div.clientWidth || div.offsetWidth || div.scrollWidth;
            this.$refs.ul.style.width = this.list.length * this.width + 'px'
            this.$refs.ul.style.marginLeft = this.initIndex * this.width + 'px'
//            this.setClass(0)
            this.setClass(this.currentTitle.itemIndex)
            this.liClick(this.cMenu,this.currentTitle.itemTitle,this.currentTitle.itemIndex)
        },
        methods: {
            animT(i){
                this.arrow = i
                if (this.initIndex * i + 4 >= this.list.length) return false;
                if (this.initIndex * i == 3 && i > 0) return false;
                this.initIndex += i
                this.currentIndex += -i
                this.setClass(this.currentIndex)
                this.$refs.ul.style.marginLeft = this.initIndex * this.width + 'px'
            },
            setClass(i){
                for (var k = 0; k < this.list.length; k++) {
                    this.$refs.index[k].className = ''
                }
                this.$refs.index[i].className = 'active'
                if (i + 1 <= this.list.length-1) {
                    this.$refs.index[i + 1].className = 'size1'
                }
                if (i - 1 >= 0) {
                    this.$refs.index[i - 1].className = 'size1'
                }
                if (i - 2 >= 0) {
                    this.$refs.index[i - 2].className = 'size2'
                }
                if (i + 2 <= this.list.length-1) {
                    this.$refs.index[i + 2].className = 'size2'
                }
            },
            liClick(menu,item, i){
                this.title = item
                var cz = this.currentIndex - i
                this.currentIndex = i
                this.initIndex += cz
                this.setClass(i)
                this.$refs.ul.style.marginLeft = this.initIndex * this.width + 'px'
                this.$emit('getList',[item])
            }
        }
    }
</script>

<style scoped>
    h3 {
        text-align: center;
        font-size: 26px;
        height: 40px;
        line-height: 40px;
        color: #fff;
        font-weight: bold;
    }
    .nav{
        position: relative;
        width: 100%;
        height: 124px;
        flex: none;
        background: url("../../static/img/info-list-bg.png") no-repeat;
        background-size: contain;
        background-position: center bottom;
        display: flex;
        flex-direction: column;
        justify-content: center;
        overflow: hidden;
    }
    .border-wrap{
        position: absolute;
        bottom: 0;
        left: 0;
        right: 0;
        height: 1px;
        background: linear-gradient(to right,#112642 0%,#06adfb 50%,#112642 100%);
        border-radius: 50%;
        box-shadow: 0 0 50px 0 #093b8d;
    }
    .move-ico{
        position: absolute;
        top: 0;
        bottom: 0;
        display: inline-block;
        cursor: pointer;
        color: #086298;
        font-size: 30px;
        width: 30px;
        z-index: 2;
    }
    .move-ico:hover{
        color: #1095ed;
    }
    .nav-list .aleft {
        text-align: left;
        left: 0;
        background: linear-gradient(to left , rgba(18,25,51,.2),rgba(18,25,51,1));
    }
    .nav-list .aright {
        text-align: right;
        right: 0;
        background: linear-gradient(to right ,rgba(18,25,51,.2),rgba(18,25,51,1));
    }
    .nav-list {
        position: relative;
        box-sizing: border-box;
        height: 40px;
        line-height: 40px;
        padding: 0 10px;
        width: 100%;
        overflow: hidden;
    }

    .nav-list ul {
        transition: all 1s;
        display: block;
        overflow: hidden;
        height: 40px;
    }
    .nav-list li {
        transition: all 2s;
        display: inline-block;
        color: #b7bcc5;
        height: 100%;
        width: calc(((100vw * .6) - 100px) / 7);
        float: left;
        text-align: center;
        box-sizing: border-box;
        font-size: 16px;
        transform: scale(.6) translate3D(0, 0, 0);
        cursor: pointer;
    }

    .nav-list li.size2 {
        transform: scale(.8);
    }

    .nav-list li.size1 {
        transform: scale(.9);
    }

    .nav-list li.active {
        color: #24416f;
        transform: scale(1);

    }
</style>



