<template>
    <div class="financecbrc">
        <!--银监会行政处罚-->
        <div class="overflowAuto">
            <table class="cbrc-table">
                <thead class="cbrc-thead">
                <tr>
                    <th>序号</th>
                    <th>决定文书</th>
                    <th>主要违法违规事实（案由）</th>
                    <th>行政处罚决定</th>
                    <th>处罚决定日期</th>
                </tr>
                </thead>
                <tbody class="cbrc-tbody">
                <tr v-for="(item,index) in datalist">
                    <td>{{index+1}}<span class="cbrc-span-right"></span></td>
                    <td class="circ-td-a"><b  @click="showPopup(item.id)">{{item.decDocument}}</b></td>
                    <td class="cbrc-td"><span class="cbrc-span-left"></span>{{item.illegalFacts}}</td>
                    <td class="cbrc-td"><span class="cbrc-span-left"></span>{{item.admPenalDecision}}</td>
                    <td ><span class="cbrc-span-left"></span>{{item.decisionDate}}</td>
                </tr>
                </tbody>
            </table>
        </div>

        <!--分页-->
        <div class="pageWrap"  v-if="this.totalRecord>this.pageSize">
            <el-pagination
                    @current-change="handleCurrentChange"
                    :current-page.sync="this.currentPage"
                    :page-size="this.pageSize"
                    layout="prev, pager, next, jumper"
                    :total="this.totalRecord">
            </el-pagination>
        </div>

        <!--弹框-->
        <el-dialog  :visible.sync="dialogVisible">
            <p class="title">银监会行政处罚详情</p>
            <!--这里写自己的内容-->
            <div class="dialog-con">
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">项目名称：</span>
                    </h4>
                    <p class="item-value"><span>{{sanction.productName}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">行政决定部门：</span>
                    </h4>
                    <p class="item-value"><span>{{sanction.admDecisDepartment}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">行政决定类别：</span>
                    </h4>
                    <p class="item-value"><span>{{sanction.admDecisCategory}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">设定依据：</span>
                    </h4>
                    <p class="item-value"><span>{{sanction.setBasis}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">行政相对人类别：</span>
                    </h4>
                    <p class="item-value"><span>{{sanction.admCategory}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">行政处罚决定书文号：</span>
                    </h4>
                    <p class="item-value"><span>{{sanction.penaltyNum}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">被处罚当事人姓名或名称：</span>
                    </h4>
                    <p class="item-value"><span>{{sanction.bepunishedName}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">主要违法违规事实（案由）：</span>
                    </h4>
                    <p class="item-value"><span>{{sanction.violationFacts}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">行政处罚依据：</span>
                    </h4>
                    <p class="item-value"><span>{{sanction.punishmentBasis}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">行政处罚决定：</span>
                    </h4>
                    <p class="item-value"><span>{{sanction.penaltyDecision}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                            <span class="titleBarName fl">作出处罚决定的机关名称：</span>
                    </h4>
                    <p class="item-value"><span>{{sanction.organizationName}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">作出处罚决定的日期：</span>
                    </h4>
                    <p class="item-value"><span>{{sanction.punishDate}}</span></p>
                </div>
            </div>
        </el-dialog>

    </div>
</template>

<script>
    import * as api from '@/assets/js/api'
    export default {
        name: 'details',
        data () {
            return {
                pageSize:50,
                currentPage:1,
                totalRecord:'',
                dialogVisible: false,
                datalist: [],
                sanction:{}
            }
        },
        methods:{
            handleCurrentChange(val) {//分页
                console.log(`当前页: ${val}`);
                this.getList({
                    entId:1,
                    entName:this.$router.currentRoute.params.entName,
                    page:val,
                    pageSize:this.pageSize
                })
            },
            showPopup(cbrcPunishId){//模态框
                this.dialogVisible = true;
                api.Get('/cloudMap/finance/getFinanceDetails',{
                    entId:1,
                    entName:this.$router.currentRoute.params.entName,
                    cbrcPunishId: cbrcPunishId
                }).then((res)=>{
                    console.log("模态框");
                    console.log(res);
                    this.sanction =res.data;
                })
            },
            getList(params){//列表
                api.Get('/cloudMap/finance/getFinanceList',params).then((res)=>{
                    console.log("银监会行政处罚");
                    console.log(res);
                    this.datalist =res.data;
                    this.totalRecord=res.totalRecord;
                })
            }
        },
        mounted(){
            console.log("银监会行政处罚");
            this.getList({//列表
                entId:1,
                entName:this.$router.currentRoute.params.entName,
                page:1,
                pageSize:this.pageSize
            })
        }
    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    /* 弹窗样式 */
    .dialog-con{
        max-height:600px;
        overflow:auto;
        overflow-x:hidden;
    }
    .dialog-con:hover::-webkit-scrollbar-thumb{
        background:rgba(7,100,180,1);
    }
    .dialog-item{
        color:#fff;
        margin-left:26px;
        margin-bottom:28px;
        margin-right: 30px;
    }
    .dialog-item .item-name{
        margin-bottom:8px;
    }
    .dialog-item .item-name span{
        height:15px;
        line-height:15px;
        font-size: 12px;
    }
    .dialog-item .item-name i{
        margin-left:1px;
        margin-right:6px;
        height:15px;
        width:2px;
        background:#00c2ff;
    }
    .dialog-item .item-value{
        display:block;
        word-wrap: break-word;
    }
    .financecbrc{
        width: 100%;
        height: 100%;
    }
    .cbrc-table{
        width: 100%;
        border-collapse:separate;
        border-spacing:0 4px;
    }
    .cbrc-thead th{
        height:34px;
        line-height: 34px;
        font-size: 12px;
        font-weight: bold;
        text-align: left;
        padding:0 10px;
        background: rgba(40,92,198,0.35);
        color: #cdd2de;
        border-top: 1px solid #284f9d;
        border-bottom: 1px solid #284f9d;
    }
    .cbrc-thead th:first-child{
        width: 65px;
        text-align: center;
        border-left: 1px solid #284f9d;
    }
    .cbrc-thead th:last-child{
        width: 14%;
        border-right: 1px solid #284f9d;
    }
    .cbrc-thead th:nth-child(3){
        padding-left:30px;
    }
    .cbrc-thead th:nth-child(4){
        padding-left:30px;
    }
    .cbrc-tbody td{
        font-size: 12px;
        min-height:34px;
        color: #fff;
        text-align: left;
        vertical-align: top;
        padding-top: 20px;
        background: rgba(88,143,255,0.15);
        border-top:1px solid rgba(99,150,255,0.35);
        border-bottom:1px solid rgba(99,150,255,0.35);
    }
    .cbrc-tbody td:first-child{
        width: 65px;
        text-align: center;
    }
    .cbrc-tbody tr td:first-child{
        border-left:1px solid rgba(99,150,255,0.35);
    }
    .cbrc-tbody tr td:last-child{
        width: 115px;
        border-right:1px solid rgba(99,150,255,0.35);
    }
    .cbrc-tbody td .cbrc-span-right{
        width:1px;
        height:25px;
        background: #214c9a;
        float: right;
        margin-right:15px;
    }
    .cbrc-span-left{
        width:1px;
        height:25px;
        float: left;
        background: #214c9a;
        margin-right:15px;
    }
    .cbrc-tbody td b{
        color: #fff;
        text-decoration: underline;
        cursor: pointer;
    }
    .cbrc-td{
        padding:15px 15px 25px;
    }
    .circ-td-a a{
        color: #00FFFF;
    }
    .cbrc-tbody tr:hover {
        border:1px solid #2751a2;
        background:rgba(40,92,198,0.35);
        color:#cdd2de;
    }
    .overflowAuto{
        box-sizing: border-box;
        flex: auto;
        overflow: auto;
        overflow-x: hidden;
        height:424px;
        padding-right: 5px;
    }
    .overflowAuto:hover::-webkit-scrollbar-thumb{
        background:rgba(7,100,180,1)
    }
    .pageWrap{
        margin-top:10px;
    }
</style>



