<template>
    <div class="boot-page">
        <table class="court-table">
            <thead>
            <tr>
                <th class="tc w50">序号</th>
                <th>受理号</th>
                <th>药品名称</th>
                <th>专利类型</th>
                <th class="w220">专利到期日或专利授权/公开日期</th>
            </tr>
            </thead>
            <tbody>
            <tr v-for="(list,index) in datalist">
                <td class="tc">{{index+1}}</td>
                <td>
                    <a @click="showPopup(list.id)">{{list.acceptanceNum}}</a>
                </td>
                <td>{{list.drugName}}</td>
                <td>{{list.patentType}}</td>
                <td>{{list.paMaturityDate}}</td>
            </tr>
            </tbody>
        </table>

        <!--弹框-->
        <el-dialog  :visible.sync="dialogVisible">
            <p class="title">{{title}}</p>
            <!--这里写自己的内容-->
            <div class="dialog-con">
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">受理号：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.acceptanceNum}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">药品名称：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.drugName}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">申请人：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.applicant}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">专利（类型）：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.patentType}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">专利名称：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.patentName}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">邮编：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.postcode}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">专利到期日或专利授权/公开日期：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.paMaturityDate}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">外国专利：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.foreignPatent}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">外国专利人：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.foPatentHolder}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">申请人联系地址：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.applicantAddress}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">专利人：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.patentHolder}}</span></p>
                </div>
            </div>
        </el-dialog>
    </div>
</template>

<script type="es6">
    import * as api from '@/assets/js/api.js'
    export default {
        name: 'drugPatents',
        data () {
            return {
                dialogVisible: false,
                title:'进口药品商品名',
                datalist: [],
                dataDetails: []
            }
        },
        mounted(){
            api.Get("/cloudMap/fda/getDrugPatentsList",{
                "entName": this.$router.currentRoute.params.entName
            })
                .then(res=>{
                    console.log('请求成功返回的结果'+res.code);
                    this.datalist = res.data;
                })
                .catch(err=>{
                    console.log('请求失败返回的错误信息');
                })
        },
        methods:{
            showPopup(id){
                api.Get('/cloudMap/fda/getDrugPatentsDetails',{
                    "entName": this.$router.currentRoute.params.entName,
                    "patId":id
                })
                    .then(res=>{
                        console.log('请求成功返回的结果'+res.code);
                        this.dataDetails = res.data;
                    })
                    .catch(err=>{
                        console.log('请求失败返回的错误信息');
                    })
                this.dialogVisible = true;
            }
        }
    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    @import "../assets/css/tableCon.css";
    @import "../assets/css/dialog.css";
</style>



