<template>
    <div class="boot-page">
        <ul class="clearfix foodLcence-list">
            <li v-for="list in datalist"><a @click="showPopup(list.id,list.licenseName)">
                <h4 class="foodLcence-title">{{list.licenseName}}</h4>
                <p><span>许可证编号：<em>{{list.licenseNo}}</em></span></p>
                <p><span>有效期至: <em>{{list.termOfValidity}}</em></span></p>
            </a></li>
        </ul>

        <!--弹框-->
        <el-dialog  :visible.sync="dialogVisible">
            <p class="title">{{title}}</p>
            <!--这里写自己的内容-->
            <div class="dialog-con">
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">生产者名称：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.producerName}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">社会信用代码（身份证号码）：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.societyCode}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">法定代表人（负责人）：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.legalRepresentative}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">住所：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.residence}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">生产地址：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.productAddress}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">食品类别：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.foodCategory}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">许可证编号：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.licenseNo}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">日常监督管理机构：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.suAgency}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">日常监督管理人员：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.suAdmin}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">发证机关：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.issuingAuthority}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">签发人：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.issuer}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">发证日期：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.dateOfIssue}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">有效期至：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.termOfValidity}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">许可明细：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.licenseDetails}}</span></p>
                </div>
            </div>
        </el-dialog>
    </div>
</template>

<script type="es6">
    import * as api from '@/assets/js/api.js'
    export default {
        name: 'foodLcence',
        data () {
            return {
                dialogVisible: false,
                title:'食品生产许可获证企业（SC）',
                datalist: [],
                dataDetails: []
            }
        },
        mounted(){
            api.Get("/cloudMap/fda/getFoodLcenceList",{
                "entName": this.$router.currentRoute.params.entName
            })
                .then(res=>{
                    console.log('请求成功返回的结果'+res.code);
                    this.datalist = res.data;
                })
                .catch(err=>{
                    console.log('请求失败返回的错误信息');
                })
        },
        methods:{
            showPopup(id,title){
                api.Get('/cloudMap/fda/getFoodLcenceDetails',{
                    "entName": this.$router.currentRoute.params.entName,
                    "flId":id
                })
                    .then(res=>{
                        console.log('请求成功返回的结果'+res.code);
                        this.dataDetails = res.data;
                        this.title = title;
                    })
                    .catch(err=>{
                        console.log('请求失败返回的错误信息');
                    })
                this.dialogVisible = true;
            }
        }
    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    @import "./assets/css/dialog.css";
    .boot-page{
        width:100%;
        height:100%;
    }
    .foodLcence-list li{
        display:inline-block;
        width:33%;
    }
    .foodLcence-list li a{
        display:block;
        margin:5px 5px 0 0;
        height:118px;
        border:1px solid rgba(99,150,255,0.25);
        background:rgba(88,143,255,0.15);
        color:#fff;
        text-decoration:none;
        padding-left:40px;
        padding-right: 20px;
        -webkit-transition: box-shadow 0.3s;
        -moz-transition: box-shadow 0.3s;
        -ms-transition: box-shadow 0.3s;
        -o-transition: box-shadow 0.3s;
        transition: box-shadow 0.3s;
        cursor:pointer;
    }
    .foodLcence-list li a:hover{
        -webkit-box-shadow: 0 0 10px 0 rgba(99,150,255,0.4);
        -moz-box-shadow: 0 0 10px 0 rgba(99,150,255,0.4);
        box-shadow: 0 0 10px 0 rgba(99,150,255,0.4);
        -webkit-transition: box-shadow 0.3s;
        -moz-transition: box-shadow 0.3s;
        -ms-transition: box-shadow 0.3s;
        -o-transition: box-shadow 0.3s;
        transition: box-shadow 0.3s;
    }
    .foodLcence-list .foodLcence-title{
        width:100%;
        line-height:27px;
        font-size:14px;
        font-weight:500;
        margin-top:24px;
        overflow:hidden;
        text-overflow:ellipsis;
        white-space: nowrap;
    }
    .foodLcence-list p{
        line-height:22px;
        font-size:12px;
        color:#c6c6c6;
    }
    .foodLcence-list p em{
        color:#fff;
    }
</style>



