<template>
    <div class="customsimported">
        <!--海关进出口信息-->
        <div class="overflowAuto">
            <table class="imported-table">
                <thead class="imported-thead">
                <tr>
                    <th>序号</th>
                    <th>海关编码</th>
                    <th>月度</th>
                    <th>商品名称</th>
                    <th>收发货地</th>
                    <th>金额(USD)</th>
                </tr>
                </thead>
                <tbody class="imported-tbody">
                <tr v-for="(item,index) in datalist">
                    <td>{{index+1}} <span></span></td>
                    <td class="customs-num">{{item.customCode}}<span></span></td>
                    <td class="monthly">{{item.monthly}}<span></span></td>
                    <td class="imported-td"><b @click="showPopup(item.id)">{{item.commodityName}}</b> <span></span></td>
                    <td>{{item.deliveryPlace}}<span></span></td>
                    <td>{{item.usdAmount }}</td>
                </tr>
                </tbody>
            </table>
        </div>

        <!--分页-->
        <div class="pageWrap"  v-if="this.totalRecord>this.pageSize">
            <el-pagination
                    @current-change="handleCurrentChange"
                    :current-page.sync="this.currentPage"
                    :page-size="this.pageSize"
                    layout="prev, pager, next, jumper"
                    :total="this.totalRecord">
            </el-pagination>
        </div>

        <!--弹框-->
        <el-dialog  :visible.sync="dialogVisible">
            <p class="title">海关进出品详情</p>
            <!--这里写自己的内容-->
            <div class="dialog-con">
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">海关编码：</span>
                    </h4>
                    <p class="item-value"><span>{{dialog.customCode}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">商品名称：</span>
                    </h4>
                    <p class="item-value"><span>{{dialog.commodityName}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">月度：</span>
                    </h4>
                    <p class="item-value"><span>{{dialog.monthly}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">企业编码：</span>
                    </h4>
                    <p class="item-value"><span>{{dialog.entCode}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">企业名称：</span>
                    </h4>
                    <p class="item-value"><span>{{dialog.entName}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">收发货地：</span>
                    </h4>
                    <p class="item-value"><span>{{dialog.deliveryPlace}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">金额（USD）：</span>
                    </h4>
                    <p class="item-value"><span>{{dialog.usdAmount}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">出口最终国或进口原产国：</span>
                    </h4>
                    <p class="item-value"><span>{{dialog.countryOrigin}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">贸易方式：</span>
                    </h4>
                    <p class="item-value"><span>{{dialog.tradeMode}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">运输方式：</span>
                    </h4>
                    <p class="item-value"><span>{{dialog.shippingType}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">中转国：</span>
                    </h4>
                    <p class="item-value"><span>{{dialog.transitCountry}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">数量单位：</span>
                    </h4>
                    <p class="item-value"><span>{{dialog.quantityUnit}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">企业电话：</span>
                    </h4>
                    <p class="item-value"><span>{{dialog.entTel}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">企业邮编：</span>
                    </h4>
                    <p class="item-value"><span>{{dialog.entPostcode}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">企业传真：</span>
                    </h4>
                    <p class="item-value"><span>{{dialog.entFax}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">电子邮件：</span>
                    </h4>
                    <p class="item-value"><span>{{dialog.email}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">联系人：</span>
                    </h4>
                    <p class="item-value"><span>{{dialog.contacts}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">企业性质：</span>
                    </h4>
                    <p class="item-value"><span>{{dialog.entType}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">企业详细地址：</span>
                    </h4>
                    <p class="item-value"><span>{{dialog.entAddress}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">省市：</span>
                    </h4>
                    <p class="item-value"><span>{{dialog.proCities}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">年份：</span>
                    </h4>
                    <p class="item-value"><span>{{dialog.particularYear}}</span></p>
                </div>
            </div>
        </el-dialog>

    </div>
</template>

<script>
    import * as api from '@/assets/js/api'
    export default {
        name: 'customsDetails',
        data () {
            return {
                pageSize:50,
                currentPage:1,
                totalRecord:'',
                dialogVisible: false,
                commodityName:'',
                datalist: [],
                dialog:{}
            }
        },
        methods:{
            //分页
            handleCurrentChange(val) {
                console.log(`当前页: ${val}`);
                this.getList({
                    entId:1,
                    entName:this.$router.currentRoute.params.entName,
                    page:val,
                    pageSize:this.pageSize
                })
            },
            showPopup(exporDetailsId ){
                this.dialogVisible = true;
                api.Get('/cloudMap/customs/getCustomsDetails',{//模态框
                    entId:1,
                    entName:this.$router.currentRoute.params.entName,
                    exporDetailsId:exporDetailsId
                }).then((res)=>{
                    console.log("模态框");
                    console.log(res);
                    this.dialog =res.data;
                })
            },
            //列表
            getList(params){//列表
                api.Get('/cloudMap/customs/getCustomsList',params).then((res)=>{
                    console.log("海关进出口信息");
                    console.log(res);
                    this.datalist =res.data;
                    this.totalRecord=res.totalRecord;
                })
            }
        },
        mounted(){
            this.getList({//列表
                entId:1,
                entName:this.$router.currentRoute.params.entName,
                page:1,
                pageSize:this.pageSize
            })
        }
    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    /* 弹窗样式 */
    .dialog-con{
        max-height:600px;
        overflow:auto;
        overflow-x:hidden;
    }
    .dialog-con:hover::-webkit-scrollbar-thumb{
        background:rgba(7,100,180,1);
    }
    .dialog-item{
        color:#fff;
        margin-left:26px;
        margin-bottom:28px;
        margin-right: 30px;
    }
    .dialog-item .item-name{
        margin-bottom:8px;
    }
    .dialog-item .item-name span{
        height:15px;
        line-height:15px;
        font-size: 12px;
    }
    .dialog-item .item-name i{
        margin-left:1px;
        margin-right:6px;
        height:15px;
        width:2px;
        background:#00c2ff;
    }
    .dialog-item .item-value{
        display:block;
        word-wrap: break-word;
    }

    .customsimported{
        width: 100%;
        height: 100%;
    }
    .imported-table{
        width: 100%;
        border-collapse:separate;
        border-spacing:0 4px;
    }
    .imported-thead th{
        height:34px;
        line-height: 34px;
        font-size: 12px;
        font-weight: bold;
        text-align: left;
        padding:0 10px;
        background: rgba(40,92,198,0.35);
        color: #cdd2de;
        border-top: 1px solid #284f9d;
        border-bottom: 1px solid #284f9d;
    }
    .imported-thead th:first-child{
        width: 65px;
        text-align: center;
        border-left: 1px solid #284f9d;
    }
    .imported-thead th:last-child{
        border-right: 1px solid #284f9d;
    }
    .imported-tbody td{
        height:34px;
        line-height: 32px;
        color: #fff;
        font-size: 12px;
        text-align: left;
        padding:0 10px;
        background: rgba(88,143,255,0.15);
        border-top:1px solid rgba(99,150,255,0.35);
        border-bottom:1px solid rgba(99,150,255,0.35);
        box-sizing: border-box;
    }
    .imported-tbody td:first-child{
        width: 65px;
        text-align: center;
    }
    .imported-tbody tr td:first-child{
        border-left:1px solid rgba(99,150,255,0.35);
    }
    .imported-tbody tr td:last-child{
        border-right:1px solid rgba(99,150,255,0.35);
    }
    .imported-tbody td span{
        width:1px;
        height:25px;
        background: #214c9a;
        float: right;
        margin-top:5px;
    }
    .imported-tbody td b{
        color: #fff;
        cursor: pointer;
        text-decoration: underline;
    }
    .imported-td{
        min-width: 38%;
    }
    .customs-num{
        min-width: 12%;
    }
    .monthly{
        min-width: 7%;
    }
    .imported-tbody tr:hover {
        border:1px solid #2751a2;
        background:rgba(40,92,198,0.35);
        color:#cdd2de;
    }
    .overflowAuto{
        box-sizing: border-box;
        flex: auto;
        overflow: auto;
        overflow-x: hidden;
        height:424px;
        padding-right: 5px;
    }
    .overflowAuto:hover::-webkit-scrollbar-thumb{
        background:rgba(7,100,180,1)
    }
    .pageWrap{
        margin-top:10px;
    }
</style>



