<template>
    <div class="internetdata">
        <!--互联网信息-->
        <div class="overflowAuto">
            <div class="internetdata-info" v-for="item in datalist">
                <!--左侧图片-->
                <div class="info-left">
                    <img src="../../assets/img/internetLeft.png" alt="">
                    <!--<img :src="item.infoImg"/>-->
                </div>
                <!--右侧文字信息-->
                <div class="info-right">
                    <h2><span @click="showPopup(item.titleName,item.id)">{{item.titleName}}</span></h2>
                    <p>{{item.articleCon}}</p>
                    <p class="right-details">
                        <span>{{item.articleSource}}</span>
                        <span>{{item.articleTime}}</span>
                    </p>
                </div>
            </div>
        </div>

        <!--分页-->
        <div class="pageWrap"  v-if="this.totalRecord>this.pageSize">
            <el-pagination
                    @current-change="handleCurrentChange"
                    :current-page.sync="this.currentPage"
                    :page-size="this.pageSize"
                    layout="prev, pager, next, jumper"
                    :total="this.totalRecord">
            </el-pagination>
        </div>

        <!--弹框-->
        <el-dialog  :visible.sync="dialogVisible">
            <p class="title">{{title}}</p>
                <!--这里写自己的内容-->
                <div class="con" >
                    <p>{{titleText.articleCon}}</p>
                    <p class="boxLine"></p>
                    <p class="lastText">
                        <span>{{titleText.articleSource}}</span>
                        <span>{{titleText.articleTime}}</span>
                    </p>
                </div>
        </el-dialog>


    </div>
</template>

<script type="es6">
    import * as api from '@/assets/js/api'
    export default {
        name: 'internetDetails',
        data () {
            return {
                pageSize:50,
                currentPage:1,
                totalRecord:'',
                dialogVisible: false,
                title:'',
                datalist:[],
                titleText:{}
            }
        },
        methods:{
            //分页
            handleCurrentChange(val) {
                console.log(`当前页: ${val}`);
                this.getList({
                    entId:1,
                    entName:this.$router.currentRoute.params.entName,
                    page:val,
                    pageSize:this.pageSize
                })
            },
            showPopup(txt,contentId){
                this.dialogVisible = true;
                this.title = txt;
                api.Get('/cloudMap/inAssetsInfo/getInternetDetails',{//模态框
                    entId:1,
                    entName:this.$router.currentRoute.params.entName,
                    contentId:contentId
                }).then((res)=>{
                    console.log("模态框");
                    console.log(res);
                    this.titleText =res.data;
                })
            },
            getList(params){//列表
                api.Get('/cloudMap/internet/getInternetList',params).then((res)=>{
                    console.log("互联网数据");
                    console.log(res);
                    this.datalist =res.data;
                    this.totalRecord=res.totalRecord;
                })
            }
        },
        mounted(){
            this.getList({//列表
                entId:1,
                entName:this.$router.currentRoute.params.entName,
                page:1,
                pageSize:this.pageSize
            })
        }
    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    /*模态框*/
    .con{
        height: 600px;
        overflow: auto;
        overflow-x: hidden;
        padding:0 20px;
    }
    .con:hover::-webkit-scrollbar-thumb{
        background:rgba(7,100,180,1)
    }
    .con p{
        text-indent: 30px;
        line-height: 30px;
        margin-bottom: 30px;
        color: #fff;
        letter-spacing: 1px;
    }
    .boxLine{
        width: 100%;
        border:1px dashed #ffffff;
        margin-top: 50px;
        margin-bottom: 10px!important;
    }
    .lastText{
        display: flex;
        justify-content: space-between;
        align-items: center;
        padding:0 15px;
        margin-bottom: 15px!important;
    }
    .lastText span:first-child{
        text-indent: 0;
    }
    .internetdata{
        width: 100%;
        height: 100%;
    }
    /*循环的信息模块*/
    .internetdata-info{
        width: 100%;
        height: 120px;
        display: flex;
        align-items: center;
        margin-bottom:5px;
        background: rgba(88,143,255,0.15);
        border:1px solid rgba(99,150,255,0.35);;
    }
    /*左侧图片*/
    .internetdata-info .info-left{
        width: 160px;
        height:100px;
        margin:0 10px;
    }
    .internetdata-info .info-left img{
        width: 100%;
        height: 100%;
    }
    /*右侧文本区域*/
    .info-right{
        flex: 1;
        padding-right:10px;
    }
    .info-right h2{
        text-align: left;
        margin-bottom:5px;
    }
    .info-right h2 span{
        font-size: 14px;
        font-weight: 700;
        text-decoration:underline;
        color: #fff;
        cursor: pointer;
    }
    .info-right p{
        font-size: 12px;
        color: #d5d7db;
        text-align: left;
    }
    .right-details{
        margin-top:10px;
        padding-right:10px;
        display: flex;
        justify-content:space-between;
    }
    .internetdata-info:hover {
        border:1px solid #2751a2;
        background:rgba(40,92,198,0.35);
        color:#cdd2de;
    }
    .overflowAuto{
        box-sizing: border-box;
        flex: auto;
        overflow: auto;
        height:424px;
        overflow-x: hidden;
        padding-right: 8px;
    }
    .overflowAuto:hover::-webkit-scrollbar-thumb{
        background:rgba(7,100,180,1)
    }
    .pageWrap{
        margin-top:10px;
    }
</style>



