<template>
    <div class="boot-page" >
        <!--严重违法失信企业名单-->
        <div class="blacklistInfo overflowAuto">
            <table>
                <tr>
                    <th class="firstTh">序号</th>
                    <th class="secondTh">类别</th>
                    <th class="thirdTh">列入原因</th>
                    <th class="fourthTh">决定机关/日期</th>
                    <th class="fifthTh">移除原因</th>
                    <th class="lastTh">决定机关/日期</th>
                </tr>
                <tr v-for="(item,index) in blacklistInfo">
                    <td class="firstTd">{{index+1}}</td>
                    <td>
                        {{item.illeagalType}}
                    </td>
                    <td class="secondTd">
                        {{item.inputReason}}
                    </td>
                    <td>
                        <span>
                            {{item.inputOrganization}}<br/>{{item.inputDate}}
                        </span>
                    </td>
                    <td class="fourthTd">
                        {{item.outputReason}}
                    </td>
                    <td  class="lastTd">
                            {{item.outputOrganization}}<br/>{{item.outputDate}}
                    </td>
                </tr>
            </table>
        </div>
        <div class="pageWrap" v-if="this.totalRecord>50">
            <el-pagination
                    @size-change="handleSizeChange"
                    @current-change="handleCurrentChange"
                    :current-page.sync="currentPage3"
                    :page-size="this.pageSize"
                    layout="prev, pager, next, jumper"
                    :total="1000">
            </el-pagination>
        </div>
    </div>
</template>

<script>
    import * as api from '@/assets/js/api'
    export default {
        name: 'blacklistInfoList',
        mounted(){
            api.Get('/cloudMap/business/getBreakLawPromiseList',{
                "entId": 1,
                "entName":this.entName,
                "pageSize":50,
                "page":1
            }).then((res)=>{
                console.log(res)
                this.totalRecord=res.totalRecord;
                this.blacklistInfo =res.data;
            })
        },
        data () {
            return {
                entName:this.$router.currentRoute.params.entName,
                totalRecord:0,
                pageSize:50,
                currentPage3:1,
                blacklistInfo:[]
            }
        },
        methods:{
            handleSizeChange(val) {
                console.log(`每页 ${val} 条`);
            },
            handleCurrentChange(val) {
                console.log(`当前页: ${val}`);
                api.Get('/cloudMap/business/getBreakLawPromiseList',{
                    "entId": 1,
                    "entName":this.entName,
                    "pageSize":50,
                    "page":val
                }).then((res)=>{
                    console.log(res)
                    this.totalRecord=res.totalRecord;
                    this.blacklistInfo =res.data;
                })
            }
        },
        components:{

        }
    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    @import 'assets/css/common.css';
    @import 'assets/css/tableStyle.css';
    /*黑名单*/
    .blacklistInfo .firstTd{
        border-left:1px solid #1e417a;
    }
    .blacklistInfo .lastTd{
        border-right:1px solid #1e417a;
    }
    .blacklistInfo .firstTh{
        width:10%;
    }
    .blacklistInfo .secondTh{
        width:10%;
    }
    .blacklistInfo .thirdTh{
        width:17%;
    }
    .blacklistInfo .fourthTh{
        width:15%
    }
    .blacklistInfo .fifthTh{
        width:17%
    }
    .blacklistInfo .lastTh{
         width:15%
    }
    .blacklistInfo span{
        text-align: left;
        float: right;
        width:calc(100% - 15px);
        margin: 10px 0;
        margin-right:0px;
        padding-right: 15px;
        border-right: 1px solid #214c99;
        display: inline-block;
    }
    .secondTd,.fourthTd{
        padding:10px 20px;
    }
    tr:hover .secondTd,tr:hover .fourthTd{
        padding:10px 20px;
    }
</style>



