<template>
    <div class="boot-page">
        <ul class="clearfix foodLcence-list">
            <li v-for="list in datalist"><a @click="showPopup(list.id)">
                <h4 class="foodLcence-title">{{list.atInfoName}}</h4>
                <p><span>编号：<em>{{list.atNum}}</em></span></p>
                <p><span>发证日期: <em>{{list.dateOfIssue}}</em></span></p>
            </a></li>
        </ul>

        <!--弹框-->
        <el-dialog  :visible.sync="dialogVisible">
            <p class="title">{{title}}</p>
            <!--这里写自己的内容-->
            <div class="dialog-con">
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">编号：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.atNum}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">社会信用代码/组织机构代码：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.societyCode}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">分类码：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.classificationNum}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">省市：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.proCities}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">企业名称：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.entName}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">法定代表人：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.legalRepresentative}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">企业负责人：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.personCharge}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">质量负责人：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.qualityOfficer}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">注册地址：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.regAddress}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">生产地址：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.productAddress}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">生产范围：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.productdRange}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">发证日期：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.dateOfIssue}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">有效期至：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.termOfValidity}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">发证机关：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.issuingAuthority}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">签发人：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.issuer}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">日常监管机构：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.regulatoryBody}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">日常监管人员：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.supervisor}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">监督举报电话：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.supervisoryCall}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">备注：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.remarks}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">相关数据库查询：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.relatedBaseQuery}}</span></p>
                </div>
            </div>
        </el-dialog>
    </div>
</template>

<script type="es6">
    import * as api from '@/assets/js/api.js'
    export default {
        name: 'certificationInfo',
        data () {
            return {
                dialogVisible: false,
                title:'药品生产企业',
                datalist: [],
                dataDetails: []
            }
        },
        mounted(){
            api.Get("/cloudMap/fda/getCertificationInfoList",{
                "entName": this.$router.currentRoute.params.entName
            })
                .then(res=>{
                    console.log('请求成功返回的结果'+res.code);
                    this.datalist = res.data;
                })
                .catch(err=>{
                    console.log('请求失败返回的错误信息');
                })
        },
        methods:{
            showPopup(id){
                api.Get('/cloudMap/ep/getCertificationInfoDetails',{
                    "entName": this.$router.currentRoute.params.entName,
                    "cerId":id
                })
                    .then(res=>{
                        console.log('请求成功返回的结果'+res.code);
                        this.dataDetails = res.data;
                    })
                    .catch(err=>{
                        console.log('请求失败返回的错误信息');
                    })
                this.dialogVisible = true;
            }
        }
    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    @import "../assets/css/dialog.css";
    .boot-page{
        width:100%;
        height:100%;
    }
    .foodLcence-list li{
        display:inline-block;
        width:33%;
    }
    .foodLcence-list li a{
        display:block;
        margin:5px 5px 0 0;
        height:118px;
        border:1px solid rgba(99,150,255,0.25);
        background:rgba(88,143,255,0.15);
        color:#fff;
        text-decoration:none;
        padding-left:40px;
        padding-right: 20px;
        -webkit-transition: box-shadow 0.3s;
        -moz-transition: box-shadow 0.3s;
        -ms-transition: box-shadow 0.3s;
        -o-transition: box-shadow 0.3s;
        transition: box-shadow 0.3s;
        cursor:pointer;
    }
    .foodLcence-list li a:hover{
        -webkit-box-shadow: 0 0 10px 0 rgba(99,150,255,0.4);
        -moz-box-shadow: 0 0 10px 0 rgba(99,150,255,0.4);
        box-shadow: 0 0 10px 0 rgba(99,150,255,0.4);
        -webkit-transition: box-shadow 0.3s;
        -moz-transition: box-shadow 0.3s;
        -ms-transition: box-shadow 0.3s;
        -o-transition: box-shadow 0.3s;
        transition: box-shadow 0.3s;
    }
    .foodLcence-list .foodLcence-title{
        width:100%;
        line-height:27px;
        font-size:14px;
        font-weight:500;
        margin-top:24px;
        overflow:hidden;
        text-overflow:ellipsis;
        white-space: nowrap;
    }
    .foodLcence-list p{
        line-height:22px;
        font-size:12px;
        color:#c6c6c6;
    }
    .foodLcence-list p em{
        color:#fff;
    }
</style>



