<template>
    <div class="boot-page">
        <div class="overflowAuto">
            <table class="court-table">
                <thead>
                <tr>
                    <th class="tc w50">序号</th>
                    <th>注册证编号</th>
                    <th>产品名称</th>
                    <th class="w100">批准日期</th>
                </tr>
                </thead>
                <tbody>
                <tr v-for="(item,index) in datalist">
                    <td class="tc">{{index+1}}</td>
                    <td>
                        <a @click="showPopup(item.id)">{{item.regCertificateNum}}</a>
                    </td>
                    <td>{{item.productName}}</td>
                    <td>{{item.approvalDate}}</td>
                </tr>
                </tbody>
            </table>
        </div>

        <!--分页-->
        <div class="pageWrap"  v-if="this.totalRecord>this.pageSize">
            <el-pagination
                    @current-change="handleCurrentChange"
                    :current-page.sync="this.currentPage"
                    :page-size="this.pageSize"
                    layout="prev, pager, next, jumper"
                    :total="this.totalRecord">
            </el-pagination>
        </div>

        <!--弹框-->
        <el-dialog  :visible.sync="dialogVisible">
            <p class="title">{{title}}</p>
            <!--这里写自己的内容-->
            <div class="dialog-con">
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">注册证编号：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.regCertificateNum}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">注册人名称：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.registrantName}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">注册人住所：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.residence}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">生产地址：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.productionAds}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">代理人名称：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.agentName}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">代理人住所：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.agentAds}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">产品名称：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.productName}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">型号、规格：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.specifications}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">结构及组成：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.structureForm}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">适用范围：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.applyScope}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">其他内容：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.otherCon}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">备注：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.remarks}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">批准日期：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.approvalDate}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">有效期至：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.termOfValidity}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">附件：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.enclosure}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">产品标准：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.productStandard}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">变更日期：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.changeDate}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">主要组成成分（体外诊断试剂）：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.majorComponent}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">预期用途（体外诊断试剂）：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.intendedUse}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">产品储存条件及有效期（体外诊断试剂）：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.storageCon}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">审批部门：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.approvalDepar}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">变更情况：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.alteration}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">生产国或地区（英文）：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.enProCountry}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">生产厂商名称（中文）：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.chManufacturer}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">产品名称（中文）：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.chProductName}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">售后服务机构：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.serInstitutions}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">生产国或地区（中文）：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.chProCountry}}</span></p>
                </div>
            </div>
        </el-dialog>
    </div>
</template>

<script type="es6">
    import * as api from '@/assets/js/api'
    export default {
        name: 'importedEquipment',
        data () {
            return {
                dialogVisible: false,
                title:'进口器械',
                pageSize:50,
                currentPage:1,
                totalRecord:'',
                datalist: [
//                    {
//                        "id": 1,
//                        "regCertificateNum":"卫机健字(1998)第408号",
//                        "productName":"心脏起搏器",
//                        "approvalDate": "2016-12-1"
//                    },
//                    {
//                        "id": 2,
//                        "regCertificateNum":"卫机健字(1998)第408号",
//                        "productName":"心脏起搏器",
//                        "approvalDate": "2016-12-1"
//                    },
//                    {
//                        "id": 3,
//                        "regCertificateNum":"卫机健字(1998)第408号",
//                        "productName":"心脏起搏器",
//                        "approvalDate": "2016-12-1"
//                    }
                ],
                dataDetails:{
//                    "regCertificateNum":"xxxxx",
//                    "registrantName":"xxxxx",
//                    "residence":"xxxxx",
//                    "productionAds":"xxxxx",
//                    "agentName":"xxxxx",
//                    "agentAds":"xxxxx",
//                    "productName":"xxxxx",
//                    "specifications":"xxxxx",
//                    "structureForm":"xxxxx",
//                    "applyScope":"xxxxx",
//                    "otherCon":"xxxxx",
//                    "remarks":"xxxxx",
//                    "approvalDate":"xxxxx",
//                    "termOfValidity":"xxxxx",
//                    "enclosure":"xxxxx",
//                    "productStandard":"xxxxx",
//                    "changeDate":"xxxxx",
//                    "postcode":"xxxxx",
//                    "majorComponent":"xxxxx",
//                    "intendedUse":"xxxxx",
//                    "storageCon":"xxxxx",
//                    "approvalDepar":"xxxxx",
//                    "alteration":"xxxxx",
//                    "enProCountry":"xxxxx",
//                    "chManufacturer":"xxxx",
//                    "chProductName":"XXXX",
//                    "serInstitutions":"xxxx",
//                    "chProCountry":"xxxx"
                }
            }
        },
        methods:{
            getList(params){//列表
                api.Get('/cloudMap/fda/getImportedEquipmentList',params).then((res)=>{
                    console.log(res);
                    this.datalist =res.data;
                    this.totalRecord=res.totalRecord;
                })
                    .catch(err=>{
                        console.log('请求失败返回的错误信息');
                    })
            },
            handleCurrentChange(val) {//分页
                console.log(`当前页: ${val}`);
                this.getList({
                    entId:1,
                    entName:this.$router.currentRoute.params.entName,
                    page:val,
                    pageSize:this.pageSize
                })
            },
            showPopup(importedId){//模态框
                this.dialogVisible = true;
                api.Get('/cloudMap/fda/getImportedEquipmentDetails',{
                    entId:1,
                    entName:this.$router.currentRoute.params.entName,
                    importedId: importedId
                }).then((res)=>{
                    console.log(res);
                    this.dataDetails =res.data;
                })
            }
        },
        mounted(){
            this.getList({//列表
                entId:1,
                entName:this.$router.currentRoute.params.entName,
                page:1,
                pageSize:this.pageSize
            })
        }
    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    @import "../assets/css/tableCon.css";
    @import "../assets/css/dialog.css";
</style>



