<template>
    <div class="boot-page">
        <div class="overflowAuto">
            <table class="court-table">
                <thead>
                <tr>
                    <th class="tc w50">序号</th>
                    <th class="w220">案号</th>
                    <th>案件名称</th>
                    <th class="w220">法院</th>
                    <th class="w100">裁判日期</th>
                </tr>
                </thead>
                <tbody>
                <tr v-for="(item,index) in datalist">
                    <td class="tc">{{index+1}}</td>
                    <td><a @click="showPopup(item.caseName,item.caseNo,item.id,item.refereeDate)">{{item.caseNo}}</a></td>
                    <td>{{item.caseName}}</td>
                    <td>{{item.courtName}}</td>
                    <td>{{item.refereeDate}}</td>
                </tr>
                </tbody>
            </table>
        </div>

        <!--分页-->
        <div class="pageWrap"  v-if="this.totalRecord>this.pageSize">
            <el-pagination
                    @current-change="handleCurrentChange"
                    :current-page.sync="this.currentPage"
                    :page-size="this.pageSize"
                    layout="prev, pager, next, jumper"
                    :total="this.totalRecord">
            </el-pagination>
        </div>

        <!--弹框-->
        <el-dialog  :visible.sync="dialogVisible">
            <p class="title">{{title}}</p>
            <!--这里写自己的内容-->
            <div class="dialog-con">
                <div class="writtenVerdict">
                    <h4 class="wtitle">{{this.caseTitle}}</h4>
                    <div class="wcontent">
                        <div class="whead">
                            <p class="tc wtype">{{dataDetails.publishingOrg}}<br/>{{this.caseNo}}</p>
                            <p class="clearfix wline">
                                <span class="fl">{{dataDetails.civilSupervision}}</span>
                                <span>{{dataDetails.judgmentType}}</span>
                                <span class="fr">{{this.refereeDate}}</span>
                            </p>
                        </div>
                        <div class="wtext">
                            <p>{{dataDetails.refereeContent}}</p>
                        </div>
                    </div>
                </div>
            </div>
        </el-dialog>
    </div>
</template>

<script type="es6">
    import * as api from '@/assets/js/api.js'
    export default {
        name: 'refereeDocuments',
        data () {
            return {
                dialogVisible: false,
                title:'裁判文书',
                pageSize:50,
                currentPage:1,
                totalRecord:'',
                caseTitle:'',
                caseNo:'',
                refereeDate:'',
                datalist: [],
                dataDetails: {}
            }
        },
        methods:{
            handleCurrentChange(val) {//分页
                console.log(`当前页: ${val}`);
                this.getList({
                    entId:1,
                    entName:this.$router.currentRoute.params.entName,
                    page:val,
                    pageSize:this.pageSize
                })
            },
            showPopup(caseName, caseNo,refereeListId,date){//模态框
                this.dialogVisible = true;
                this.caseTitle=caseName;
                this.caseNo=caseNo;
                this.refereeDate=date;
                api.Get("/cloudMap/court/getRefereeDocumentsDetails",{
                    entId:1,
                    entName:this.$router.currentRoute.params.entName,
                    refereeListId:refereeListId
                }).then((res)=>{
                    this.dataDetails=res.data;
                })
            },
            getList(params){//列表
                api.Get('/cloudMap/court/getRefereeDocumentsList',params).then((res)=>{
                    console.log(res);
                    this.datalist = res.data;
                    this.totalRecord=res.totalRecord;
                    console.log('请求成功返回的结果'+res.code);
                    console.log("总页数"+res.totalRecord)
                })
            },
        },
        mounted(){
            this.getList({//列表
                entId:1,
                entName:this.$router.currentRoute.params.entName,
                page:1,
                pageSize:this.pageSize
            })
        }
    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    @import "./assets/css/tableCon.css";
    @import "./assets/css/dialog.css";
    .tc{
        text-align:center;
    }
    .writtenVerdict{
        padding:4px 15px 30px;
    }
    .writtenVerdict .wtitle{
        text-align:center;
        font-size:16px;
        font-weight:bold;
        color:#fff;
        line-height:33px;
        margin-bottom:32px;
    }
    .writtenVerdict .wcontent{
        text-align:center;
        font-size:14px;
        color:#fff;
        margin-bottom:10px;
    }
    .whead{
        line-height:32px;
    }
    .whead .wtype{
        margin-bottom:34px;
    }
    .whead .wline{
        line-height:24px;
        border-bottom:1px dashed #fff;
        margin-bottom:37px;
    }
    .wtext p{
        line-height:32px;
        text-indent:2em;
        text-align:left;
        margin-bottom:32px;
    }
</style>



