<template>
    <div class="boot-page">
        <!--变更信息-->
        <div class="changeInfo overflowAuto">
            <table>
                <tr>
                    <th class="firstTh">序号</th>
                    <th class="secondTh">变更日期</th>
                    <th class="thirdTh">变更事项</th>
                    <th class="fourthTh">变更前内容</th>
                    <th class="lastTh">变更后内容</th>
                </tr>
                <tr v-for="(item,index) in changeInfo">
                    <td class="firstTd">{{index+1}}</td>
                    <td>{{item.changeDate}} </td>
                    <td>
                        {{item.changeItem}}
                    </td>
                    <td>
                        <span>
                            {{item.changeBefore}}
                        </span>
                    </td>
                    <td class="lastTd">
                        <span class="lastSpan">
                            {{item.changeAfter}}
                        </span>
                    </td>
                </tr>
            </table>
        </div>
        <div class="pageWrap" v-if="this.totalRecord>50">
            <el-pagination
                    @size-change="handleSizeChange"
                    @current-change="handleCurrentChange"
                    :current-page.sync="currentPage3"
                    :page-size="this.pageSize"
                    layout="prev, pager, next, jumper"
                    :total="1000">
            </el-pagination>
        </div>
    </div>
</template>

<script>
    import * as api from '@/assets/js/api'
    export default {
        name: 'changeInfoList',
        mounted(){
            api.Get('/cloudMap/business/getChangeInfoList',{
                "entId": 1,
                "entName": this.entName,
                "pageSize":50,
                "page":1
            }).then((res)=>{
                console.log(res)
                this.totalRecord=res.totalRecord;
                this.changeInfo =res.data;
            })
        },
        data () {
            return {
                entName:this.$router.currentRoute.params.entName,
                totalRecord:0,
                pageSize:50,
                currentPage3:1,
                changeInfo:[]
            }
        },
        methods:{
            handleSizeChange(val) {
                console.log(`每页 ${val} 条`);
            },
            handleCurrentChange(val) {
                console.log(`当前页: ${val}`);
                api.Get('/cloudMap/business/getChangeInfoList',{
                    "entId": 1,
                    "entName":this.entName,
                    "pageSize":50,
                    "page":val
                }).then((res)=>{
                    console.log(res)
                    this.totalRecord=res.totalRecord;
                    this.changeInfo =res.data;
                })
            }
        },
        components:{

        }
    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    @import 'assets/css/common.css';
    /*变更信息*/
    .changeInfo .firstTd{
        border-left:1px solid #1e417a;
    }
    .changeInfo .lastTd{
        border-right:1px solid #1e417a;
    }
    .changeInfo .firstTh{
        width:10%;
    }
    .changeInfo .secondTh{
        width:15%;
    }
    .changeInfo .thirdTh{
        width:15%;
    }
    .changeInfo .fourthTh,.changeInfo .lastTh{
        width:30%
    }
    .changeInfo span{
        text-align: left;
        float: right;
        width:calc(100% - 15px);
        margin: 10px 0;
        margin-right:0px;
        padding-right: 15px;
        border-right: 1px solid #214c99;
        display: inline-block;
    }
    .changeInfo .lastSpan{
        border-right: none;
    }

</style>



