<template>
    <div class="boot-page">
        <div class="overflowAuto">
            <table class="court-table">
                <thead>
                <tr>
                    <th class="tc w50">序号</th>
                    <th>案号</th>
                    <th>失信被执行人行为具体情形</th>
                    <th>做出执行依据单位</th>
                    <th class="w100">发布时间</th>
                </tr>
                </thead>
                <tbody>
                <tr v-for="(list,index) in datalist">
                    <td class="tc">{{index+1}}</td>
                    <td>
                        <a @click="showPopup(list.id)">{{list.caseNo}}</a>
                    </td>
                    <td>{{list.beDescription}}</td>
                    <td>{{list.unitExecution}}</td>
                    <td>{{list.releaseDate}}</td>
                </tr>
                </tbody>
            </table>
        </div>

        <!--分页-->
        <div class="pageWrap"  v-if="this.totalRecord>this.pageSize">
            <el-pagination
                    @current-change="handleCurrentChange"
                    :current-page.sync="this.currentPage"
                    :page-size="this.pageSize"
                    layout="prev, pager, next, jumper"
                    :total="this.totalRecord">
            </el-pagination>
        </div>

        <!--弹框-->
        <el-dialog  :visible.sync="dialogVisible">
            <p class="title">{{title}}</p>
            <!--这里写自己的内容-->
            <div class="dialog-con">
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">被执行人姓名/名称：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.beName}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">性别：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.gender}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">年龄：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.age}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">身份证号码/组织机构代码：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.idCard}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">执行法院：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.executionCourt}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">省份：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.province}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">执行依据文号：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.enBasisNum}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">立案时间：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.filingTime}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">案号：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.caseNo}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">做出执行依据单位：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.unitExecution}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">生效法律文书确定的义务：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.takeObligation}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">被执行人的履行情况：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.executeResult}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">失信被执行人行为具体情形：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.beDescription}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">发布时间：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.releaseDate}}</span></p>
                </div>
            </div>
        </el-dialog>
    </div>
</template>

<script type="es6">
    import * as api from '@/assets/js/api.js'
    export default {
        name: 'brokenPromisesDetails',
        data () {
            return {
                dialogVisible: false,
                title:'失信被执行人信息',
                pageSize:50,
                currentPage:1,
                totalRecord:'',
                datalist: [],
                dataDetails:[]
            }
        },
        methods:{
            handleCurrentChange(val) {//分页
                console.log(`当前页: ${val}`);
                this.getList({
                    entId: 1,
                    entName: this.$router.currentRoute.params.entName,
                    page: val,
                    pageSize: this.pageSize
                })
            },
            showPopup(id){//模态框
                this.dialogVisible = true;
                console.log(id);
                api.Get('/cloudMap/court/getDishonestyDetails',{
                    "entName": this.$router.currentRoute.params.entName,
                    "id":id
                })
                    .then(res=>{
                        console.log('请求成功返回的结果'+res.code);
                        this.dataDetails = res.data;
                    })
                    .catch(err=>{
                        console.log('请求失败返回的错误信息');
                    })
            },
            getList(params){//列表
                api.Get('/cloudMap/court/getDishonestyList',params).then((res)=>{
                    console.log(res);
                    console.log('请求成功返回的结果'+res.code);
                    this.datalist = res.data;
                    this.totalRecord=res.totalRecord;
                })
                    .catch(err=>{
                        console.log('请求失败返回的错误信息');
                    })
            }
        },
        mounted(){
            this.getList({//列表
                entId: 1,
                entName: this.$router.currentRoute.params.entName,
                page: 1,
                pageSize: this.pageSize
            })
        }
    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    @import "./assets/css/tableCon.css";
    @import "./assets/css/dialog.css";
</style>



