<template>
    <div class="boot-page">
        <table class="court-table">
            <thead>
            <tr>
                <th class="tc w50">序号</th>
                <th>证书编号</th>
                <th>认定专业</th>
                <th class="w100">认证日期</th>
                <th class="w100">有效期截止日</th>
            </tr>
            </thead>
            <tbody>
            <tr v-for="(list,index) in datalist">
                <td class="tc">{{index+1}}</td>
                <td>
                    <a href="javascript:;">{{list.certificateNum}}</a>
                </td>
                <td>{{list.cogSpecialty}}</td>
                <td>{{list.atDate}}</td>
                <td>{{list.termOfValidity}}</td>
            </tr>
            </tbody>
        </table>
    </div>
</template>

<script type="es6">
    import * as api from '@/assets/js/api.js'
    export default {
        name: 'clinicalTrialMechanism',
        data () {
            return {
                datalist: []
            }
        },
        mounted(){
            api.Get("/cloudMap/fda/getClinicalTrialMechanismList",{
                "entName": this.$router.currentRoute.params.entName
            })
                .then(res=>{
                    console.log('请求成功返回的结果'+res.code);
                    this.datalist = res.data;
                })
                .catch(err=>{
                    console.log('请求失败返回的错误信息');
                })
        }
    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    @import "../assets/css/tableCon.css";
</style>



