<template>
    <div class="boot-page">
        <!--虚假广告通告信息-->
        <div class="overflowAuto">
            <table class="court-table">
                <thead>
                    <tr>
                        <th class="tc w50">序号</th>
                        <th>总局通告号</th>
                        <th>广告中标识产品名称</th>
                        <th>违法内容简述</th>
                        <th class="w100">总局通告时间</th>
                    </tr>
                </thead>
                <tbody>
                    <tr v-for="(item,index) in datalist">
                        <td class="tc">{{index+1}}</td>
                        <td>
                            <a @click="showPopup(item.id)">{{item.admNoticeNum}}</a>
                        </td>
                        <td>{{item.adProductName}}</td>
                        <td>{{item.illegalCon}}</td>
                        <td>{{item.admNoticeDate}}</td>
                    </tr>
                </tbody>
            </table>
        </div>

        <!--分页-->
        <div class="pageWrap"  v-if="this.totalRecord>this.pageSize">
            <el-pagination
                    @current-change="handleCurrentChange"
                    :current-page.sync="this.currentPage"
                    :page-size="this.pageSize"
                    layout="prev, pager, next, jumper"
                    :total="this.totalRecord">
            </el-pagination>
        </div>

        <!--弹框-->
        <el-dialog  :visible.sync="dialogVisible">
            <p class="title">{{title}}</p>
            <!--这里写自己的内容-->
            <div class="dialog-con">
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">产品类别：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.proCategory}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">广告中标识产品名称：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.adProductName}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">产品名称：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.productName}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">生产企业名称：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.maEnterpriseName}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">生产企业所在地：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.maEpLocation}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">产品批准文号：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.proApprovalNum}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">违法内容简述：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.illegalCon}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">总局通告号：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.admNoticeNum}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">总局通告时间：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.admNoticeDate}}</span></p>
                </div>
            </div>
        </el-dialog>
    </div>
</template>

<script type="es6">
    import * as api from '@/assets/js/api'
    export default {
        name: 'falseAdNotice',
        data () {
            return {
                dialogVisible: false,
                title:'虚假广告通告信息',
                pageSize:50,
                currentPage:1,
                totalRecord:'',
                datalist: [
//                    {
//                        "id":1,
//                        "admNoticeNum": 'XXXXXXXXXXXXX',
//                        "adProductName": 'XXXXXXXXXXXXX',
//                        "illegalCon": 'XXXXXXXXXXXXX',
//                        "admNoticeDate": '2016-12-1'
//                    },
//                    {
//                        "id":2,
//                        "admNoticeNum": 'XXXXXXXXXXXXX',
//                        "adProductName": 'XXXXXXXXXXXXX',
//                        "illegalCon": 'XXXXXXXXXXXXX',
//                        "admNoticeDate": '2016-12-1'
//                    },
//                    {
//                        "id":3,
//                        "admNoticeNum": 'XXXXXXXXXXXXX',
//                        "adProductName": 'XXXXXXXXXXXXX',
//                        "illegalCon": 'XXXXXXXXXXXXX',
//                        "admNoticeDate": '2016-12-1'
//                    }
                ],
                dataDetails:{
//                    "id":1,
//                    "proCategory":"xxxx",
//                    "adProductName":"xxxxx",
//                    "productName":"xxxx",
//                    "maEnterpriseName":"xxxx",
//                    "maEpLocation":"xxxx",
//                    "proApprovalNum":"xxxx",
//                    "illegalCon":"xxxx",
//                    "admNoticeNum":"xxxx",
//                    "admNoticeDate":"xxxx"
                }
            }
        },
        methods:{
            getList(params){//列表
                api.Get('/cloudMap/fda/getFalseAdNoticeList',params).then((res)=>{
                    console.log(res);
                    this.datalist =res.data;
                    this.totalRecord=res.totalRecord;
                })
                    .catch(err=>{
                        console.log('请求失败返回的错误信息');
                    })
            },
            handleCurrentChange(val) {//分页
                console.log(`当前页: ${val}`);
                this.getList({
                    entId:1,
                    entName:this.$router.currentRoute.params.entName,
                    page:val,
                    pageSize:this.pageSize
                })
            },
            showPopup(adNoticeId){//模态框
                this.dialogVisible = true;
                api.Get('/cloudMap/fda/getFalseAdNoticeDetails',{
                    entId:1,
                    entName:this.$router.currentRoute.params.entName,
                    adNoticeId: adNoticeId
                }).then((res)=>{
                    console.log(res);
                    this.dataDetails =res.data;
                })
            }
        },
        mounted(){
            this.getList({//列表
                entId:1,
                entName:this.$router.currentRoute.params.entName,
                page:1,
                pageSize:this.pageSize
            })
        }
    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    @import "../assets/css/tableCon.css";
    @import "../assets/css/dialog.css";
</style>



