<template>
    <div class="financecsrc">
        <!--证监会行政处罚-->
        <div class="overflowAuto">
            <table class="csrc-table">
                <thead class="csrc-thead">
                <tr>
                    <th>序号</th>
                    <th>文号</th>
                    <th>名称</th>
                    <th>发布机关</th>
                    <th>发文日期</th>
                </tr>
                </thead>
                <tbody class="csrc-tbody">
                <tr v-for="(item,index) in datalist">
                    <td>{{index+1}}<span></span></td>
                    <td class="csrc-td"><b @click="showPopup(item.id)">{{item.documentNum}}</b><span></span></td>
                    <td>{{item.financeName}} <span></span></td>
                    <td>{{item.publishingOrgan}}<span></span></td>
                    <td>{{item.dispatchDate}}</td>
                </tr>
                </tbody>
            </table>
        </div>

        <!--分页-->
        <div class="pageWrap"  v-if="this.totalRecord>this.pageSize">
            <el-pagination
                    @current-change="handleCurrentChange"
                    :current-page.sync="this.currentPage"
                    :page-size="this.pageSize"
                    layout="prev, pager, next, jumper"
                    :total="this.totalRecord">
            </el-pagination>
        </div>

        <!--弹框-->
        <el-dialog  :visible.sync="dialogVisible">
            <p class="title">证监会行政处罚详情</p>
            <!--这里写自己的内容-->
            <div class="dialog-con">
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">索引号：</span>
                    </h4>
                    <p class="item-value"><span>{{modalText.indexQuotes}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">分类：</span>
                    </h4>
                    <p class="item-value"><span>{{modalText.category}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">发布机构：</span>
                    </h4>
                    <p class="item-value"><span>{{modalText.publishingOrg}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">发文日期：</span>
                    </h4>
                    <p class="item-value"><span>{{modalText.postingDate}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">名称：</span>
                    </h4>
                    <p class="item-value"><span>{{modalText.financeName}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">文号：</span>
                    </h4>
                    <p class="item-value"><span>{{modalText.documentNum}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">行政处罚决定书全文：</span>
                    </h4>
                    <p class="item-value"><span>{{modalText.punishText}}</span></p>
                </div>
            </div>
        </el-dialog>
    </div>
</template>

<script>
    import * as api from '@/assets/js/api'
    export default {
        name: 'financcCsrc',
        data () {
            return {
                pageSize:50,
                currentPage:1,
                totalRecord:'',
                dialogVisible: false,
                datalist: [],
                modalText:{}
            }
        },
        methods:{
            handleCurrentChange(val) {//分页
                console.log(`当前页: ${val}`);
                this.getList({
                    entId:1,
                    entName:this.$router.currentRoute.params.entName,
                    page:val,
                    pageSize:this.pageSize
                })
            },
            showPopup(csrcPunishId ){//模态框
                this.dialogVisible = true;
                api.Get('/cloudMap/finance/getCsrcDetails',{
                    entId:1,
                    entName:this.$router.currentRoute.params.entName,
                    csrcPunishId: csrcPunishId
                }).then((res)=>{
                    console.log("模态框");
                    console.log(res);
                    this.modalText =res.data;
                })
            },
            //列表
            getList(params){//列表
                api.Get('/cloudMap/finance/getCsrcList',params).then((res)=>{
                    console.log("证监会行政处罚");
                    console.log(res);
                    this.datalist =res.data;
                    this.totalRecord=res.totalRecord;
                })
            }
        },
        mounted(){
            this.getList({//列表
                entId:1,
                entName:this.$router.currentRoute.params.entName,
                page:1,
                pageSize:this.pageSize
            })
        }
    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    /* 弹窗样式 */
    .dialog-con{
        max-height:600px;
        overflow:auto;
        overflow-x:hidden;
    }
    .dialog-con:hover::-webkit-scrollbar-thumb{
        background:rgba(7,100,180,1);
    }
    .dialog-item{
        color:#fff;
        margin-left:26px;
        margin-bottom:28px;
        margin-right: 30px;
    }
    .dialog-item .item-name{
        margin-bottom:8px;
    }
    .dialog-item .item-name span{
        height:15px;
        line-height:15px;
        font-size: 12px;
    }
    .dialog-item .item-name i{
        margin-left:1px;
        margin-right:6px;
        height:15px;
        width:2px;
        background:#00c2ff;
    }
    .dialog-item .item-value{
        display:block;
        word-wrap: break-word;
    }
    .financecsrc{
        width: 100%;
        height: 100%;
    }
    .csrc-table{
        width: 100%;
        border-collapse:separate;
        border-spacing:0 4px;
    }
    .csrc-thead th{
        height:34px;
        line-height: 34px;
        font-size: 12px;
        font-weight: bold;
        text-align: left;
        padding:0 10px;
        background: rgba(40,92,198,0.35);
        color: #cdd2de;
        border-top: 1px solid #284f9d;
        border-bottom: 1px solid #284f9d;
    }
    .csrc-thead th:first-child{
        width: 65px;
        text-align: center;
        border-left: 1px solid #284f9d;
    }
    .csrc-thead th:last-child{
        border-right: 1px solid #284f9d;
    }
    .csrc-tbody td{
        height:34px;
        line-height: 32px;
        font-size: 12px;
        color: #fff;
        text-align: left;
        padding:0 10px;
        background: rgba(88,143,255,0.15);
        border-top:1px solid rgba(99,150,255,0.35);
        border-bottom:1px solid rgba(99,150,255,0.35);
        box-sizing: border-box;
    }
    .csrc-tbody td:first-child{
        width: 65px;
        text-align: center;
    }
    .csrc-tbody tr td:first-child{
        border-left:1px solid rgba(99,150,255,0.35);
    }
    .csrc-tbody tr td:last-child{
        border-right:1px solid rgba(99,150,255,0.35);
    }
    .csrc-tbody td span{
        width:1px;
        height:25px;
        background: #214c9a;
        float: right;
        margin-top:5px;
    }
    .csrc-tbody td b{
        color: #fff;
        cursor: pointer;
        text-decoration: underline;
    }
    .csrc-tbody tr:hover {
        border:1px solid #2751a2;
        background:rgba(40,92,198,0.35);
        color:#cdd2de;
    }
    .overflowAuto{
        box-sizing: border-box;
        flex: auto;
        overflow: auto;
        overflow-x: hidden;
        height:424px;
        padding-right: 5px;
    }
    .overflowAuto:hover::-webkit-scrollbar-thumb{
        background:rgba(7,100,180,1)
    }
    .pageWrap{
        margin-top:10px;
    }
</style>



