<template>
    <div class="boot-page" >
        <!--知识产权出质登记信息-->
        <div class="intellectualPropertyInfo overflowAuto">
            <table>
                <tr>
                    <th class="firstTh">序号</th>
                    <th class="secondTh">登记编号</th>
                    <th class="thirdTh">知识产权名称</th>
                    <th class="fourthTh">出质人</th>
                    <th class="fifthTh">质权人</th>
                    <th class="lastTh">公示日期</th>
                </tr>
                <tr v-for="(item,index) in intellectualPropertyInfo">
                    <td class="firstTd">{{index+1}}</td>
                    <td  class="secondTd" @click="showPopup()">
                            {{item.regNumber}}
                    </td>
                    <td>
                            {{item.ipName}}
                    </td>
                    <td>
                            {{item.pledgor}}
                    </td>
                    <td>
                            {{item.pledgee}}
                    </td>
                    <td  class="lastTd">
                            {{item.publicityDate}}
                    </td>
                </tr>
            </table>
            <!--弹框-->
            <el-dialog  :visible.sync="dialogVisible">
                <p class="title">知识产权出质登记信息</p>
                <!--这里写自己的内容-->
                <div class="dialog-con">
                    <div class="writtenVerdict">
                        <div class="dialog-item">
                            <h4 class="clearfix item-name">
                                <i class="fl"></i>
                                <span class="titleBarName fl">登记编号：</span>
                            </h4>
                            <p class="item-value"><span>{{intellectualPropertyDetails.regNumber}}</span></p>
                        </div>
                        <div class="dialog-item">
                            <h4 class="clearfix item-name">
                                <i class="fl"></i>
                                <span class="titleBarName fl">登记日期：</span>
                            </h4>
                            <p class="item-value"><span>{{intellectualPropertyDetails.regDate}}</span></p>
                        </div>
                        <div class="dialog-item">
                            <h4 class="clearfix item-name">
                                <i class="fl"></i>
                                <span class="titleBarName fl">知识产权名称：</span>
                            </h4>
                            <p class="item-value"><span>{{intellectualPropertyDetails.ipName}}</span></p>
                        </div>
                        <div class="dialog-item">
                            <h4 class="clearfix item-name">
                                <i class="fl"></i>
                                <span class="titleBarName fl">种类：</span>
                            </h4>
                            <p class="item-value"><span>{{intellectualPropertyDetails.type}}</span></p>
                        </div>
                        <div class="dialog-item">
                            <h4 class="clearfix item-name">
                                <i class="fl"></i>
                                <span class="titleBarName fl">出质人：</span>
                            </h4>
                            <p class="item-value"><span>{{intellectualPropertyDetails.pledgor}}</span></p>
                        </div>
                        <div class="dialog-item">
                            <h4 class="clearfix item-name">
                                <i class="fl"></i>
                                <span class="titleBarName fl">质权人：</span>
                            </h4>
                            <p class="item-value"><span>{{intellectualPropertyDetails.pledgee}}</span></p>
                        </div>
                        <div class="dialog-item">
                            <h4 class="clearfix item-name">
                                <i class="fl"></i>
                                <span class="titleBarName fl">出质状态：</span>
                            </h4>
                            <p class="item-value"><span>{{intellectualPropertyDetails.status}}</span></p>
                        </div>
                        <div class="dialog-item">
                            <h4 class="clearfix item-name">
                                <i class="fl"></i>
                                <span class="titleBarName fl">公示日期：</span>
                            </h4>
                            <p class="item-value"><span>{{intellectualPropertyDetails.publicityDate}}</span></p>
                        </div>
                    </div>
                </div>
            </el-dialog>
        </div>
        <div class="pageWrap" v-if="this.totalRecord>100">
            <el-pagination
                    @size-change="handleSizeChange"
                    @current-change="handleCurrentChange"
                    :current-page.sync="currentPage3"
                    :page-size="this.pageSize"
                    layout="prev, pager, next, jumper"
                    :total="1000">
            </el-pagination>
        </div>
    </div>
</template>

<script>
    import * as api from '@/assets/js/api'
    export default {
        name: 'intellectualPropertyInfoList',
        mounted(){
            api.Get('/cloudMap/business/getIPList',{
                "entId": 1,
                "entName": this.entName,
                "pageSize":50,
                "page":1
            }).then((res)=>{
                console.log(res)
                this.totalRecord=res.totalRecord;
                this.intellectualPropertyInfo =res.data;
            })
        },
        data () {
            return {
                entName:this.$router.currentRoute.params.entName,
                totalRecord:0,
                pageSize:50,
                currentPage3:1,
                dialogVisible: false,
                intellectualPropertyInfo:[],
                intellectualPropertyDetails:{}
            }
        },
        methods:{
            handleSizeChange(val) {
                console.log(`每页 ${val} 条`);
            },
            handleCurrentChange(val) {
                console.log(`当前页: ${val}`);
                api.Get('/cloudMap/business/getIPList',{
                    "entId": 1,
                    "entName": this.entName,
                    "pageSize":50,
                    "page":val
                }).then((res)=>{
                    console.log(res)
                    this.totalRecord=res.totalRecord;
                    this.intellectualPropertyInfo =res.data;
                })
            },
            showPopup( ){
                this.dialogVisible = true;
                api.Get('/cloudMap/business/getIPDetails',{
                    "entId": 1,
                    "entName": this.entName
                }).then((res)=>{
                    console.log(res)
                    this.intellectualPropertyDetails =res.data;
                })
            }
        },
        components:{

        }
    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    @import 'assets/css/common.css';
    @import 'assets/css/tableStyle.css';
    @import 'assets/css/dialog.css';
    .secondTd{
        text-decoration: underline;
        cursor: pointer;
    }
    .dialog-item .item-value{
        text-align: left;
    }
    .writtenVerdict{
        padding:4px 15px 30px;
    }
</style>



