<template>
    <div class="boot-page" >
        <!--股东及出资信息-->
        <div class="shareholderInfo overflowAuto">
            <table  class="shareholderInfoTable">
                <tr>
                    <th class="firstTh">序号</th>
                    <th class="secondTh">股东类型</th>
                    <th class="thirdTh">股东</th>
                    <th class="fourthTh">认缴额</th>
                    <th class="fifthTh">实缴额</th>
                    <th class="lastTh">操作</th>
                </tr>
                <template v-for="(item,index) in shareholderInfo">
                    <tr class="mainTr">
                        <td class="firstTd">{{index+1}}</td>
                        <td>{{item.shareholderType}}</td>
                        <td>{{item.shareholder}}</td>
                        <td>{{item.subcribeAmount}}</td>
                        <td>{{item.paidAmount}}</td>
                        <!--<td class="lastTd"  @click="showTr(index)" :class="{'active':seen}" ref="tr">明细</td>-->
                        <td class="lastTd"  @click="showTr(index)" v-html="html">明细</td>
                    </tr>
                    <!--<transition name="fold">-->
                    <tr v-show="seen" class="example subTr" v-if="current==index">
                        <td></td>
                        <td></td>
                        <td></td>
                        <td class="subTd">
                            <ul class="subTdList" v-for="(val,i) in item.subcribeDetail">
                                <li>
                                    出资方式：{{val.paid_type}}
                                </li>
                                <li>
                                    出资额：{{val.paid_amount}}
                                </li>
                                <li>
                                    出资日期：{{val.paid_date}}
                                </li>
                            </ul>
                        </td>
                        <td class="subTd">
                            <ul class="subTdList" v-for="(val,i) in item.paidDetail">
                                <li>
                                    出资方式：{{val.paid_type}}
                                </li>
                                <li>
                                    出资额：{{val.paid_amount}}
                                </li>
                                <li>
                                    出资日期：{{val.paid_date}}
                                </li>
                            </ul>
                        </td>
                        <td></td>
                    </tr>
                    <!--</transition>-->
                </template>
            </table>
        </div>
        <div class="pageWrap" v-if="this.totalRecord>50">
            <el-pagination
                    @size-change="handleSizeChange"
                    @current-change="handleCurrentChange"
                    :current-page.sync="currentPage3"
                    :page-size="this.pageSize"
                    layout="prev, pager, next, jumper"
                    :total="1000">
            </el-pagination>
        </div>
    </div>
</template>

<script>
    import * as api from '@/assets/js/api'
    export default {
        name: 'shareholderInfoList',
        mounted(){
            api.Get('/cloudMap/business/getShareholderInfoList',{
                "entId": 1,
                "entName":this.entName,
                "pageSize":50,
                "page":1
            }).then((res)=>{
                console.log(res);
                this.totalRecord=res.totalRecord;
                this.shareholderInfo =res.data;
            })
        },
        data () {
            return {
                entName:this.$router.currentRoute.params.entName,
                totalRecord:0,
                pageSize:50,
                currentPage3:1,
                seen:false,
                html:'明细',
                current:-1,
                shareholderInfo:[]
            }
        },
        methods:{
            handleSizeChange(val) {
                console.log(`每页 ${val} 条`);
            },
            handleCurrentChange(val) {
                console.log(`当前页: ${val}`);
                api.Get('/cloudMap/business/getShareholderInfoList',{
                    "entId": 1,
                    "entName":this.entName,
                    "pageSize":50,
                    "page":val
                }).then((res)=>{
                    console.log(res.totalRecord);
                    this.totalRecord=res.totalRecord;
                    this.shareholderInfo =res.data;
                })
            },
            showTr(i){
                this.current=i;
                this.seen=!this.seen;
//                this.seen=true;
                if(this.seen){
                    event.target.classList.add('active');
                    event.target.innerHTML='收起';
                }else{
                    event.target.classList.remove('active');
                    event.target.innerHTML='明细';
                }

            },
//            hideTr(i){
//                this.current=i;
//                this.seen=false;
//            }
        },
        components:{

        }
    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    @import 'assets/css/common.css';
    /*股东及出资信息*/
    tr th{
        height:34px;
        background:rgba(40,92,198,0.35);
        color:#cdd2de;
        border-top:1px solid #284f9d;
        border-bottom:1px solid #284f9d;
        padding:0 20px;
        text-align: left;
    }
    tr td{
        height:34px;
        background:rgba(88,143,255,0.15);
        color:#fff;
        border:1px solid #325eb2;
        box-sizing: border-box;
        border-left:none;
        border-right:none;
        text-align: left;
        padding:0 20px;
    }
    /*tr:hover td{*/
        /*background:#1a3f83;*/
        /*opacity: 0.6;*/
        /*border:1px solid #325eb2;*/
        /*border-right:none;*/
        /*border-left:none;*/
        /*text-align: left;*/
        /*padding:0 20px;*/
    /*}*/
    tr .firstTd{
        border-left:1px solid #1e417a;
        text-align: left;
        padding:0 20px;
    }
    tr .lastTd{
        border-right:1px solid #1e417a;
    }
    tr .lastTd.active{
        border-right:1px solid #1e417a;
        color:#d6b92a;
    }
    tr:hover .firstTd{
        border-left:1px solid #1e417a;
        text-align: left;
        padding:0 20px;
    }
    tr:hover .lastTd{
        border-right:1px solid #1e417a;
    }
    .shareholderInfoTable .firstTh{
        width:10%;
    }
    .shareholderInfoTable .secondTh{
        width:15%;
    }
    .shareholderInfoTable .thirdTh{
        width:21%;
    }
    .shareholderInfoTable .fourthTh,.shareholderInfoTable .fifthTh{
        width:22%;
    }
    .shareholderInfoTable .lastTh{
        width:10%;
    }
    .shareholderInfoTable tr .lastTd{
        text-decoration: underline;
    }
    .shareholderInfoTable .mainTr:hover td{
        background: rgba(40,92,198,0.35);
    }
    .shareholderInfoTable .subTr{
        /*display: none;*/
        background: #123168;
    }
    .shareholderInfoTable .show{
        display: block;
    }
    .subTr .subTd{
        color:#fff;
        /*text-align: left;*/
        vertical-align: top;
    }
    .mainTr .lastTd{
        text-decoration: underline;
        cursor: pointer;
    }
    .subTr .lastTd{
        width:10%;
        color:#d6b92a;
        border-right:1px solid #213c6c;
        text-decoration: underline;
        vertical-align: top;
        cursor: pointer;
        text-align: left;
        padding-top:6px;
    }
    .subTdList{
        /*margin:6px 8% 3px 0%;*/
        padding-bottom: 6px;
        border-bottom:1px solid #1d4897;
    }
    /*.subTdList:last-child  {*/
    /*border:none;*/
    /*}*/
    /*.subTdList:nth-last-child(2){*/
    /*border:none;*/
    /*}*/
    .subTdList li{
        text-align: left;
        line-height:22px;
    }
</style>



