<template>
    <div class="boot-page">
        <div class="overflowAuto">
            <table class="court-table">
                <thead>
                    <tr>
                        <th class="tc w50">序号</th>
                        <th>证书编号</th>
                        <th>域名</th>
                        <th>类型</th>
                        <th class="w100">有效截至日期</th>
                    </tr>
                </thead>
                <tbody>
                    <tr v-for="(item,index) in datalist">
                        <td class="tc">{{index+1}}</td>
                        <td>
                            <a @click="showPopup(item.id)">{{item.certificateNum}}</a>
                        </td>
                        <td>{{item.domainName}}</td>
                        <td>{{item.typeStatus}}</td>
                        <td>{{item.expirationDate}}</td>
                    </tr>
                </tbody>
            </table>
        </div>

        <!--分页-->
        <div class="pageWrap"  v-if="this.totalRecord>this.pageSize">
            <el-pagination
                    @current-change="handleCurrentChange"
                    :current-page.sync="this.currentPage"
                    :page-size="this.pageSize"
                    layout="prev, pager, next, jumper"
                    :total="this.totalRecord">
            </el-pagination>
        </div>

        <!--弹框-->
        <el-dialog  :visible.sync="dialogVisible">
            <p class="title">{{title}}</p>
            <!--这里写自己的内容-->
            <div class="dialog-con">
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">证书编号：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.certificateNum}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">服务性质：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.serviceNature}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">机构名称：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.orgName}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">法定代表：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.legalRepresen}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">有效截至日期：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.expirationDate}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">地址和邮编：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.addressCode}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">网站域名：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.webDomainName}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">发证机关：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.issuingAuthority}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">有效起始日期：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.effeStartDate}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">网站负责人：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.siteManager}}</span></p>
                </div>
            </div>
        </el-dialog>
    </div>
</template>

<script type="es6">
    import * as api from '@/assets/js/api'
    export default {
        name: 'internetDrug',
        data () {
            return {
                dialogVisible: false,
                title:'互联网药品信息服务',
                pageSize:50,
                currentPage:1,
                totalRecord:'',
                datalist: [
//                    {
//                        "id": 1,
//                        "certificateNum":"XXXXXXXXXX",
//                        "domainName": "XXXXXXXXXXXXX",
//                        "typeStatus": "互联网药品信息服务",
//                        "expirationDate": "2016-12-1"
//                    },
//                    {
//                        "id": 2,
//                        "certificateNum":"XXXXXXXXXX",
//                        "domainName": "XXXXXXXXXXXXX",
//                        "typeStatus": "互联网药品信息服务",
//                        "expirationDate": "2016-12-1"
//                    },
//                    {
//                        "id": 3,
//                        "certificateNum":"XXXXXXXXXX",
//                        "domainName": "XXXXXXXXXXXXX",
//                        "typeStatus": "互联网药品信息服务",
//                        "expirationDate": "2016-12-1"
//                    }
                ],
                dataDetails:{
//                    "id":1,
//                    "certificateNum":"xxxxx",
//                    "serviceNature":"xxxxx",
//                    "orgName":"xxxxx",
//                    "legalRepresen":"xxxxx",
//                    "expirationDate":"xxxxx",
//                    "addressCode":"xxxxx",
//                    "webDomainName":"xxxxx",
//                    "issuingAuthority":"xxxxx",
//                    "effeStartDate":"xxxxx",
//                    "siteManager":"xxxxx"
                }
            }
        },
        methods:{
            getList(params){//列表
                api.Get('/cloudMap/fda/getInternetDrugList',params).then((res)=>{
                    console.log(res);
                    this.datalist =res.data;
                    this.totalRecord=res.totalRecord;
                })
                    .catch(err=>{
                        console.log('请求失败返回的错误信息');
                    })
            },
            handleCurrentChange(val) {//分页
                console.log(`当前页: ${val}`);
                this.getList({
                    entId:1,
                    entName:this.$router.currentRoute.params.entName,
                    page:val,
                    pageSize:this.pageSize
                })
            },
            showPopup(drugId){//模态框
                this.dialogVisible = true;
                api.Get('/cloudMap/fda/getInternetDrugDetails',{
                    entId:1,
                    entName:this.$router.currentRoute.params.entName,
                    drugId: drugId
                }).then((res)=>{
                    console.log(res);
                    this.dataDetails =res.data;
                })
            }
        },
        mounted(){
            this.getList({//列表
                entId:1,
                entName:this.$router.currentRoute.params.entName,
                page:1,
                pageSize:this.pageSize
            })
        }
    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    @import "./assets/css/tableCon.css";
    @import "./assets/css/dialog.css";
</style>



