<template>
    <div class="boot-page">
        <!--企业基本信息-->
        <div class="baseInfo overflowAuto">
            <h3 class="titleBar clearfix">
                <i class="fl"></i>
                <span class="titleBarName fl">
                企业基本信息
            </span>
            </h3>
            <table>
                <tr>
                    <td class="tdname">注册号</td>
                    <td class="tdcon borderRightNone">{{baseInfo.regNumber}}</td>
                    <td class="tdname">注册时间</td>
                    <td class="tdcon">{{baseInfo.regDate}}</td>
                </tr>
                <tr>
                    <td class="tdname">企业名称</td>
                    <td class="tdcon borderRightNone">{{baseInfo.entName}}</td>
                    <td class="tdname">经营期限</td>
                    <td class="tdcon">{{baseInfo.operateStart}} 至 {{baseInfo.operateEnd}}</td>
                </tr>
                <tr>
                    <td class="tdname">企业类型</td>
                    <td class="tdcon borderRightNone">{{baseInfo.entType}}</td>
                    <td class="tdname">登记机关</td>
                    <td class="tdcon">{{baseInfo.registerOrganization}}</td>
                </tr>
                <tr>
                    <td class="tdname">法定代表人</td>
                    <td class="tdcon borderRightNone">{{baseInfo.legalRepresentative}}</td>
                    <td class="tdname">核准日期</td>
                    <td class="tdcon">{{baseInfo.approvalDate}}</td>
                </tr>
                <tr>
                    <td class="tdname">注册资本</td>
                    <td class="tdcon borderRightNone">{{baseInfo.regAsset}}</td>
                    <td class="tdname">住所</td>
                    <td class="tdcon">{{baseInfo.residence}}</td>
                </tr>
                <tr>
                    <td class="tdname">成立日期</td>
                    <td class="tdcon borderRightNone">{{baseInfo.registerDate}}</td>
                    <td class="tdname">登记状态</td>
                    <td class="tdcon">{{baseInfo.regStatus}}</td>
                </tr>
                <tr>
                    <td class="tdname" colspan="4">经营范围</td>
                </tr>
                <tr>
                    <td class="tdcon lastTd" colspan="4">{{baseInfo.businessScope}}</td>
                </tr>
            </table>
        </div>
    </div>
</template>

<script>
    import * as api from '@/assets/js/api'
    export default {
        name: 'baseInfoList',
        mounted(){
            api.Get('/cloudMap/business/getBaseInfo',{
                "entId": 1,
                "entName": this.$router.currentRoute.params.entName
            }).then((res)=>{
                console.log(res)
                this.baseInfo =res.data;
            })
        },
        data () {
            return {
                baseInfo:{}
            }
        },
        methods:{

        },
        components:{

        }
    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    @import 'assets/css/common.css';
    .baseInfo td.tdname{
        height:34px;
        border:1px solid rgba(42,86,175,0.75);
        background:rgba(40,92,198,0.35);
        color:#cdd2de;
        text-align: left;
        padding-left:2%;
    }
    .baseInfo td.tdname:hover{
        height:34px;
        border:1px solid #2751a2;
        background:rgba(40,92,198,0.35);
        color:#cdd2de;
        text-align: left;
        padding-left:2%;
    }
    .baseInfo td.tdcon{
        height:34px;
        border:1px solid rgba(99,150,255,0.35);
        border-left:none;
        background:rgba(88,143,255,0.15);
        color:#fff;
        line-height:28px;
        text-align: left;
        padding-left:38px;
        box-sizing: border-box;
    }
    .baseInfo td.tdcon:hover{
        background: rgba(40,92,198,0.35);
        border:1px solid #325eb2;
        border-left:none;
        box-sizing: border-box;
        text-align: left;
        padding-left:38px;
    }
    .baseInfo td.lastTd{
        padding:13px 23px;
        vertical-align: top;
        text-indent:2em;
        border-left:1px solid rgba(99,150,255,0.35);
    }
    .baseInfo tr:hover td.lastTd{
        padding:13px 23px;
        vertical-align: top;
        text-indent:2em;
        border-left:1px solid rgba(99,150,255,0.35);
    }
    .baseInfo td.borderRightNone{
        border-right:none;
    }
    .baseInfo td.borderRightNone:hover{
        border-right:none;
    }

</style>



