<template>
    <div class="boot-page" >
        <!--专利-->
        <div class="patent">
            <div class="div-dataList">
            <table id="tb_data">
                <tr>
                    <th class="firstTh">序号</th>
                    <th class="secondTh">申请号</th>
                    <th class="thirdTh">专利类型</th>
                    <th class="fourthTh">公开（公告）日</th>
                    <th class="fifthTh">摘要</th>
                </tr>
                <tr v-for="(item,index) in patent">
                    <td class="firstTd">{{index+1}}</td>
                    <td  class="secondTd">
                            {{item.applyNumber}}
                    </td>
                    <td>
                            {{item.patentType}}
                    </td>
                    <td>
                            {{item.publicityDate}}
                    </td>
                    <td  class="lastTd">
                            <i @click="showPopup(item.id)">{{item.abstractTit}} </i><br/>
                            {{item.abstract}}
                    </td>
                </tr>
            </table>
            </div>
            <div class="div-pager" v-if="this.totalRecord>this.pageSize">
            <el-pagination
                    @size-change="handleSizeChange"
                    @current-change="handleCurrentChange"
                    :current-page.sync="this.currentPage"
                    :page-size="this.pageSize"
                    layout="prev, pager, next, jumper"
                    :total="this.totalRecord">
            </el-pagination>
            </div>
        </div>

        <!--弹框-->
        <el-dialog  :visible.sync="dialogVisible">
            <p class="title">专利详情</p>
            <!--这里写自己的内容-->
            <div class="dialog-con">
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">名称：</span>
                    </h4>
                    <p class="item-value"><span>{{abstractList.patent}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">专利类型：</span>
                    </h4>
                    <p class="item-value"><span>{{abstractList.patentType}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">申请号：</span>
                    </h4>
                    <p class="item-value"><span>{{abstractList.appNumber}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">申请日：</span>
                    </h4>
                    <p class="item-value"><span>{{abstractList.appDate}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">公开（公告）号：</span>
                    </h4>
                    <p class="item-value"><span>{{abstractList.publicNum}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">公开（公告）日：</span>
                    </h4>
                    <p class="item-value"><span>{{abstractList.publicityDate}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">分类号：</span>
                    </h4>
                    <p class="item-value"><span>{{abstractList.classificationNum}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">优先权：</span>
                    </h4>
                    <p class="item-value"><span>{{abstractList.priority}} </span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">申请（专利权）人：</span>
                    </h4>
                    <p class="item-value"><span>{{abstractList.applicant}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">发明（设计）人：</span>
                    </h4>
                    <p class="item-value"><span>{{abstractList.inventor}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">专利代理机构：</span>
                    </h4>
                    <p class="item-value"><span>{{abstractList.agency}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">代理人：</span>
                    </h4>
                    <p class="item-value"><span>{{abstractList.agent}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">地址：</span>
                    </h4>
                    <p class="item-value"><span>{{abstractList.entAddress}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">最新法律状态：</span>
                    </h4>
                    <p class="item-value"><span>{{abstractList.legalStatus}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">国际申请：</span>
                    </h4>
                    <p class="item-value"><span>{{abstractList.intApplication}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">国际公布：</span>
                    </h4>
                    <p class="item-value"><span>{{abstractList.intPublication}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">摘要：</span>
                    </h4>
                    <p class="item-value"><span>{{abstractList.abstract}}</span></p>
                </div>
            </div>
        </el-dialog>
    </div>
</template>

<script>
    import * as api from '@/assets/js/api.js'
    export default {
        name: 'patentList',
        data () {
            return {
                dialogVisible: false,
                totalRecord:100,
                pageSize:50,
                currentPage:1,
                patent:[
//                    {
//                        "applyNumber":"24532445234",
//                        "patentType":"发明公布",
//                        "publicityDate":"2012-11-11",
//                        "abstractTit":"一种基于大数据预测用户行为的控制方法及装置",
//                        "abstract":"本发明提供一种基于大数据预测用户行为的控制方法及装置，本发明提供一种基于大数据预测用户行为的控制方法及装置，本发明提供一种基于大数据预测用户行为的控制方法及装置。"
//                    },
//                    {
//                        "applyNumber":"24532445234",
//                        "patentType":"发明公布",
//                        "publicityDate":"2012-11-11",
//                        "abstractTit":"一种基于大数据预测用户行为的控制方法及装置",
//                        "abstract":"本发明提供一种基于大数据预测用户行为的控制方法及装置，本发明提供一种基于大数据预测用户行为的控制方法及装置，本发明提供一种基于大数据预测用户行为的控制方法及装置。"
//                    }
                ],
                abstractList:{
//                    "name":"分离乙醇发酵液的方法",
//                    "patentType":"发明专利",
//                    "appNumber":"CN201510061313.2",
//                    "appDate":"2015-02-05",
//                    "publicNum":"CN104610018A",
//                    "publicDate":"2015-02-05",
//                    "classificationNum":"C07C9/76（2006.1）",
//                    "priority":"--",
//                    "applicant":"中石化上海工程有限公司",
//                    "inventor":"缪晡,许天宇,陈栋",
//                    "agency":"上海申新律师事务所 31272",
//                    "agent":"张惠明",
//                    "address":"xx省   xx市   xx区",
//                    "legalStatus":"公开",
//                    "intApplication":"--",
//                    "intPublication":"--",
//                    "abstract":"本发明涉及一种分离乙醇发酵液的方法，主要解决现有技术中能耗高的问题。本发明通过采用一种分离乙醇发酵液的方法，从分子筛变压吸附器顶部排出的物流中的到少一份分经精馏塔可用于乙醇发酵液的分离中。"
                }
            }
        },
        mounted(){
            api.Get('/cloudMap/inAssetsInfo/getPatentList',{entId:1,entName:this.$router.currentRoute.params.entName,page:1,pageSize:this.pageSize}).then(res=>{
                this.patent=res.data;
                this.totalRecord=res.totalRecord;
                //console.log(this.$router.currentRoute.params.entName);
                console.log(res);
            });
        },
        methods:{
            showPopup(id){
                this.dialogVisible = true
                api.Get('/cloudMap/inAssetsInfo/getPatentInfo',
                    {id:id,entId:1,entName:this.$router.currentRoute.params.entName}).then(res=>{
                    this.abstractList=res.data;
                    //console.log(id);
                    console.log(res);
                });
            },
            handleSizeChange(val) {
                console.log(`每页 ${val} 条`);
            },
            handleCurrentChange(val) {
                console.log(`当前页: ${val}`);
                api.Get('/cloudMap/inAssetsInfo/getPatentList',
                    {entId:1,entName:this.$router.currentRoute.params.entName,page:val,pageSize:this.pageSize}).then(res=>{
                    this.patent=res.data;
                    this.totalRecord=res.totalRecord;
                    console.log(res);
                });

            },
        },
        components:{

        }
    }
        </script>

        <!-- Add "scoped" attribute to limit CSS to this component only -->
        <style scoped>
        @import 'assets/css/common.css';
    @import 'assets/css/tableStyle.css';
    .patent .firstTh{
        width:10%;
    }
    .patent .secondTh{
        width:15%;
    }
    .patent .thirdTh{
        width:15%;
    }
    .patent .fourthTh{
        width:16%
    }
    .patent .fifthTh{
        width:44%
    }
    .patent i{
        text-decoration: underline;
        cursor: pointer;
        display: inline-block;
        margin-bottom: 4px;
    }
    .lastTd{
        padding: 10px 20px;
    }
</style>



