<template>
    <div class="customsregister">
        <!--海关注册信息-->
        <div class="overflowAuto">
            <table class="register-table">
                <thead class="register-thead">
                    <tr>
                        <th>序号</th>
                        <th>海关编码</th>
                        <th>报关关别</th>
                        <th>企业管理类别</th>
                        <th>注册有效期</th>
                    </tr>
                </thead>
                <tbody class="register-tbody">
                    <tr v-for="(item,index) in datalist">
                        <td>{{index+1}}<span></span></td>
                        <td class="register-num">{{item.customCode}}<span></span></td>
                        <td>{{item.reportType}} <span></span></td>
                        <td>{{item.enterpriseAdminType}}<span></span></td>
                        <td>{{item.registrationPeriod}}</td>
                    </tr>
                </tbody>
            </table>
        </div>

        <!--分页-->
        <div class="pageWrap" v-if="this.totalRecord>this.pageSize">
            <el-pagination
                    @current-change="handleCurrentChange"
                    :current-page.sync="this.currentPage"
                    :page-size="this.pageSize"
                    layout="prev, pager, next, jumper"
                    :total="this.totalRecord">
            </el-pagination>
        </div>
    </div>
</template>

<script>
    import * as api from '@/assets/js/api'
    export default {
        name: 'customsRegister',
        data () {
            return {
                pageSize:50,
                currentPage:1,
                totalRecord:'',
                datalist: []
            }
        },
        methods: {
            //分页
            handleCurrentChange(val) {
                console.log(`当前页: ${val}`);
                this.getList({
                    entId:1,
                    entName:this.$router.currentRoute.params.entName,
                    page:val,
                    pageSize:this.pageSize
                })
            },
            //列表
            getList(params){//列表
                api.Get('/cloudMap/customs/getRegisterList',params).then((res)=>{
                    console.log("海关注册信息");
                    console.log(res);
                    this.datalist =res.data;
                    this.totalRecord=res.totalRecord;
                })
            }
        },
        mounted(){
            this.getList({//列表
                entId:1,
                entName:this.$router.currentRoute.params.entName,
                page:1,
                pageSize:this.pageSize
            })
        }
    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    .customsregister{
        width: 100%;
        height: 100%;
    }
    .register-table{
        width: 100%;
        border-collapse:separate;
        border-spacing:0 4px;
    }
    .register-thead th{
        height:34px;
        line-height: 34px;
        font-size: 12px;
        font-weight: bold;
        text-align: left;
        padding:0 10px;
        background: rgba(40,92,198,0.35);
        color: #cdd2de;
        border-top: 1px solid #284f9d;
        border-bottom: 1px solid #284f9d;
    }
    .register-thead th:first-child{
        width: 65px;
        text-align: center;
        border-left: 1px solid #284f9d;
    }
    .register-thead th:last-child{
        border-right: 1px solid #284f9d;
    }
    .register-tbody td{
        height:34px;
        line-height: 32px;
        color: #fff;
        font-size: 12px;
        text-align: left;
        padding:0 10px;
        background: rgba(88,143,255,0.15);
        border-top:1px solid rgba(99,150,255,0.35);
        border-bottom:1px solid rgba(99,150,255,0.35);
        box-sizing: border-box;
    }
    .register-tbody td:first-child{
        width: 65px;
        text-align: center;
    }
    .register-tbody tr td:first-child{
        border-left:1px solid rgba(99,150,255,0.35);
    }
    .register-tbody tr td:last-child{
        border-right:1px solid rgba(99,150,255,0.35);
    }
    .register-tbody td span{
        width:1px;
        height:25px;
        background: #214c9a;
        float: right;
        margin-top:5px;
    }
    .register-num{
        min-width:20%;
    }
    .register-tbody tr:hover {
        border:1px solid #2751a2;
        background:rgba(40,92,198,0.35);
        color:#cdd2de;
    }
    .overflowAuto{
        box-sizing: border-box;
        flex: auto;
        overflow: auto;
        height:424px;
        padding-right: 5px;
        overflow-x: hidden;
    }
    .overflowAuto:hover::-webkit-scrollbar-thumb{
        background:rgba(7,100,180,1)
    }
    .pageWrap{
        margin-top:10px;
    }
</style>



