<template>
    <div class="boot-page">
        <div class="overflowAuto">
            <table class="court-table">
                <thead>
                    <tr>
                        <th class="tc w50">序号</th>
                        <th>项目序号</th>
                        <th>产品名称</th>
                        <th>项目名称</th>
                        <th class="w100">认可日期</th>
                    </tr>
                </thead>
                <tbody>
                    <tr v-for="(item,index) in datalist">
                        <td class="tc">{{index+1}}</td>
                        <td>
                            <a @click="showPopup(item.id)">{{item.prjNum}}</a>
                        </td>
                        <td>{{item.productName}}</td>
                        <td>{{item.prjName}}</td>
                        <td>{{item.approvalDate}}</td>
                    </tr>
                </tbody>
            </table>
        </div>

        <!--分页-->
        <div class="pageWrap"  v-if="this.totalRecord>this.pageSize">
            <el-pagination
                    @current-change="handleCurrentChange"
                    :current-page.sync="this.currentPage"
                    :page-size="this.pageSize"
                    layout="prev, pager, next, jumper"
                    :total="this.totalRecord">
            </el-pagination>
        </div>

        <!--弹框-->
        <el-dialog  :visible.sync="dialogVisible">
            <p class="title">{{title}}</p>
            <!--这里写自己的内容-->
            <div class="dialog-con">
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">产品名称：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.productName}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">项目序号：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.prjNum}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">项目名称：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.prjName}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">检测标准名称：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.testStaName}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">检测标准编号：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.testStaCode}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">限制范围及说明：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.explain}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">检测单位：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.testStaCompany}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">省市：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.proCities}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">认可日期：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.approvalDate}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">有效期：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.termOfValidity}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">备注：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.remarks}}</span></p>
                </div>
            </div>
        </el-dialog>
    </div>
</template>

<script type="es6">
    import * as api from '@/assets/js/api'
    export default {
        name: 'inspectionDirectory',
        data () {
            return {
                dialogVisible: false,
                title:'医疗器械检测中心受检目录详情',
                pageSize:50,
                currentPage:1,
                totalRecord:'',
                datalist: [
//                    {
//                        "id": 1,
//                        "prjNum":"XXXXXXXXXX",
//                        "productName":"xxxxxxxxxxxxxxxxxx",
//                        "prjName":"xxxxxxxxxxxxxxxxxx",
//                        "approvalDate":"2016-12-1"
//                    },
//                    {
//                        "id": 3,
//                        "prjNum":"XXXXXXXXXX",
//                        "productName":"xxxxxxxxxxxxxxxxxx",
//                        "prjName":"xxxxxxxxxxxxxxxxxx",
//                        "approvalDate":"2016-12-1"
//                    },
//                    {
//                        "id": 3,
//                        "prjNum":"XXXXXXXXXX",
//                        "productName":"xxxxxxxxxxxxxxxxxx",
//                        "prjName":"xxxxxxxxxxxxxxxxxx",
//                        "approvalDate":"2016-12-1"
//                    }
                ],
                dataDetails:{
//                    "id":1,
//                    "productName":"xxxxx",
//                    "prjNum":"xxxx",
//                    "prjName":"xxx",
//                    "testStaName":"xxxx",
//                    "testStaCode":"xxx",
//                    "explain":"xxx",
//                    "testStaCompany":"xxx",
//                    "proCities":"xxx",
//                    "approvalDate":"xxx",
//                    "termOfValidity":"xxx",
//                    "remarks":"xxx"
                }
            }
        },
        methods:{
            getList(params){//列表
                api.Get('/cloudMap/fda/getInspectionDirectoryList',params).then((res)=>{
                    console.log(res);
                    this.datalist =res.data;
                    this.totalRecord=res.totalRecord;
                })
                    .catch(err=>{
                        console.log('请求失败返回的错误信息');
                    })
            },
            handleCurrentChange(val) {//分页
                console.log(`当前页: ${val}`);
                this.getList({
                    entId:1,
                    entName:this.$router.currentRoute.params.entName,
                    page:val,
                    pageSize:this.pageSize
                })
            },
            showPopup(inspectionId){//模态框
                this.dialogVisible = true;
                api.Get('/cloudMap/fda/getInspectionDirectoryDetails',{
                    entId:1,
                    entName:this.$router.currentRoute.params.entName,
                    inspectionId: inspectionId
                }).then((res)=>{
                    console.log(res);
                    this.dataDetails =res.data;
                })
            }
        },
        mounted(){
            this.getList({//列表
                entId:1,
                entName:this.$router.currentRoute.params.entName,
                page:1,
                pageSize:this.pageSize
            })
        }
    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    @import "../assets/css/tableCon.css";
    @import "../assets/css/dialog.css";
</style>



