<template>
    <div class="boot-page" >
        <!--股权出质信息-->
        <div class="equityCapitalInfo overflowAuto">
            <table>
                <tr>
                    <th class="firstTh">序号</th>
                    <th class="secondTh">登记编号</th>
                    <th class="thirdTh">出质人</th>
                    <th class="fourthTh">质权人</th>
                    <th class="fifthTh">出质股权数额</th>
                    <th class="lastTh">公示日期</th>
                </tr>
                <tr v-for="(item,index) in equityCapitalInfo">
                    <td class="firstTd">{{index+1}}</td>
                    <td  class="secondTd"  @click="showPopup()">
                            {{item.regNumber}}
                    </td>
                    <td>
                            {{item.pledgor}}
                    </td>
                    <td>
                            {{item.pledgee}}
                    </td>
                    <td>
                            {{item.pledgedStockAmount}}
                    </td>
                    <td  class="lastTd">
                            {{item.publicityDate}}
                    </td>
                </tr>
            </table>
            <!--弹框-->
            <el-dialog  :visible.sync="dialogVisible">
                <p class="title">股权出质信息</p>
                <!--这里写自己的内容-->
                <div class="dialog-con">
                    <div class="writtenVerdict">
                        <div class="dialog-item">
                            <h4 class="clearfix item-name">
                                <i class="fl"></i>
                                <span class="titleBarName fl">登记编号：</span>
                            </h4>
                            <p class="item-value"><span>{{equityCapitalDetails.regNumber}}</span></p>
                        </div>
                        <div class="dialog-item">
                            <h4 class="clearfix item-name">
                                <i class="fl"></i>
                                <span class="titleBarName fl">登记日期：</span>
                            </h4>
                            <p class="item-value"><span>{{equityCapitalDetails.regDate}}</span></p>
                        </div>
                        <div class="dialog-item">
                            <h4 class="clearfix item-name">
                                <i class="fl"></i>
                                <span class="titleBarName fl">状态：</span>
                            </h4>
                            <p class="item-value"><span>{{equityCapitalDetails.status}}</span></p>
                        </div>
                        <div class="dialog-item">
                            <h4 class="clearfix item-name">
                                <i class="fl"></i>
                                <span class="titleBarName fl">出质人：</span>
                            </h4>
                            <p class="item-value"><span>{{equityCapitalDetails.pledgor}}</span></p>
                        </div>
                        <div class="dialog-item">
                            <h4 class="clearfix item-name">
                                <i class="fl"></i>
                                <span class="titleBarName fl">出质人证照/证件号码：</span>
                            </h4>
                            <p class="item-value"><span>{{equityCapitalDetails.pledgorLisenceNumber}}</span></p>
                        </div>
                        <div class="dialog-item">
                            <h4 class="clearfix item-name">
                                <i class="fl"></i>
                                <span class="titleBarName fl">出质股权数额：</span>
                            </h4>
                            <p class="item-value"><span>{{equityCapitalDetails.pledgedStockAmount}}</span></p>
                        </div>
                        <div class="dialog-item">
                            <h4 class="clearfix item-name">
                                <i class="fl"></i>
                                <span class="titleBarName fl">质权人：</span>
                            </h4>
                            <p class="item-value"><span>{{equityCapitalDetails.pledgee}}</span></p>
                        </div>
                        <div class="dialog-item">
                            <h4 class="clearfix item-name">
                                <i class="fl"></i>
                                <span class="titleBarName fl">质权人证照/证件号码：</span>
                            </h4>
                            <p class="item-value"><span>{{equityCapitalDetails.pledgeeLisenceNumber}}</span></p>
                        </div>
                        <div class="dialog-item">
                            <h4 class="clearfix item-name">
                                <i class="fl"></i>
                                <span class="titleBarName fl">公示日期：</span>
                            </h4>
                            <p class="item-value"><span>{{equityCapitalDetails.publicityDate}}</span></p>
                        </div>
                    </div>
                </div>
            </el-dialog>
        </div>
        <div class="pageWrap" v-if="this.totalRecord>50">
            <el-pagination
                    @size-change="handleSizeChange"
                    @current-change="handleCurrentChange"
                    :current-page.sync="currentPage3"
                    :page-size="this.pageSize"
                    layout="prev, pager, next, jumper"
                    :total="1000">
            </el-pagination>
        </div>
    </div>
</template>

<script>
    import * as api from '@/assets/js/api'
    export default {
        name: 'equityCapitalInfoList',
        mounted(){
            api.Get('/cloudMap/business/getEquityCapitalList',{
                "entId": 1,
                "entName": this.entName,
                "pageSize":50,
                "page":1
            }).then((res)=>{
                console.log(res)
                this.totalRecord=res.totalRecord;
                this.equityCapitalInfo =res.data;
            })
        },
        data () {
            return {
                totalRecord:0,
                pageSize:50,
                currentPage3:1,
                dialogVisible: false,
                equityCapitalInfo:[],
                equityCapitalDetails:{}
            }
        },
        methods:{
            handleSizeChange(val) {
                console.log(`每页 ${val} 条`);
            },
            handleCurrentChange(val) {
                console.log(`当前页: ${val}`);
                api.Get('/cloudMap/business/getEquityCapitalList',{
                    "entId": 1,
                    "entName": this.entName,
                    "pageSize":50,
                    "page":val
                }).then((res)=>{
                    console.log(res)
                    this.totalRecord=res.totalRecord;
                    this.equityCapitalInfo =res.data;
                })
            },
            showPopup( ){
                this.dialogVisible = true;
                api.Get('/cloudMap/business/getEquityCapitalDetails',{
                    "entId": 1,
                    "entName":this.entName
                }).then((res)=>{
                    console.log(res);
                    this.equityCapitalDetails =res.data;
                })
            }
        },
        components:{

        }
    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    @import 'assets/css/common.css';
    @import 'assets/css/tableStyle.css';
    @import 'assets/css/dialog.css';
    .secondTd{
        text-decoration: underline;
        cursor: pointer;
    }
    .dialog-item .item-value{
        text-align: left;
    }
    .writtenVerdict{
        padding:4px 15px 30px;
    }

</style>



