<template>
    <div class="boot-page">
        <!--法院公告-->
        <div class="overflowAuto">
            <table class="court-table">
                <thead>
                    <tr>
                        <th class="tc w50">序号</th>
                        <th>公告类型</th>
                        <th>公告人</th>
                        <th>当事人</th>
                        <th class="w100">发布时间</th>
                        <th></th>
                    </tr>
                </thead>
                <tbody>
                    <tr v-for="(list,index) in datalist">
                        <td class="tc">{{index+1}}</td>
                        <td>
                            <a @click="showPopup(list.anId)">{{list.noticeType}}</a>
                        </td>
                        <td>{{list.noticePeople}}</td>
                        <td>{{list.litigant}}</td>
                        <td>{{list.releaseDate}}</td>
                        <td>
                            <a @click="showPopup(list.anId)">详情</a>
                        </td>
                    </tr>
                </tbody>
            </table>
        </div>

        <!--分页-->
        <div class="pageWrap"  v-if="this.totalRecord>this.pageSize">
            <el-pagination
                    @current-change="handleCurrentChange"
                    :current-page.sync="this.currentPage"
                    :page-size="this.pageSize"
                    layout="prev, pager, next, jumper"
                    :total="this.totalRecord">
            </el-pagination>
        </div>


        <!--弹框-->
        <el-dialog  :visible.sync="dialogVisible">
            <p class="title">{{title}}</p>
            <!--这里写自己的内容-->
            <div class="dialog-con">
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">公告类型：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.noticeType}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">公告人：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.noticePeople}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">当事人：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.litigant}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">公告时间：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.announceDate}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">正文：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.mainText}}</span></p>
                </div>
            </div>
        </el-dialog>
    </div>
</template>

<script type="es6">
    import * as api from '@/assets/js/api.js'
    export default {
        name: 'courtAnnouncement',
        data () {
            return {
                dialogVisible: false,
                title:'法院公告信息',
                pageSize:50,
                currentPage:1,
                totalRecord:'',
                datalist: [],
                dataDetails: []
            }
        },
        methods:{
            handleCurrentChange(val) {//分页
                console.log(`当前页: ${val}`);
                this.getList({
                    entId:1,
                    entName:this.$router.currentRoute.params.entName,
                    page:val,
                    pageSize:this.pageSize
                })
            },
            showPopup(id){//模态框
                this.dialogVisible = true;
                api.Get('/cloudMap/court/getCourtAnnouncementDetails',{
                    "entName": this.$router.currentRoute.params.entName,
                    "anId":id
                })
                    .then(res=>{
                        console.log('请求成功返回的结果'+res.code);
                        console.log(res.data);
                        this.dataDetails = res.data;
                    })
                    .catch(err=>{
                        console.log('请求失败返回的错误信息');
                    })
            },
            getList(params){//列表
                api.Get('/cloudMap/court/getCourtAnnouncementList',params).then((res)=>{
                    console.log(res);
                    console.log('请求成功返回的结果'+res.code);
                    this.datalist = res.data;
                }).catch(err=>{
                    console.log('请求失败返回的错误信息');
                })
            },
        },
        mounted(){
            this.getList({//列表
                entId:1,
                entName:this.$router.currentRoute.params.entName,
                page:1,
                pageSize:this.pageSize
            })
        }
    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    @import "./assets/css/tableCon.css";
    @import "./assets/css/dialog.css";
</style>



