<template>
    <div class="search-wrap" @click.self="closeBox">
        <div class="content-wrap">
            <!--<span class="close" @click="closeBox">&times;</span>-->
            <span class="close" @click="closeBox"></span>
            <div class="condition-wrap">
                <p class="label-line">筛选</p>
                <div class="condition">
                    <ul>
                        <li  v-for="(item,index) in conditionList">
                            <span class="condition-label">{{item}}：</span>
                            <el-select v-model="selectValue[index]" placeholder="请选择" popper-class="search" @change="filterList">
                                <el-option
                                        v-for="item1 in options"
                                        :key="item1.value"
                                        :label="item1.label"
                                        :value="item1.value">
                                </el-option>
                            </el-select>
                        </li>
                    </ul>
                </div>
            </div>
            <div class="result-wrap">
                <div class="result-count">
                    <p class="multiple-wrap">
                        <span class="multiple-label">检索条件</span>
                        <span class="multiple-condition">综合条件：<span class="condition-value">{{conditionValue}}</span></span>
                    </p>
                    <p class="totalRecord">共找到<span class="resultCount">&nbsp;{{totalRecord}}&nbsp;</span>条相关记录</p>
                </div>
                <div class="list-wrap">
                    <ul>
                        <li v-if="resultList.length > 0" v-for="item in resultList">
                            <p class="entName" @click="getEntInfo($event)">{{item.entName}}</p>
                            <p class="entInfo">
                                <span class="legalPerson">法定代表人：{{item.legalRepresentative}}</span>
                                <span class="registerTime">成立：{{item.registerTime}}</span>
                                <span class="regAsset">注册资本：{{item.regAsset}}</span>
                            </p>
                        </li>
                        <li v-else>暂无数据</li>
                    </ul>
                </div>
                <!--分页-->
                <div class="pageWrap"  v-if="totalRecord > pageSize">
                    <el-pagination
                            @current-change="handleCurrentChange"
                            :current-page.sync="currentPage"
                            :page-size="pageSize"
                            layout="prev, pager, next, jumper"
                            :total="totalRecord">
                    </el-pagination>
                </div>
            </div>
        </div>
    </div>
</template>

<script>
    import * as api from '@/assets/js/api'
    export default {
        name: 'search',
        props:['conditionValue','dataList'],
        data(){
            return {
                conditionList:['年报','专利','法律诉讼','分公司','著作权','失信记录','企业网址','商标','行政处罚'],
                selectValue:[2,2,2,2,2,2,2,2,2],
                options:[
                    {
                        value: 2,
                        label: '不限'
                    },{
                        value: 1,
                        label: '有'
                    },{
                        value: 0,
                        label: '无'
                    }
                ],
                totalRecord: '',
                pageSize: 50,
                currentPage: 1,
                resultList:[]
            }
        },
        mounted(){
            if(this.dataList.code){
                this.totalRecord = this.dataList.totalRecord
                this.resultList= this.dataList.data
            } else{
                if(this.conditionValue !== ''){
                    this.getList({
                        entName: this.conditionValue,
                        page: 1,
                        pageSize: this.pageSize
                    })
                }
            }
        },
        methods:{
            closeBox(){
                this.$emit('closeBox')
            },
            getList(params){
                api.Get('/cloudMap/entSearch/getEntList',params).then((res)=>{
                    this.totalRecord = res.totalRecord
                    this.resultList = res.data;
                })
            },
            filterList(){
                this.getList({
                    entName: this.conditionValue,
                    annualReport: this.selectValue[0],
                    patent: this.selectValue[1],
                    legalProceedings: this.selectValue[2],
                    branch: this.selectValue[3],
                    copyright: this.selectValue[4],
                    breakRecords: this.selectValue[5],
                    entUrl: this.selectValue[6],
                    trademark: this.selectValue[7],
                    administrativeSanction: this.selectValue[8],
                    page: 1,
                    pageSize: this.pageSize
                })
            },
            getEntInfo(e){
                let name = e.target.innerHTML
                this.$emit('getEntInfo',name)
            },
            //分页
            handleCurrentChange(val) {
                this.getList({
                    entName: this.conditionValue,
                    page: val,
                    pageSize: this.pageSize
                })
            }
        },
        watch:{
            conditionValue(val,oldVal){
                console.log(val)
                this.getList({
                    entName: val,
                    page: 1,
                    pageSize: this.pageSize
                })
//            conditionValue:{
//                handler(val,oldVal){
//                    this.getList({
//                        entName: val,
//                        page: 1,
//                        pageSize: this.pageSize
//                    })
//                },
//                deep:true,
//                immediate:true
//            }
            }
        }
    }
</script>

<style scoped>

.search-wrap{
    position: relative;
    width: 100%;
    height: 100%;
    z-index: 10;
    background: rgba(3,38,95,.9);
}
.content-wrap{
    position: absolute;
    box-sizing: border-box;
    top: 0;
    right:0;
    bottom: 0;
    left: 0;
    margin: auto;
    width: 1262px;
    height: 740px;
    /*padding: 90px 65px 56px 55px;*/
    padding: 90px 65px 40px 55px;
    background: url("../../../static/img/search-wrap-bg.png") center no-repeat;
    background-size: 100% 100%;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: stretch;
}
.content-wrap .close{
    position: absolute;
    /*top: 15px;*/
    /*right: 17px;*/
    /*width: 20px;*/
    /*height: 20px;*/
    top: 15px;
    right: 15px;
    width: 30px;
    height: 30px;
    /*line-height: 15px;*/
    /*color: #ebd91e;*/
    /*font-size: 40px;*/
    cursor: pointer;
    display: flex;
    justify-content: center;
    /*transition: all .5s;*/
    /*background: #34b3b5;*/
    /*box-shadow: 0 0 20px 8px #34b3b5;*/
    background: url("../../../static/img/close-icon.png") center no-repeat;
}
/*.content-wrap .close:hover{*/
    /*color:#faf92d;*/
/*}*/
.condition-wrap{
    height: 148px;
    flex: none;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: stretch;
}
.condition-wrap .label-line{
    box-sizing: border-box;
    border-left: 3px solid #ffcc00;
    padding-left: 10px;
    color: #ffcc00;
    height: 14px;
    font-size: 14px;
    display: flex;
    justify-content: flex-start;
    align-items: center;
}
.condition-wrap .condition{
    margin-top: 10px;
    flex: auto;
    box-sizing: border-box;
    border: 1px solid #00beff;
    display: flex;
    justify-content: center;
    align-items: stretch;
}
.condition-wrap .condition ul{
    flex: auto;
    display: flex;
    justify-content: flex-start;
    align-items: center;
    flex-wrap: wrap;
    align-content: space-around;
}
.condition-wrap .condition li{
    flex: none;
    display: flex;
    justify-content: center;
    align-items: center;
    margin-left: 38px;
}
.condition-wrap .condition li .condition-label{
    width: 75px;
    color: #fff;
    font-size: 14px;
    display: flex;
    justify-content: flex-end;
    align-items: center;
}
.result-wrap{
    margin-top: 16px;
    flex: auto;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: stretch;
}
.result-wrap  .result-label{
    color: #fff;
    font-size: 16px;
    flex: none;
    margin-bottom: 15px;
}
.result-wrap  .list-wrap{
    flex: auto;
    overflow: auto;
    display: flex;
    flex-direction: column;
    justify-content: space-between;
    align-items: stretch;
    overflow: auto;
    overflow-x: hidden;
    padding-right: 5px;
}
.result-wrap  .list-wrap:hover::-webkit-scrollbar-thumb{
    background:rgba(7,100,180,1)
}
.result-wrap  .result-count .multiple-wrap{
    padding-left: 12px;
    height: 35px;
    flex: none;
    background: rgba(3,76,126,.5);
    display: flex;
    justify-content: flex-start;
    align-items: center;
}
.result-wrap  .result-count .multiple-label{
    width: 86px;
    height: 24px;
    border: 1px solid #00cbff;
    color: #fff;
    font-size: 14px;
    background: linear-gradient(#029ed2,#0178d2);
    display: flex;
    justify-content: center;
    align-items: center;
    margin-right: 15px;
}
.result-wrap  .result-count .multiple-condition{
    flex: auto;
    color: #e2e2e2;
    font-size: 14px;
}
.result-wrap  .result-count .condition-value{
    color: #fff600;
}
.result-wrap  .result-count .totalRecord{
    color: #fff;
    font-size: 12px;
    text-align: right;
    margin: 8px 0;
}
.result-wrap  .result-count .resultCount{
    color: #d5d123;
}
.result-wrap  .list-wrap ul{
    flex: auto;
    display: flex;
    flex-direction: column;
    justify-content: flex-start;
    align-items: stretch;
}
.result-wrap  .list-wrap li{
    padding-left: 12px;
    display: flex;
    flex-direction: column;
    justify-content: space-around;
    align-items: flex-start;
    height: 80px;
    margin-bottom: 6px;
    flex: none;
    background: url("../../../static/img/result-list-bg.png") center no-repeat;
    background-size: 100% 100%;
}
.result-wrap  .list-wrap li span{
    color: #fff;
    font-size: 12px;
}
.result-wrap  .list-wrap li .entName{
    color: #fff600;
    font-size: 16px;
    cursor: pointer;
}
.result-wrap  .list-wrap li .registerTime{
    margin: 0 22px;
}
.pageWrap{
    padding-top: 10px;
}
</style>



