<template>
    <div class="boot-page" >
        <div class="companyFileList">
            <!-- 分红情况 -->
            <div class="bonusInfo">
                <h3 class="titleBar clearfix">
                    <i class="fl"></i>
                    <span class="titleBarName fl">
                        分红情况
                    </span>
                </h3>
                <table>
                    <tr>
                        <th class="firstTh">报告期</th>
                        <th>董事会日期</th>
                        <th>股东大会预案公告日期</th>
                        <th>实施日期</th>
                        <th>分红方案说明</th>
                        <th>A股股权登记日</th>
                        <th>A股除权除息日</th>
                        <th>方案进度</th>
                        <th class="w8">股利支付率</th>
                        <th class="lastTh w8">分红率</th>
                    </tr>
                    <tr v-for="(item,index) in shareOutBonus">
                        <td class="firstTd">{{item.reportDate}}</td><!--报告期-->
                        <td>{{item.boardDate}}</td><!--董事会日期-->
                        <td>{{item.noticeDate}}</td><!--股东大会预案公告日期-->
                        <td>{{item.implementDate}}</td><!--实施日期-->
                        <td>{{item.bonusSolutions}}</td><!--分红方案说明-->
                        <td>{{item.registrationDate}}</td><!--A股股权登记日-->
                        <td>{{item.exDividendDate}}</td><!--A股除权除息日-->
                        <td>{{item.planProgress}}</td><!--方案进度-->
                        <td>{{item.paymentRate}}</td> <!--股利支付率-->
                        <td class="lastTd">{{item.shareOutBonusRate}}</td><!--分红率-->
                    </tr>
                </table>
            </div>
            <!--增发概况-->
            <div  class="baseInfo">
                <h3 class="titleBar clearfix">
                    <i class="fl"></i>
                    <span class="titleBarName fl">
                        增发概况
                    </span>
                    <a class="fr btn btn-show" v-show="showTable" @click="showTableFn('show')">展开</a>
                    <a class="fr btn btn-hide" v-show="hideTable" @click="showTableFn('hide')">收起</a>
                </h3>
                <div class="summaryItem" v-show="showTable">
                    <p>该股自上市以来累计增发<span class="totalNum">{{rightsIssueTotal.cumulative}}</span> 次，其中成功 <span class="totalNum">{{rightsIssueTotal.successful}}</span> 次，失败 <span class="totalNum">{{rightsIssueTotal.fail}}</span> 次，进行中 <span class="totalNum">{{rightsIssueTotal.inProgress}}</span> 次，累计实际募资净额为 <span class="totalNum">{{rightsIssueTotal.actualFundraising}}</span> 元。</p>
                </div>
                <div v-for="(increaseList,index) in increaseRightsIssue" :class="index==0? show : hide">
                    <table>
                        <tr>
                            <td class="tdname">方案进度</td>
                            <td class="tdcon borderRightNone">{{increaseList.projectProgress}}</td>
                            <td class="tdname">发行类型</td>
                            <td class="tdcon borderRightNone">{{increaseList.issueType}}</td>
                            <td class="tdname">发行方式</td>
                            <td class="tdcon ">{{increaseList.issueWay}}</td>
                        </tr>
                        <tr>
                            <td class="tdname">实际发行价格</td>
                            <td class="tdcon borderRightNone" colspan="2">{{increaseList.actualPrice}}</td>
                            <td class="tdname">新股上市公告日</td>
                            <td class="tdcon" colspan="2">{{increaseList.listedDate}}</td>
                        </tr>
                        <tr>
                            <td class="tdname">实际发行数量</td>
                            <td class="tdcon borderRightNone" colspan="2">{{increaseList.actualQuantity}}</td>
                            <td class="tdname">发行新股日</td>
                            <td class="tdcon" colspan="2">{{increaseList.issueDate}}</td>
                        </tr>
                        <tr>
                            <td class="tdname">实际募资净额</td>
                            <td class="tdcon borderRightNone" colspan="2">{{increaseList.actualFundraising}}</td>
                            <td class="tdname">证监会核准公告</td>
                            <td class="tdcon" colspan="2">{{increaseList.approvedDate}}</td>
                        </tr>
                        <tr>
                            <td class="tdname">预案发行价格</td>
                            <td class="tdcon borderRightNone" colspan="2">{{increaseList.preReleasePrice}}</td>
                            <td class="tdname">发审委公告日</td>
                            <td class="tdcon" colspan="2">{{increaseList.committeeDate}}</td>
                        </tr>
                        <tr>
                            <td class="tdname">预案发行数量</td>
                            <td class="tdcon borderRightNone" colspan="2">{{increaseList.preReleaseQuantity}}</td>
                            <td class="tdname">股东大会公告日</td>
                            <td class="tdcon" colspan="2">{{increaseList.shareholdersDate}}</td>
                        </tr>
                        <tr>
                            <td class="tdname">预案募资金额</td>
                            <td class="tdcon borderRightNone" colspan="2">{{increaseList.planFundraising}}</td>
                            <td class="tdname">董事会公告日</td>
                            <td class="tdcon" colspan="2">{{increaseList.directorsDate}}</td>
                        </tr>
                    </table>
                </div>
            </div>
            <!--配股概况-->
            <div  class="baseInfo">
                <h3 class="titleBar clearfix">
                    <i class="fl"></i>
                    <span class="titleBarName fl">
                        配股概况
                    </span>
                </h3>
                <table v-for="item in rightsIssue">
                    <tr>
                        <td class="tdname">方案进度</td>
                        <td class="tdcon borderRightNone">{{item.projectProgress}}</td>
                        <td class="tdname">配股代码</td>
                        <td class="tdcon borderRightNone">{{item.rightsIssueCode}}</td>
                        <td class="tdname">配股简称</td>
                        <td class="tdcon borderRightNone">{{item.abbreviation}}</td>
                        <td class="tdname">配股年份</td>
                        <td class="tdcon borderRightNone">{{item.rightsIssueYear}}</td>
                        <td class="tdcon"></td>
                    </tr>
                    <tr>
                        <td class="tdname">实际配股比例</td>
                        <td class="tdcon borderRightNone" colspan="2">{{item.actualProportion}}</td>
                        <td class="tdname">配股上市日</td>
                        <td class="tdcon borderRightNone" colspan="2">{{item.stockListingDate}}</td>
                        <td class="tdname">证监会核准公告日</td>
                        <td class="tdcon" colspan="2">{{item.csrcApprovalDate}}</td>
                    </tr>
                    <tr>
                        <td class="tdname">每股配股价格</td>
                        <td class="tdcon borderRightNone" colspan="2">{{item.unitPrice}}</td>
                        <td class="tdname ">缴款起止日</td>
                        <td class="tdcon borderRightNone" colspan="2">{{item.paymentDate}}</td>
                        <td class="tdname">发审委公告日</td>
                        <td class="tdcon" colspan="2">{{item.porcAnnouncementDate}}</td>
                    </tr>
                    <tr>
                        <td class="tdname">实际募集资金净额</td>
                        <td class="tdcon" colspan="8">{{item.actualMoney}}</td>
                    </tr>
                    <tr>
                        <td class="tdname">预案配股比例上限</td>
                        <td class="tdcon borderRightNone" colspan="2">{{item.preProportion}}</td>
                        <td class="tdname">除权日</td>
                        <td class="tdcon borderRightNone" colspan="2">{{item.exRightDate}}</td>
                        <td class="tdname">股东大会公告日</td>
                        <td class="tdcon" colspan="2">{{item.shareholdersDate}}</td>
                    </tr>
                    <tr>
                        <td class="tdname">预案募资金额上限</td>
                        <td class="tdcon borderRightNone" colspan="2">{{item.preMoney}}</td>
                        <td class="tdname">股权登记日</td>
                        <td class="tdcon borderRightNone" colspan="2">{{item.equityRegistrationDate}}</td>
                        <td class="tdname">董事会公告日</td>
                        <td class="tdcon" colspan="2">{{item.directorsDate}}</td>
                    </tr>
                </table>
            </div>
        </div>
    </div>
</template>

<script>
    import * as api from '@/assets/js/api'
    export default {
        name: 'bonusIssueList',
        mounted(){
//主营介绍
            api.Get('/cloudMap/listedCompany/getBonusInfoList', {
                "entId": 1,
                "entName": this.entName,
            }).then((res) => {
                this.shareOutBonus = res.data;
            })
//增发概况
            api.Get('/cloudMap/listedCompany/getIncreaseRightsIssue', {
                "entId": 1,
                "entName": this.entName,
            }).then((res) => {
                this.increaseRightsIssue = res.data.increaseRightsIssue;
                this.rightsIssueTotal = res.data.rightsIssueTotal;
            })
//配股概况
            api.Get('/cloudMap/listedCompany/getRightsIssue', {
                "entId": 1,
                "entName": this.entName,
            }).then((res) => {
                this.rightsIssue = res.data;
            })
        },
        data () {
            return {
                showTable:true,
                hideTable:false,
                show:'show',
                hide:'hide',
                shareOutBonus:[],
                rightsIssueTotal:{},
                increaseRightsIssue:[],
                rightsIssue:[]
            }
        },
        methods:{
            showTableFn(state){
                if(state=='show'){
                    this.showTable=false;
                    this.hideTable=true;
                    this.hide='showHide';
                }else{
                    this.showTable=true;
                    this.hideTable=false;
                    this.hide='hide'
                }
            }
        }
    }
</script>
<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    @import 'assets/css/common.css';
    .btn{
        display:block;
        width:48px;
        height:20px;
        line-height:20px;
        text-align:center;
        font-size:14px;
        cursor:pointer;
        -webkit-border-radius: 3px;
        -moz-border-radius: 3px;
        border-radius: 3px;
        margin-right:4px;
        color:rgba(255,255,255,0.7);
        text-decoration:none;
    }
    .btn:hover{
        opacity:.8;
    }
    .btn-show{
        border:1px solid rgba(0,156,255,0.85);
        background: rgba(0,150,255,0.45);
    }
    .btn-hide{
        border:1px solid #ff8718;
        border-top-color:#ffc233;
        border-bottom-color:#ff8718;
        background:-webkit-gradient(linear, 0 top, 0 bottom, from(#ad791e), to(#ad8d33));
    }
    .show,.showHide{display:block;}
    .hide{display:none;}
    .companyFileList table{
        margin-bottom:22px;
    }
    /*分红情况*/
    .bonusInfo th{
        padding:0 0px !important;
        width:10%;
    }
    .bonusInfo .w8{
        width:8%;
    }
    .bonusInfo td {
        padding:0 0px !important;
    }
    .bonusInfo .firstTh, .bonusInfo .firstTd{
        text-align: center;
    }
    /*参股控股公司*/
    .totalNum{color:#0287de;}
    .summaryItem{color:#fff;}
    /*配股概况    增发概况*/
    .baseInfo td.tdname{
        height:34px;
        border:1px solid rgba(42,86,175,0.75);
        background:rgba(40,92,198,0.35);
        color:#cdd2de;
        text-align: left;
        padding-left:2%;
    }
    .baseInfo td.tdname:hover{
        height:34px;
        border:1px solid #2751a2;
        background:rgba(40,92,198,0.35);
        color:#cdd2de;
        text-align: left;
        padding-left:2%;
    }
    .baseInfo td.tdcon{
        height:34px;
        border:1px solid rgba(99,150,255,0.35);
        border-left:none;
        background:rgba(88,143,255,0.15);
        color:#fff;
        line-height:28px;
        text-align: left;
        padding-left:38px;
        box-sizing: border-box;
    }
    .baseInfo td.tdcon:hover{
        background: rgba(40,92,198,0.35);
        border:1px solid #325eb2;
        border-left:none;
        box-sizing: border-box;
    }
    .baseInfo td.lastTd{
        padding:13px 23px;
        vertical-align: top;
        text-indent:2em;
        border-left:1px solid rgba(99,150,255,0.35);
    }
    .baseInfo tr:hover td.lastTd{
        padding:13px 23px;
        vertical-align: top;
        text-indent:2em;
        border-left:1px solid rgba(99,150,255,0.35);
    }
    .baseInfo td.borderRightNone{
        border-right:none;
    }
    .baseInfo td.borderRightNone:hover{
        border-right:none;
    }

</style>



