<template>
    <div class="qualityconsumption">
        <!--消费品产品质量国家监督抽查结果-->
        <div class="overflowAuto">
            <table class="consumption-table">
                <thead class="consumption-thead">
                <tr>
                    <th>序号</th>
                    <th>生产日期/批号</th>
                    <th>产品名称</th>
                    <th>产品等级</th>
                    <th>抽查结果</th>
                </tr>
                </thead>
                <tbody class="consumption-tbody">
                <tr v-for="(item,index) in datalist">
                    <td>{{index+1}}<span></span></td>
                    <td><b  @click="showPopup(item.id)">{{item.manufactureDate}}</b><span></span></td>
                    <td>{{item.productName}}<span></span></td>
                    <td>{{item.productLevel}}<span></span></td>
                    <td class="consumption-td">{{item.checkResult}}</td>
                </tr>
                </tbody>
            </table>
        </div>

        <!--分页-->
        <div class="pageWrap"  v-if="this.totalRecord>this.pageSize">
            <el-pagination
                    @current-change="handleCurrentChange"
                    :current-page.sync="this.currentPage"
                    :page-size="this.pageSize"
                    layout="prev, pager, next, jumper"
                    :total="this.totalRecord">
            </el-pagination>
        </div>

        <!--弹框-->
        <el-dialog  :visible.sync="dialogVisible">
            <p class="title">消费品产品质量国家监督抽查结果</p>
            <!--这里写自己的内容-->
            <div class="dialog-con">
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">产品名称：</span>
                    </h4>
                    <p class="item-value"><span>{{checkResult.productName}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">产品等级：</span>
                    </h4>
                    <p class="item-value"><span>{{checkResult.productLevel}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">商标：</span>
                    </h4>
                    <p class="item-value"><span>{{checkResult.trademark}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">规格型号：</span>
                    </h4>
                    <p class="item-value"><span>{{checkResult.specifications}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">生产日期/批号：</span>
                    </h4>
                    <p class="item-value"><span>{{checkResult.manufactureDate}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">抽查结果：</span>
                    </h4>
                    <p class="item-value"><span>{{checkResult.checkResult}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">主要不合格项目：</span>
                    </h4>
                    <p class="item-value"><span>{{checkResult.nonconProject}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">承检机构：</span>
                    </h4>
                    <p class="item-value"><span>{{checkResult.approvalInstitution}}</span></p>
                </div>
            </div>
        </el-dialog>
    </div>
</template>

<script>
    import * as api from '@/assets/js/api'
    export default {
        name: 'qualityDetails',
        data () {
            return {
                pageSize:50,
                currentPage:1,
                totalRecord:'',
                dialogVisible: false,
                datalist: [],
                checkResult:{}
            }
        },
        methods:{
            //分页
            handleCurrentChange(val) {
                console.log(`当前页: ${val}`);
                this.getList({
                    entId:1,
                    entName:this.$router.currentRoute.params.entName,
                    page:val,
                    pageSize:this.pageSize
                })
            },
            showPopup(resultListId){//模态框
                this.dialogVisible = true;
                api.Get('/cloudMap/quality/getQualityDetails',{
                    entId:1,
                    entName:this.$router.currentRoute.params.entName,
                    resultListId:resultListId
                }).then((res)=>{
                    console.log("模态框");
                    this.checkResult =res.data;
                })
            },
            getList(params){//列表
                api.Get('/cloudMap/quality/getQualityList',params).then((res)=>{
                    console.log(res);
                    this.datalist =res.data;
                    this.totalRecord=res.totalRecord;
                })
            }
        },
        mounted(){
            this.getList({//列表
                entId:1,
                entName:this.$router.currentRoute.params.entName,
                page:1,
                pageSize:this.pageSize
            })
        }
    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    /* 弹窗样式 */
    .dialog-con{
        max-height:600px;
        overflow:auto;
        overflow-x:hidden;
    }
    .dialog-con:hover::-webkit-scrollbar-thumb{
        background:rgba(7,100,180,1);
    }
    .dialog-item{
        color:#fff;
        margin-left:26px;
        margin-bottom:28px;
        margin-right: 30px;
    }
    .dialog-item .item-name{
        margin-bottom:8px;
    }
    .dialog-item .item-name span{
        height:15px;
        line-height:15px;
        font-size: 12px;
    }
    .dialog-item .item-name i{
        margin-left:1px;
        margin-right:6px;
        height:15px;
        width:2px;
        background:#00c2ff;
    }
    .dialog-item .item-value{
        display:block;
        word-wrap: break-word;
    }
    .qualityconsumption{
        width: 100%;
        height: 100%;
    }
    .consumption-table{
        width: 100%;
        border-collapse:separate;
        border-spacing:0 4px;
    }
    .consumption-thead th{
        height:34px;
        line-height: 34px;
        font-size: 12px;
        font-weight: bold;
        text-align: left;
        padding:0 10px;
        background: rgba(40,92,198,0.35);
        color: #cdd2de;
        border-top: 1px solid #284f9d;
        border-bottom: 1px solid #284f9d;
    }
    .consumption-thead th:first-child{
        width: 65px;
        text-align: center;
        border-left: 1px solid #284f9d;
    }
    .consumption-thead th:last-child{
        border-right: 1px solid #284f9d;
    }
    .consumption-tbody td{
        height:34px;
        line-height: 32px;
        color: #fff;
        font-size: 12px;
        text-align: left;
        padding:0 10px;
        background: rgba(88,143,255,0.15);
        border-top:1px solid rgba(99,150,255,0.35);
        border-bottom:1px solid rgba(99,150,255,0.35);
        box-sizing: border-box;
    }
    .consumption-tbody tr td:first-child{
        width: 65px;
        text-align: center;
        border-left:1px solid rgba(99,150,255,0.35);
    }
    .consumption-tbody tr td:last-child{
        border-right:1px solid rgba(99,150,255,0.35);
    }
    .consumption-tbody td span{
        width:1px;
        height:25px;
        background: #214c9a;
        float: right;
        margin-top:5px;
    }
    .consumption-tbody td b{
        color: #fff;
        text-decoration: underline;
        cursor: pointer;
    }
    .consumption-tbody tr:hover {
        border:1px solid #2751a2;
        background:rgba(40,92,198,0.35);
        color:#cdd2de;
    }
    .overflowAuto{
        box-sizing: border-box;
        flex: auto;
        overflow: auto;
        overflow-x: hidden;
        height:424px;
        padding-right: 5px;
    }
    .overflowAuto:hover::-webkit-scrollbar-thumb{
        background:rgba(7,100,180,1)
    }
    .pageWrap{
        margin-top:10px;
    }
</style>



