<template>
    <div class="boot-page" >
    <!--分支机构信息-->
        <div class="branchInfo overflowAuto">
            <table>
                <tr>
                    <th class="firstTh">序号</th>
                    <th>注册号</th>
                    <th>分支机构</th>
                    <th class="lastTh">登记机关</th>
                </tr>
                <tr v-for="(item,index) in branchInfo">
                    <td class="firstTd">{{index+1}}</td>
                    <td class="secondeTd">{{item.regNumber}}</td>
                    <td>{{item.branch}}</td>
                    <td class="lastTd">{{item.registerOrganization}}</td>
                </tr>
            </table>
        </div>
        <div class="pageWrap" v-if="this.totalRecord>50">
            <el-pagination
                    @size-change="handleSizeChange"
                    @current-change="handleCurrentChange"
                    :current-page.sync="currentPage3"
                    :page-size="this.pageSize"
                    layout="prev, pager, next, jumper"
                    :total="1000">
            </el-pagination>
        </div>
    </div>
</template>

<script>
    import * as api from '@/assets/js/api'
    export default {
        name: 'branchInfoList',
        mounted(){
            api.Get('/cloudMap/business/getBranchInfoList',{
                "entId": 1,
                "entName":this.entName,
                "pageSize":50,
                "page":1
            }).then((res)=>{
                console.log(res)
                this.totalRecord=res.totalRecord;
                this.branchInfo =res.data;
            })
        },
        data () {
            return {
                entName:this.$router.currentRoute.params.entName,
                totalRecord:0,
                pageSize:50,
                currentPage3:1,
                branchInfo:[]
            }
        },
        methods:{
            handleSizeChange(val) {
                console.log(`每页 ${val} 条`);
            },
            handleCurrentChange(val) {
                console.log(`当前页: ${val}`);
                api.Get('/cloudMap/business/getBranchInfoList',{
                    "entId": 1,
                    "entName":this.entName,
                    "pageSize":50,
                    "page":val
                }).then((res)=>{
                    console.log(res)
                    this.totalRecord=res.totalRecord;
                    this.branchInfo =res.data;
                })
            }
        },
        components:{

        }
    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    @import 'assets/css/common.css';
    @import 'assets/css/tableStyle.css';
    .branchInfo {
        width: 100%;
        height: 424px;
    }
</style>



