<template>
    <div class="boot-page">
        <!--环境问题督办通知-->
        <div class="overflowAuto">
            <table class="court-table">
                <thead>
                <tr>
                    <th class="tc w50">序号</th>
                    <th>情况简述</th>
                    <th class="w100">通报时间</th>
                </tr>
                </thead>
                <tbody>
                <tr v-for="(list,index) in datalist">
                    <td class="tc">{{index+1}}</td>
                    <td>
                        <a @click="showPopup(list.noticeId)">{{list.brIntroductd}}</a></td>
                    <td>{{list.notificationTime}}</td>
                </tr>
                </tbody>
            </table>
        </div>

        <!--分页-->
        <div class="pageWrap" v-if="this.totalRecord>this.pageSize">
            <el-pagination
                    @current-change="handleCurrentChange"
                    :current-page.sync="this.currentPage"
                    :page-size="this.pageSize"
                    layout="prev, pager, next, jumper"
                    :total="this.totalRecord">
            </el-pagination>
        </div>

        <!--弹框-->
        <el-dialog  :visible.sync="dialogVisible">
            <p class="title">{{title}}</p>
            <!--这里写自己的内容-->
            <div class="dialog-con">
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">企业名称：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.entName}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">通报时间：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.notificationTime}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">情况简述：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.brIntroductd}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">所属行政区：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.admArea}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">督办级别：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.supervisionLevel}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">督办单位：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.supervisionOrgan}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">解决措施要点：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.majorSolution}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">解决时限：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.solutionDeadline}}</span></p>
                </div>
            </div>
        </el-dialog>
    </div>
</template>

<script type="es6">
    import * as api from '@/assets/js/api.js'
    export default {
        name: 'overseeNoticeList',
        data () {
            return {
                dialogVisible: false,
                title:'通知详情',
                pageSize:50,
                currentPage:1,
                totalRecord:'',
                datalist: [],
                dataDetails:{
//                    "entName":"器经史了白存证严低车样眼头一话为来",
//                    "notificationTime":"1982-01-02",
//                    "brIntroductd":"府思保风进划没手选委商建使收导斯眼。飞压东先己性值思直动交头干亲。西组都种果相六第满物维至级照相一。取劳定走象验按于任细适克。",
//                    "admArea":"华南",
//                    "supervisionLevel":"作此劳传离省必本万金点社酸五参提己红斯资因值便么线内始",
//                    "supervisionOrgan":"头放想斗机划代院定",
//                    "majorSolution":"空九断看先江正识研带二圆起而正领原",
//                    "solutionDeadline":"1977-12-01"
                }
            }
        },
        methods:{
            handleCurrentChange(val) {//分页
                console.log(`当前页: ${val}`);
                this.getList({
                    entId:1,
                    entName:this.$router.currentRoute.params.entName,
                    page:val,
                    pageSize:this.pageSize
                })
            },
            showPopup(id){//模态框
                this.dialogVisible = true;
                api.Get('/cloudMap/ep/getOverseeNoticeDetails',{
                    "entName": this.$router.currentRoute.params.entName,
                    "noticeId":id
                })
                .then(res=>{
                    console.log('请求成功返回的结果'+res.code);
                    console.log(res)
                    this.dataDetails = res.data;
                })
                .catch(err=>{
                    console.log('请求失败返回的错误信息');
                })
            },
            getList(params){//列表
                api.Get('/cloudMap/ep/getOverseeNoticeList',params).then((res)=>{
                    console.log(res);
                    console.log('请求成功返回的结果'+res.code);
                    this.datalist = res.data;
                    this.totalRecord=res.totalRecord;
                })
                .catch(err=>{
                    console.log('请求失败返回的错误信息');
                })
            }
        },
        mounted(){
            this.getList({//列表
                entId:1,
                entName:this.$router.currentRoute.params.entName,
                page:1,
                pageSize:this.pageSize
            })
        }
    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    @import "./assets/css/tableCon.css";
    @import "./assets/css/dialog.css";
</style>



