<template>
    <div class="boot-page" >
        <!--ICP备案-->
        <div class="icpRecordList">
            <div class="div-dataList">
              <table>
                <tr>
                    <th class="firstTh">序号</th>
                    <th class="secondTh">网址</th>
                    <th class="thirdTh">网站名称</th>
                    <th class="fourthTh">网站备案号</th>
                    <th class="fifthTh">审核时间</th>
                </tr>
                <tr v-for="(item,index) in icpRecord">
                    <td class="firstTd">{{index+1}}</td>
                    <td>
                            {{item.siteAdd}}
                    </td>
                    <td class="thirdTd" @click="showPopup( )">
                            {{item.entName}}
                    </td>
                    <td>
                            {{item.websiteRecordNum}}
                    </td>
                    <td  class="lastTd">
                            {{item.auditDate}}
                    </td>
                </tr>
              </table>
            </div>
            <div class="div-pager" v-if="this.totalRecord>this.pageSize">
                <el-pagination
                        @size-change="handleSizeChange"
                        @current-change="handleCurrentChange"
                        :current-page.sync="this.currentPage"
                        :page-size="this.pageSize"
                        layout="prev, pager, next, jumper"
                        :total="this.totalRecord">
                </el-pagination>
            </div>
        </div>

        <!--弹框-->
        <el-dialog  :visible.sync="dialogVisible">
            <p class="title">ICP备案</p>
            <!--这里写自己的内容-->
            <div class="dialog-con">
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">单位名称：</span>
                    </h4>
                    <p class="item-value"><span>{{recordList.entName}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">主体编号：</span>
                    </h4>
                    <p class="item-value"><span>{{recordList.subjectNum}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">单位性质：</span>
                    </h4>
                    <p class="item-value"><span>{{recordList.unitProperty}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">主体备案号：</span>
                    </h4>
                    <p class="item-value"><span>{{recordList.recordNum}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">网站编号：</span>
                    </h4>
                    <p class="item-value"><span>{{recordList.siteNum}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">网站名称：</span>
                    </h4>
                    <p class="item-value"><span>{{recordList.siteName}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">

                        <i class="fl"></i>
                        <span class="titleBarName fl">网站负责人：</span>
                    </h4>
                    <p class="item-value"><span>{{recordList.siteManager}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">网站地址：</span>
                    </h4>
                    <p class="item-value"><span>{{recordList.siteAdd}} </span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">域名：</span>
                    </h4>
                    <p class="item-value"><span>{{recordList.domainName}} </span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">网站备案号：</span>
                    </h4>
                    <p class="item-value"><span>{{recordList.websiteRecordNum}} </span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">审核时间：</span>
                    </h4>
                    <p class="item-value"><span>{{recordList.auditTime}} </span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">内容类型：</span>
                    </h4>
                    <p class="item-value"><span>{{recordList.contentType}} </span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">证件类型：</span>
                    </h4>
                    <p class="item-value"><span>{{recordList.documentType}} </span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">证件号码：</span>
                    </h4>
                    <p class="item-value"><span>{{recordList.identificationNumber}} </span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">省：</span>
                    </h4>
                    <p class="item-value"><span>{{recordList.province}} </span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">市：</span>
                    </h4>
                    <p class="item-value"><span>{{recordList.city}} </span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">县：</span>
                    </h4>
                    <p class="item-value"><span>{{recordList.county}} </span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">详细地址：</span>
                    </h4>
                    <p class="item-value"><span>{{recordList.detailedAdd}} </span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">域名编号：</span>
                    </h4>
                    <p class="item-value"><span>{{recordList.domainnameNum}} </span></p>
                </div>
            </div>
        </el-dialog>
    </div>
</template>

<script>
    import * as api from '@/assets/js/api.js'
    export default {
        name: 'icpRecordList',
        data () {
            return {
                dialogVisible: false,
                totalRecord:100,
                pageSize:50,
                currentPage:1,
                icpRecord:[
//                    {
//                        "siteAdd":"www.thtf.com.cn",
//                        "entName":"同方股份有限公司",
//                        "websiterecordNum":"京ICP备09081660号-1",
//                        "auditDate":"2011-11-11"
//                    },
//                    {
//                        "website":"www.thtf.com.cn",
//                        "entName":"同方股份有限公司",
//                        "websiteRecordNum":"京ICP备09081660号-1",
//                        "auditDate":"2011-11-11"
//                    },
                ],
                recordList:{
//                    "entName":"XXXXXXXXX",
//                    "subjectNum":"XXXXXXXXX",
//                    "unitProperty":"XXXXXXXXX",
//                    "recordNum":"XXXXXXXXX",
//                    "siteNum":"XXXXXXXXX",
//                    "siteName":"XXXXXXXXX",
//                    "siteManager":"XXXXXXXXX",
//                    "siteAdd":"XXXXXXXXX",
//                    "domainName":"XXXXXXXXX",
//                    "websiterecordNum":"XXXXXXXXX",
//                    "auditTime":"XXXXXXXXX",
//                    "contentType":"XXXXXXXXX",
//                    "documentType":"XXXXXXXXX",
//                    "identificationNumber":"XXXXXXXXX",
//                    "province":"XXXXXXXXX",
//                    "city":"XXXXXXXXX",
//                    "county":"XXXXXXXXX",
//                    "detailedAdd":"XXXXXXXXX",
//                    "domainnameNum":"XXXXXXXXX"
                }
            }
        },
        mounted(){
//            this.showPopup()
            api.Get('/cloudMap/inAssetsInfo/getIcpRecordList',{page:1,pageSize:this.pageSize,entId:1,entName:this.$router.currentRoute.params.entName}).then(res=>{
                this.icpRecord=res.data;
                this.totalRecord=res.totalRecord;
                //console.log(res);
            });
        },
        methods:{
            showPopup(id){
                this.dialogVisible = true
                api.Get('/cloudMap/inAssetsInfo/getIcpRecordInfo', {id: id, entId: 1, entName:this.$router.currentRoute.params.entName}).then(res => {
                    this.recordList = res.data;
                    //console.log(id);
                    //console.log(res);
                });
            },
            handleSizeChange(val) {
                console.log(`每页 ${val} 条`);
            },
            handleCurrentChange(val) {
                console.log(`当前页: ${val}`);
                api.Get('/cloudMap/inAssetsInfo/getIcpRecordList',{entId:1,entName:this.$router.currentRoute.params.entName,page:val,pageSize:this.pageSize}).then(res=>{
                    this.icpRecord=res.data;
                    this.totalRecord=res.totalRecord;
                    console.log(res);
                });
            },
        },
        components:{

        }
    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    @import 'assets/css/common.css';
    @import 'assets/css/tableStyle.css';
    .thirdTd{
        text-decoration: underline;
        cursor: pointer;
    }
    .icpRecordList .firstTh{
        width:10%;
    }
    .icpRecordList .secondTh{
        width:25%;
    }
    .icpRecordList .thirdTh{
        width:25%;
    }
    .icpRecordList .fourthTh{
        width:25%;
    }
    .icpRecordList .fifthTh{
        width:15%;
    }
    .icpRecordList span{
        text-align: center;
        float: right;
        width:calc(100% - 40px);
        margin: 10px 0;
        margin-right:0px;
        padding: 0 15px;
        border-right: 1px solid #214c99;
        display: inline-block;
    }
    .icpRecordList .lastSpan{
        border-right: none;
        text-align: left;
    }

</style>



