<template>
    <div class="boot-page">
        <!--进口化妆品-->
        <div class="overflowAuto">
            <table class="court-table">
                <thead>
                    <tr>
                        <th class="tc w50">序号</th>
                        <th>批准文号</th>
                        <th>产品名称（中文）</th>
                        <th class="w100">批件有效期</th>
                        <th class="w100">批准日期</th>
                    </tr>
                </thead>
                <tbody>
                    <tr v-for="(item,index) in datalist">
                        <td class="tc">{{index+1}}</td>
                        <td>
                            <a @click="showPopup(item.id)">{{item.approvalNo}}</a>
                        </td>
                        <td>{{item.cnProductName}}</td>
                        <td>{{item.effectiveDate}}</td>
                        <td>{{item.approvalDate}}</td>
                    </tr>
                </tbody>
            </table>
        </div>

        <!--分页-->
        <div class="pageWrap"  v-if="this.totalRecord>this.pageSize">
            <el-pagination
                    @current-change="handleCurrentChange"
                    :current-page.sync="this.currentPage"
                    :page-size="this.pageSize"
                    layout="prev, pager, next, jumper"
                    :total="this.totalRecord">
            </el-pagination>
        </div>

        <!--弹框-->
        <el-dialog  :visible.sync="dialogVisible">
            <p class="title">{{title}}</p>
            <!--这里写自己的内容-->
            <div class="dialog-con">
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">产品名称（中文）：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.cnProductName}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">批件状态：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.approvalState}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">产品名称（英文）：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.enProductName}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">产品类别：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.proCategory}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">生产国（地区）：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.proCountry}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">生产企业（中文）：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.chManufactEnt}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">生产企业（英文）：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.enManufactEnt}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">生产企业地址：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.manufactAddress}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">在华申报责任单位：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.chReportUnit}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">在华责任单位地址：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.chReportUnitAds}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">批准文号：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.approvalNum}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">批准日期：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.approvalDate}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">批件有效期：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.effectiveDate}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">备注：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.remarks}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">产品名称备注：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.proNameRemarks}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">产品技术要求：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.skillRequire}}</span></p>
                </div>
            </div>
        </el-dialog>
    </div>
</template>

<script type="es6">
    import * as api from '@/assets/js/api'
    export default {
        name: 'importCosmetics',
        data () {
            return {
                dialogVisible: false,
                title:'进口化妆品',
                pageSize:50,
                currentPage:1,
                totalRecord:'',
                datalist: [
//                    {
//                        "id":1,
//                        "approvalNo": 'XXXXXXXXXXXXXX',
//                        "cnProductName": 'XXXXXXXXXXXXXXXXXXXXXXXXXXXX',
//                        "effectiveDate": '3年',
//                        "approvalDate": '2016-12-1'
//                    },
//                    {
//                        "id":2,
//                        "approvalNo": 'XXXXXXXXXXXXXX',
//                        "cnProductName": 'XXXXXXXXXXXXXXXXXXXXXXXXXXXX',
//                        "effectiveDate": '3年',
//                        "approvalDate": '2016-12-1'
//                    }
                ],
                dataDetails:{
//                    "id":1,
//                    "cnProductName":"xxxxx",
//                    "approvalState":"xxxxx",
//                    "enProductName":"xxxxx",
//                    "proCategory":"xxxxx",
//                    "proCountry":"xxxxx",
//                    "chManufactEnt":"xxxxx",
//                    "enManufactEnt":"xxxxx",
//                    "manufactAddress":"xxxxx",
//                    "chReportUnit":"xxxxx",
//                    "chReportUnitAds":"xxxxx",
//                    "approvalNum":"xxxxx",
//                    "approvalDate":"xxxxx",
//                    "effectiveDate":"xxxxx",
//                    "remarks":"xxxxx",
//                    "proNameRemarks":"xxxxx",
//                    "skillRequire":"xxxxx"
                }
            }
        },
        methods:{
            getList(params){//列表
                api.Get('/cloudMap/fda/getImportCosmeticsList',params).then((res)=>{
                    console.log(res);
                    this.datalist =res.data;
                    this.totalRecord=res.totalRecord;
                })
                    .catch(err=>{
                        console.log('请求失败返回的错误信息');
                    })
            },
            handleCurrentChange(val) {//分页
                console.log(`当前页: ${val}`);
                this.getList({
                    entId:1,
                    entName:this.$router.currentRoute.params.entName,
                    page:val,
                    pageSize:this.pageSize
                })
            },
            showPopup(cosmeticsId){//模态框
                this.dialogVisible = true;
                api.Get('/cloudMap/fda/getImportCosmeticsDetails',{
                    entId:1,
                    entName:this.$router.currentRoute.params.entName,
                    cosmeticsId: cosmeticsId
                }).then((res)=>{
                    console.log(res);
                    this.dataDetails =res.data;
                })
            }
        },
        mounted(){
            this.getList({//列表
                entId:1,
                entName:this.$router.currentRoute.params.entName,
                page:1,
                pageSize:this.pageSize
            })
        }
    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    @import "../assets/css/tableCon.css";
    @import "../assets/css/dialog.css";
</style>



