<template>
    <div class="boot-page">
        <!--广告获批信息-->
        <div class="overflowAuto">
            <table class="court-table">
                <thead>
                    <tr>
                        <th class="tc w50">序号</th>
                        <th>广告批准文号</th>
                        <th>广告发布内容</th>
                        <th>广告内容类型</th>
                        <th class="w220">广告有效期</th>
                    </tr>
                </thead>
                <tbody>
                    <tr v-for="(item,index) in datalist">
                        <td class="tc">{{index+1}}</td>
                        <td>
                            <a @click="showPopup(item.id)">{{item.adApprovalNo}}</a>
                        </td>
                        <td>{{item.adReleaseCon}}</td>
                        <td>{{item.adConType}}</td>
                        <td>{{item.adValidity}}</td>
                    </tr>
                </tbody>
            </table>
        </div>

        <!--分页-->
        <div class="pageWrap"  v-if="this.totalRecord>this.pageSize">
            <el-pagination
                    @current-change="handleCurrentChange"
                    :current-page.sync="this.currentPage"
                    :page-size="this.pageSize"
                    layout="prev, pager, next, jumper"
                    :total="this.totalRecord">
            </el-pagination>
        </div>

        <!--弹框-->
        <el-dialog  :visible.sync="dialogVisible">
            <p class="title">{{title}}</p>
            <!--这里写自己的内容-->
            <div class="dialog-con">
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">药品广告批准文号：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.drugApprovalNo}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">单位名称：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.unitName}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">地址：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.unitAddress}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">邮政编码：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.postalCode}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">通用名称：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.genericName}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">商品名称：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.commodityName}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">商标名称：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.brandName}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">处方分类：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.preClassification}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">广告类别：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.adCategories}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">时长：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.whenLong}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">广告有效期：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.adValidity}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">广告发布内容：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.adReleaseCon}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">批准文号：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.approvalNo}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">备注：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.remarks}}</span></p>
                </div>
            </div>
        </el-dialog>
    </div>
</template>

<script type="es6">
    import * as api from '@/assets/js/api'
    export default {
        name: 'adApprovedInfo',
        data () {
            return {
                dialogVisible: false,
                title:'药品广告获批信息',
                pageSize:50,
                currentPage:1,
                totalRecord:'',
                datalist: [
                    {
                        "id":1,
                        "adApprovalNo":"XXXXXXXXXXXXX",
                        "adReleaseCon":"XXXXXXXXXXXXX",
                        "adConType": "药品广告",
                        "adValidityStart": "2011-12-20 至 2012-12-19"
                    },
                    {
                        "id":3,
                        "adApprovalNo":"XXXXXXXXXXXXX",
                        "adReleaseCon":"XXXXXXXXXXXXX",
                        "adConType": "药品广告",
                        "adValidity": "2011-12-20 至 2012-12-19"
                    },
                    {
                        "id":3,
                        "adApprovalNo":"XXXXXXXXXXXXX",
                        "adReleaseCon":"XXXXXXXXXXXXX",
                        "adConType": "药品广告",
                        "adValidity": "2011-12-20 至 2012-12-19"
                    }
                ],
                dataDetails:{
//                    "id":1,
//                    "drugApprovalNo":"xxxxxxx",
//                    "unitName":"xxxxxxx",
//                    "unitAddress":"xxxxxxx",
//                    "postalCode":"xxxxxxx",
//                    "genericName":"xxxxxxx",
//                    "commodityName":"xxxxxxx",
//                    "brandName":"xxxxxxx",
//                    "preClassification":"xxxxxxx",
//                    "adCategories":"xxxxxxx",
//                    "whenLong":"xxxxxxx",
//                    "adValidity":"xxxxxxx",
//                    "adReleaseCon":"xxxxxxx",
//                    "approvalNo":"xxxxxxx",
//                    "remarks":"xxxxxxx"
                }
            }
        },
        methods:{
            getList(params){//列表
                api.Get('/cloudMap/fda/getAdApprovedInfoList',params).then((res)=>{
                    console.log(res);
                    this.datalist =res.data;
                    this.totalRecord=res.totalRecord;
                })
                    .catch(err=>{
                        console.log('请求失败返回的错误信息');
                    })
            },
            handleCurrentChange(val) {//分页
                console.log(`当前页: ${val}`);
                this.getList({
                    entId:1,
                    entName:this.$router.currentRoute.params.entName,
                    page:val,
                    pageSize:this.pageSize
                })
            },
            showPopup(approvedId){//模态框
                this.dialogVisible = true;
                api.Get('/cloudMap/fda/getAdApprovedInfoDetails',{
                    entId:1,
                    entName:this.$router.currentRoute.params.entName,
                    approvedId: approvedId
                }).then((res)=>{
                    console.log(res);
                    this.dataDetails =res.data;
                })
            }
        },
        mounted(){
            this.getList({//列表
                entId:1,
                entName:this.$router.currentRoute.params.entName,
                page:1,
                pageSize:this.pageSize
            })
        }
    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    @import "../assets/css/tableCon.css";
    @import "../assets/css/dialog.css";
</style>



