<template>
    <div class="boot-page" >
        <!--软件著作权-->
        <div class="sfcopyright">
            <div class="div-dataList">
            <table>
                <tr>
                    <th class="firstTh">序号</th>
                    <th class="secondTh">软件名称</th>
                    <th class="thirdTh">版本号</th>
                    <th class="fourthTh">登记号</th>
                    <th class="fifthTh">分类号</th>
                    <th class="sixthTh">登记日期</th>
                </tr>
                <tr v-for="(item,index) in sfcopyright">
                    <td class="firstTd">{{index+1}}</td>
                    <td  class="secondTd" @click="showPopup(item.id)">
                            {{item.sfName}}
                    </td>
                    <td>
                            {{item.versionNum}}
                    </td>
                    <td>
                            {{item.regNumber}}
                    </td>
                    <td>
                        {{item.classificNum}}
                    </td>
                    <td  class="lastTd">
                            {{item.regDate}}
                    </td>
                </tr>
            </table>
            </div>
            <div class="div-pager" v-if="this.totalRecord>this.pageSize">
                <el-pagination
                        @size-change="handleSizeChange"
                        @current-change="handleCurrentChange"
                        :current-page.sync="this.currentPage"
                        :page-size="this.pageSize"
                        layout="prev, pager, next, jumper"
                        :total="this.totalRecord">
                </el-pagination>
            </div>
        </div>

        <!--弹框-->
        <el-dialog  :visible.sync="dialogVisible">
            <p class="title">软件著作权详情</p>
            <!--这里写自己的内容-->
            <div class="dialog-con">
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">软件全称：</span>
                    </h4>
                    <p class="item-value"><span>{{softwareList.sfName}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">软件简称 ：</span>
                    </h4>
                    <p class="item-value"><span>{{softwareList.abbreviation}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">登记号：</span>
                    </h4>
                    <p class="item-value"><span>{{softwareList.regNumber}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">分类号：</span>
                    </h4>
                    <p class="item-value"><span>{{softwareList.classificNum}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">版本号：</span>
                    </h4>
                    <p class="item-value"><span>{{softwareList.versionNum}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">著作权人（国籍）：</span>
                    </h4>
                    <p class="item-value"><span>{{softwareList.copyrightUser}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">首次发表日期：</span>
                    </h4>
                    <p class="item-value"><span>{{softwareList.publicationDate}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">登记日期：</span>
                    </h4>
                    <p class="item-value"><span>{{softwareList.regDate}} </span></p>
                </div>
            </div>
        </el-dialog>
    </div>
</template>

<script>
    import * as api from '@/assets/js/api.js'
    export default {
        name: 'sfcopyrightList',
        data () {
            return {
                dialogVisible: false,
                totalRecord:0,
                pageSize:50,
                currentPage:1,
                sfcopyright:[
//                    {
//                        "sfName":"资产管理平台—资产目录子系统【简称：资产目录系统】",
//                        "versionNum":"V1.0",
//                        "regNumber":"2017-z-0324",
//                        "typeNumber":"30000-1000",
//                        "regDate":"2011-11-11"
//                    },
//                    {
//                        "sfName":"资产管理平台—资产目录子系统【简称：资产目录系统】",
//                        "versionNum":"V1.0",
//                        "regNumber":"2017-z-0324",
//                        "typeNumber":"30000-1000",
//                        "regDate":"2011-11-11"
//                    }
                ],
                softwareList:{
//                    "fullName":"资产管理平台-资源目录子系统",
//                    "abbreviation" :"资源目录系统",
//                    "regNum":"2010-K-026127",
//                    "classificNum":"30000-0000",
//                    "versionNum":"V1.0",
//                    "copyrightUser":"贵州茅台股份有限公司",
//                    "publicationDate":"2010-7-1",
//                    "regDate":"2010-7-1"
                }
            }
        },
        mounted(){
            api.Get('/cloudMap/inAssetsInfo/getsfCopyrightList',{page:1,pageSize:this.pageSize,entId:1,entName:this.$router.currentRoute.params.entName}).then(res=>{
                this.sfcopyright=res.data;
                this.totalRecord=res.totalRecord;
                console.log(res);
            });
        },
        methods:{
            showPopup(id){
                this.dialogVisible = true;
                api.Get('/cloudMap/inAssetsInfo/getsfCopyrightInfo', {id: id, entId: 1, entName:this.$router.currentRoute.params.entName}).then(res => {
                    this.softwareList = res.data;
                    console.log(id);
                    console.log(res);
                });
            },
            handleSizeChange(val) {
                console.log(`每页 ${val} 条`);
            },
            handleCurrentChange(val) {
                console.log(`当前页: ${val}`);
                api.Get('/cloudMap/inAssetsInfo/getsfCopyrightList',{entId:1,entName:this.$router.currentRoute.params.entName,page:val,pageSize:this.pageSize}).then(res=>{
                    this.sfcopyright=res.data;
                    this.totalRecord=res.totalRecord;
                    console.log(res);
                });

            },
        },
        components:{

        }
    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    @import 'assets/css/common.css';
    @import 'assets/css/tableStyle.css';
    .sfcopyright .firstTh{
        width:10%;
    }
    .sfcopyright .secondTh{
        width:35%;
    }
    .sfcopyright .thirdTh{
        width:10%;
    }
    .sfcopyright .fourthTh{
        width:15%;
    }
    .sfcopyright .fifthTh{
        width:15%;
    }
    .sfcopyright .sixthTh{
        width:15%;
    }
    .secondTd{
        text-decoration: underline;
        cursor: pointer;
    }

</style>



