<template>
    <div class="boot-page">
        <table class="court-table">
            <thead>
            <tr>
                <th class="tc w50">序号</th>
                <th>批准文号</th>
                <th>产品名称/中文名称</th>
                <th>类别</th>
                <th class="w100">批准日期</th>
            </tr>
            </thead>
            <tbody>
            <tr v-for="list in datalist">
                <td class="tc">{{list.id}}</td>
                <td>
                    <a @click="showPopup(list.id,list.category)">{{list.approvalNo}}</a>
                </td>
                <td>{{list.productName}}</td>
                <td>{{list.category}}</td>
                <td>{{list.approvalDate}}</td>
            </tr>
            </tbody>
        </table>

        <!--弹框-->
        <el-dialog  :visible.sync="dialogVisible">
            <p class="title">{{title}}</p>
            <!--这里写自己的内容-->
            <div class="dialog-con">
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">产品名称：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.productName}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">批准文号：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.approvalNo}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">产品编号：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.productNo}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">有效期至：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.termOfValidity}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">申请人中文名称：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.applicant}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">申请人地址：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.applicantAddress}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">保健功能：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.healthFunction}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">功效成分/标志性成分含量：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.funIngredients}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">主要原料：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.mainMaterial}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">适宜人群：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.suitCrowds}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">不适宜人群：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.unfitCrowd}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">食用方法及食用量：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.edibleMethod}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">产品规格：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.productSpec}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">产品技术要求：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.skillRequire}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">保质期：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.shelfLife}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">贮藏方法：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.storageMethod}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">注意事项：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.attentMatters}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">批准日期：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.approvalDate}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">批准变更日期：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.changeDate}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">变更内容：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.changeCon}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">备案日期：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.filingDate}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">备案内容：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.filingCon}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">转让方中文名称：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.cnTransferor}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">转让方英文名称：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.enTransferor}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">转让方地址：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.transferorAds}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">受让方：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.transferee}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">受让方地址：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.transfereeAds}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">转让前批准文号：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.befApprovalNo}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">批准转让日期：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.approvalDate}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">补发日期：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.reissueDate}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">注销日期：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.withdrawDate}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">注销原因：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.withdrawReason}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">备注：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.remarks}}</span></p>
                </div>
            </div>
        </el-dialog>
    </div>
</template>

<script type="es6">
    import * as api from '@/assets/js/api.js'
    export default {
        name: 'healthFoodLogin',
        data () {
            return {
                dialogVisible: false,
                title:'国产保健食品',
                datalist: [],
                dataDetails: []
            }
        },
        mounted(){
            api.Get("/cloudMap/fda/getHealthFoodLoginList",{
                "entName": this.$router.currentRoute.params.entName
            })
                .then(res=>{
                    console.log('请求成功返回的结果'+res.code);
                    this.datalist = res.data;
                })
                .catch(err=>{
                    console.log('请求失败返回的错误信息');
                })
        },
        methods:{
            showPopup(id){
                api.Get('/cloudMap/fda/getHealthFoodLoginDetails',{
                    "entName": this.$router.currentRoute.params.entName,
                    "hfId":id
                })
                    .then(res=>{
                        console.log('请求成功返回的结果'+res.code);
                        this.dataDetails = res.data;
                    })
                    .catch(err=>{
                        console.log('请求失败返回的错误信息');
                    })
                this.dialogVisible = true;
            }
        }
    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    @import "./assets/css/tableCon.css";
    @import "./assets/css/dialog.css";
</style>



