<template>
    <div class="boot-page">
        <!--A级纳税人名单-->
        <div class="overflowAuto">
            <div class="taxpayers-title"><span>评价年份</span></div>
            <ul class="taxpayers-list">
                <li v-for="list in datalist">
                    <a href="javascript:;">
                        <span class="mtop">{{list.particularYear}}</span><span class="f14">年</span>
                    </a>
                </li>
            </ul>
        </div>
    </div>
</template>

<script type="es6">
    import * as api from '@/assets/js/api.js'
    export default {
        name: 'taxDetails',
        data () {
            return {
                datalist: []
            }
        },
        mounted(){
            api.Get("/cloudMap/tax/getTaxDetailsList",{
                "entName": this.$router.currentRoute.params.entName
            })
                .then(res=>{
                    console.log('请求成功返回的结果'+res.code);
                    this.datalist = res.data;
                })
                .catch(err=>{
                    console.log('请求失败返回的错误信息');
                })
        }
    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    @import "./assets/css/ballLamp.css";
</style>



