<template>
    <div class="boot-page">
        <!--环保行政处罚-->
        <div class="overflowAuto">
            <table class="court-table">
                <thead>
                <tr>
                    <th class="tc w50">序号</th>
                    <th>决定书文号</th>
                    <th>环境违法事实和证据</th>
                    <th>处罚结果</th>
                    <th class="w100">处罚日期</th>
                </tr>
                </thead>
                <tbody>
                <tr v-for="(list,index) in datalist">
                    <td class="tc">{{index+1}}</td>
                    <td>
                        <a @click="showPopup(list.envId)">{{list.wrDecisionNo}}</a>
                    </td>
                    <td>{{list.punishContent}}</td>
                    <td>{{list.punishResult}}</td>
                    <td>{{list.penaltyDate}}</td>
                </tr>
                </tbody>
            </table>
        </div>

        <!--分页-->
        <div class="pageWrap" v-if="this.totalRecord>this.pageSize">
            <el-pagination
                    @current-change="handleCurrentChange"
                    :current-page.sync="this.currentPage"
                    :page-size="this.pageSize"
                    layout="prev, pager, next, jumper"
                    :total="this.totalRecord">
            </el-pagination>
        </div>

        <!--弹框-->
        <el-dialog  :visible.sync="dialogVisible">
            <p class="title">{{title}}</p>
            <!--这里写自己的内容-->
            <div class="dialog-con">
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">决定书文号：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.wrDecisionNo}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">处罚决定的机关：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.organizationName}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">环境违法事实和证据：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.punishContent}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">处罚依据：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.punishmentBasis}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">处罚结果：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.punishResult}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">处罚类型：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.punishType}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">处罚日期：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.penaltyDate}}</span></p>
                </div>
            </div>
        </el-dialog>
    </div>
</template>

<script type="es6">
    import * as api from '@/assets/js/api.js'
    export default {
        name: 'environmentalDetails',
        data () {
            return {
                dialogVisible: false,
                title:'环保行政处罚详情',
                pageSize:50,
                currentPage:1,
                totalRecord:'',
                datalist: [],
                dataDetails:{}
            }
        },
        methods:{
            handleCurrentChange(val) {//分页
                console.log(`当前页: ${val}`);
                this.getList({
                    entId:1,
                    entName:this.$router.currentRoute.params.entName,
                    page:val,
                    pageSize:this.pageSize
                })
            },
            showPopup(id){//模态框
                this.dialogVisible = true;
                api.Get('/cloudMap/ep/getEnvironmentalDetails',{
                    "entName": this.$router.currentRoute.params.entName,
                    "envId":id
                })
                .then(res=>{
                    console.log('请求成功返回的结果'+res.code);
                    this.dataDetails = res.data;
                })
                .catch(err=>{
                    console.log('请求失败返回的错误信息');
                })
            },
            getList(params){//列表
                api.Get('/cloudMap/ep/getEnvironmentalList',params).then((res)=>{
                    console.log(res);
                    console.log('请求成功返回的结果'+res.code);
                    this.datalist = res.data;
                    this.totalRecord=res.totalRecord;
                })
                .catch(err=>{
                    console.log('请求失败返回的错误信息');
                })
            }
        },
        mounted(){
            this.getList({//列表
                entId:1,
                entName:this.$router.currentRoute.params.entName,
                page:1,
                pageSize:this.pageSize
            })
        }
    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    @import "./assets/css/tableCon.css";
    @import "./assets/css/dialog.css";
</style>



