<template>
    <div class="boot-page" >
        <!--商标-->
        <div class="trademark">
            <div class="div-dataList">
            <table>
                <tr>
                    <th class="firstTh">序号</th>
                    <th class="secondTh">商标</th>
                    <th class="thirdTh">商品名称</th>
                    <th class="fourthTh">注册号</th>
                    <th class="fifthTh">分别</th>
                    <th class="sixthTh">状态</th>
                    <th class="seventhTh">申请时间</th>
                </tr>
                <tr v-for="(item,index) in trademark">
                    <td class="firstTd">{{index+1}}</td>
                    <td>
                        <img  alt="商标" src="../../assets/img/trademark.png" /><!--:src="item.trademark"-->
                    </td>
                    <td>
                            {{item.brandName}}
                    </td>
                    <td class="fourthTd" @click="showPopup(item.id)">
                            {{item.regNumber}}
                    </td>
                    <td>
                            {{item.trademarkType}}
                    </td>
                    <td>
                            {{item.trademarkState}}
                    </td>
                    <td  class="lastTd">
                            {{item.applicationDate}}
                    </td>
                </tr>
            </table>
            </div>
            <div class="div-pager" v-if="this.totalRecord>this.pageSize">
                <el-pagination
                        @size-change="handleSizeChange"
                        @current-change="handleCurrentChange"
                        :current-page.sync="this.currentPage"
                        :page-size="this.pageSize"
                        layout="prev, pager, next, jumper"
                        :total="this.totalRecord">
                </el-pagination>
            </div>
        </div>

        <!--弹框-->
        <el-dialog  :visible.sync="dialogVisible">
            <p class="title">商标详情</p>
            <!--这里写自己的内容-->
            <div class="dialog-con">
                <div class="dialogImg">
                    <img src="../../assets/img/patent.png" alt="">
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">商标名称：</span>
                    </h4>
                    <p class="item-value"><span>{{tradeList.brandName}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">注册号：</span>
                    </h4>
                    <p class="item-value"><span>{{tradeList.regNumber}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">商标分类：</span>
                    </h4>
                    <p class="item-value"><span>{{tradeList.trademarkType}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">商标状态：</span>
                    </h4>
                    <p class="item-value"><span>{{tradeList.trademarkState}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">申请人：</span>
                    </h4>
                    <p class="item-value"><span>{{tradeList.applicant}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">申请日期：</span>
                    </h4>
                    <p class="item-value"><span>{{tradeList.applicationDate}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">商品/服务列表：</span>
                    </h4>
                    <p class="item-value"><span>{{tradeList.commodityList}}</span></p>
                </div>
            </div>
        </el-dialog>
    </div>
</template>

<script>
    import * as api from '@/assets/js/api.js'
    export default {
        name: 'trademarkList',
        data () {
            return {
                dialogVisible: false,
                totalRecord:100,
                pageSize:50,
                currentPage:1,
                trademark:[
//                    {
//                        "trademark":"../../assets/img/trademark.png",
//                        "productName":"众塑",
//                        "regNumber":"1234567876",
//                        "type":"37-建筑修理",
//                        "status":"商标注册申请受理通知书发文",
//                        "applyTime":"2012-11-11"
//                    },
//                    {
//                        "trademark":"../../assets/img/trademark.png",
//                        "productName":"众塑",
//                        "regNumber":"1234567876",
//                        "type":"37-建筑修理",
//                        "status":"商标注册申请受理通知书发文",
//                        "applyTime":"2012-11-11"
//                    },
                ],
                tradeList:{
//                    "brandName":"分离乙醇发酵液的方法",
//                    "regisNum":"24352345234",
//                    "trademarkType":"37 - 化学制药",
//                    "trademarkState":"商标注册申请受理通知书发文",
//                    "applicant":"北京同仁堂股份有限公司",
//                    "applicationDate":"2016-10-26",
//                    "commodityList":"产品产品产品产品产品产品1产品产品产品产品产品产品2产品产品产品产品产品产品3产品产品产品产品产品产品4产品产品产品产品产品产品5产品产品产品产品产品产品6"
            }
            }
        },
        mounted(){
            api.Get('/cloudMap/inAssetsInfo/getTrademarkList',{page:1,pageSize:this.pageSize,entId:1,entName:this.$router.currentRoute.params.entName}).then(res=>{
                this.trademark=res.data;
                this.totalRecord=res.totalRecord;
                console.log(res);
            });
        },
        methods:{
            showPopup(id){
                this.dialogVisible = true;
                api.Get('/cloudMap/inAssetsInfo/getTrademarkInfo',{id:id,entId:1,entName:this.$router.currentRoute.params.entName}).then(res=>{
                    this.tradeList=res.data;
                    //console.log(res);
                });
            },
            handleSizeChange(val) {
                console.log(`每页 ${val} 条`);
            },
            handleCurrentChange(val) {
                console.log(`当前页: ${val}`);
                api.Get('/cloudMap/inAssetsInfo/getTrademarkList',{entId:1,entName:this.$router.currentRoute.params.entName,page:val,pageSize:this.pageSize}).then(res=>{
                    this.trademark=res.data;
                    this.totalRecord=res.totalRecord;
                    console.log(res);
                });

            },
        },
        components:{

        }
    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    @import 'assets/css/common.css';
    @import 'assets/css/tableStyle.css';
    .trademark .firstTh{
        width:10%;
    }
    .trademark .secondTh{
        width:15%;
    }
    .trademark .thirdTh{
        width:15%;
    }
    .trademark .fourthTh{
        width:12%
    }
    .trademark .fifthTh{
        width:14%
    }
    .trademark .sixthTh{
        width:18%
    }
    .trademark .seventhTh{
        width:15%
    }
    .trademark .fourthTd{
        text-decoration: underline;
        cursor: pointer;
    }
    .trademark img{
        display: inline-block;
        width: 104px;
        height: 53px;
        margin: 4px;
        line-height:53px;
        border:none;
    }
</style>



