<template>
    <div class="boot-page" >
        <!--行政许可信息-->
        <div class="admPermitInfo overflowAuto">
            <table>
                <tr>
                    <th class="firstTh">序号</th>
                    <th class="secondTh">行政许可编号</th>
                    <th class="thirdTh">许可文件</th>
                    <th class="fourthTh">有效期</th>
                    <th class="lastTh">许可机关</th>
                </tr>
                <tr v-for="(item,index) in admPermitInfo">
                    <td class="firstTd">{{index+1}}</td>
                    <td>
                            {{item.permitNumber}}
                    </td>
                    <td class="thirdTd" @click="showPopup()">
                            {{item.permitFile}}
                    </td>
                    <td>
                        {{item.termOfValidityStart}} - {{item.termOfValidityEnd}}
                    </td>
                    <td class="lastTd">
                            {{item.licensingAuthority}}
                    </td>
                </tr>
            </table>
        </div>
        <div class="pageWrap" v-if="this.totalRecord>50">
            <el-pagination
                    @size-change="handleSizeChange"
                    @current-change="handleCurrentChange"
                    :current-page.sync="currentPage3"
                    :page-size="this.pageSize"
                    layout="prev, pager, next, jumper"
                    :total="1000">
            </el-pagination>
        </div>
        <!--弹框-->
        <el-dialog  :visible.sync="dialogVisible">
            <p class="title">行政许可信息</p>
            <!--这里写自己的内容-->
            <div class="dialog-con">
                <div class="writtenVerdict">
                    <div class="dialog-item">
                        <h4 class="clearfix item-name">
                            <i class="fl"></i>
                            <span class="titleBarName fl">行政许可证号：</span>
                        </h4>
                        <p class="item-value"><span>{{admPermitDetails.permitNumber}}</span></p>
                    </div>
                    <div class="dialog-item">
                        <h4 class="clearfix item-name">
                            <i class="fl"></i>
                            <span class="titleBarName fl">文件名称：</span>
                        </h4>
                        <p class="item-value"><span>{{admPermitDetails.permitFile}}</span></p>
                    </div>
                    <div class="dialog-item">
                        <h4 class="clearfix item-name">
                            <i class="fl"></i>
                            <span class="titleBarName fl">许可机关：</span>
                        </h4>
                        <p class="item-value"><span>{{admPermitDetails.licensingAuthority}}</span></p>
                    </div>
                    <div class="dialog-item">
                        <h4 class="clearfix item-name">
                            <i class="fl"></i>
                            <span class="titleBarName fl">有效期：</span>
                        </h4>
                        <p class="item-value"><span>{{admPermitDetails.termOfValidityStart}} - {{admPermitDetails.termOfValidityEnd}}</span></p>
                    </div>
                    <div class="wline"></div>
                    <div class="dialog-item">
                        <h4 class="clearfix item-name">
                            <i class="fl"></i>
                            <span class="titleBarName fl">许可内容：</span>
                        </h4>
                        <p class="item-value"><span>{{admPermitDetails.permitContent}}</span></p>
                    </div>
                </div>
            </div>
        </el-dialog>
    </div>
</template>

<script>
    import * as api from '@/assets/js/api'
    export default {
        name: 'admPermitInfoList',
        mounted(){
            api.Get('/cloudMap/business/getAdmPermitInfoList',{
                "entId": 1,
                "entName":this.entName,
                "pageSize":50,
                "page":1
            }).then((res)=>{
                console.log(res)
                this.totalRecord=res.totalRecord;
                this.admPermitInfo =res.data;
            })
        },
        data () {
            return {
                totalRecord:0,
                pageSize:50,
                currentPage3:1,
                dialogVisible: false,
                admPermitInfo:[],
                admPermitDetails:{ },
                entName:this.$router.currentRoute.params.entName
            }
        },
        methods:{
            handleSizeChange(val) {
                console.log(`每页 ${val} 条`);
            },
            handleCurrentChange(val) {
                console.log(`当前页: ${val}`);
                api.Get('/cloudMap/business/getAdmPermitInfoList',{
                    "entId": 1,
                    "entName":this.entName,
                    "pageSize":50,
                    "page":val
                }).then((res)=>{
                    console.log(res)
                    this.totalRecord=res.totalRecord;
                    this.admPermitInfo =res.data;
                })
            },
            showPopup( ){
                this.dialogVisible = true;
                api.Get('/cloudMap/business/getAdmPermitDetails',{
                    "id": 1,
                    "entName":this.entName
                }).then((res)=>{
                    console.log(res)
                    this.admPermitDetails =res.data;
                })
            }
        },
        components:{

        }
    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    @import 'assets/css/common.css';
    @import 'assets/css/tableStyle.css';
    @import 'assets/css/dialog.css';
    .thirdTd{
        text-decoration: underline;
        cursor: pointer;
    }
    .dialog-item .item-value{
        text-align: left;
    }
    .writtenVerdict{
        padding:4px 15px 30px;
    }
    .wline{
        line-height:24px;
        border-bottom:1px dashed #fff;
        margin-bottom:37px;
    }
     p.txtIndent{
        line-height:32px;
        text-indent:2em;
        text-align:left;
        margin-bottom:32px;
    }
</style>



