<template>
    <div class="boot-page" >
        <!--司法协助信息-->
        <div class="judicialAssistInfo overflowAuto">
            <table>
                <tr>
                    <th class="firstTh">序号</th>
                    <th class="secondTh">执行通知书文号</th>
                    <th class="thirdTh">执行法院</th>
                    <th class="lastTh">股权数额</th>
                </tr>
                <tr v-for="(item,index) in judicialAssistInfo">
                    <td class="firstTd">{{index+1}}</td>
                    <td  class="secondTd" @click="showPopup()">
                            {{item.noticeNumber}}
                    </td>
                    <td>
                            {{item.excuteCourt}}
                    </td>
                    <td  class="lastTd">
                            {{item.equityAmount}}
                    </td>
                </tr>
            </table>
            <!--弹框-->
            <el-dialog  :visible.sync="dialogVisible">
                <p class="title">司法协助信息</p>
                <!--这里写自己的内容-->
                <div class="dialog-con">
                    <div class="writtenVerdict">
                        <div class="dialog-item">
                            <h4 class="clearfix item-name">
                                <i class="fl"></i>
                                <span class="titleBarName fl">执行通知书文号：</span>
                            </h4>
                            <p class="item-value"><span>{{judicialAssistDetails.noticeNumber}}</span></p>
                        </div>
                        <div class="dialog-item">
                            <h4 class="clearfix item-name">
                                <i class="fl"></i>
                                <span class="titleBarName fl">执行裁定书文号：</span>
                            </h4>
                                <p class="item-value"><span>{{judicialAssistDetails.consultationNumber}}</span></p>
                        </div>
                        <div class="dialog-item">
                            <h4 class="clearfix item-name">
                                <i class="fl"></i>
                                <span class="titleBarName fl">执行法院：</span>
                            </h4>
                            <p class="item-value"><span>{{judicialAssistDetails.enforceCourt}}</span></p>
                        </div>
                        <div class="dialog-item">
                            <h4 class="clearfix item-name">
                                <i class="fl"></i>
                                <span class="titleBarName fl">被执行人：</span>
                            </h4>
                            <p class="item-value"><span>{{judicialAssistDetails.excutedPerson}}</span></p>
                        </div>
                        <div class="dialog-item">
                            <h4 class="clearfix item-name">
                                <i class="fl"></i>
                                <span class="titleBarName fl">持有股权、其它投资权益的数额：</span>
                            </h4>
                            <p class="item-value"><span>{{judicialAssistDetails.equityInterestsAmount}}</span></p>
                        </div>
                        <div class="dialog-item">
                            <h4 class="clearfix item-name">
                                <i class="fl"></i>
                                <span class="titleBarName fl">证照种类：</span>
                            </h4>
                            <p class="item-value"><span>{{judicialAssistDetails.lisenceType}}</span></p>
                        </div>
                        <div class="dialog-item">
                            <h4 class="clearfix item-name">
                                <i class="fl"></i>
                                <span class="titleBarName fl">证照号码：</span>
                            </h4>
                            <p class="item-value"><span>{{judicialAssistDetails.lisenceNumber}}</span></p>
                        </div>
                        <div class="dialog-item">
                            <h4 class="clearfix item-name">
                                <i class="fl"></i>
                                <span class="titleBarName fl">类型/状态：</span>
                            </h4>
                            <p class="item-value"><span>{{judicialAssistDetails.typeStatus}}</span></p>
                        </div>
                        <div class="dialog-item">
                            <h4 class="clearfix item-name">
                                <i class="fl"></i>
                                <span class="titleBarName fl">执行事项：</span>
                            </h4>
                            <p class="item-value"><span>{{judicialAssistDetails.excuteItem}}</span></p>
                        </div>
                        <div class="dialog-item">
                            <h4 class="clearfix item-name">
                                <i class="fl"></i>
                                <span class="titleBarName fl">公示日期：</span>
                            </h4>
                            <p class="item-value"><span>{{judicialAssistDetails.publicityDate}}</span></p>
                        </div>
                    </div>
                </div>
            </el-dialog>
        </div>
        <div class="pageWrap" v-if="this.totalRecord>50">
            <el-pagination
                    @size-change="handleSizeChange"
                    @current-change="handleCurrentChange"
                    :current-page.sync="currentPage3"
                    :page-size="this.pageSize"
                    layout="prev, pager, next, jumper"
                    :total="1000">
            </el-pagination>
        </div>
    </div>
</template>

<script>
    import * as api from '@/assets/js/api'
    export default {
        name: 'judicialAssistInfoList',
        mounted(){
            api.Get('/cloudMap/business/getJudicialAssistInfoList',{
                "entId": 1,
                "entName":this.entName,
                "pageSize":50,
                "page":1
            }).then((res)=>{
                console.log(res)
                this.totalRecord=res.totalRecord;
                this.judicialAssistInfo =res.data;
            })
        },
        data () {
            return {
                entName:this.$router.currentRoute.params.entName,
                totalRecord:0,
                pageSize:50,
                currentPage3:1,
                dialogVisible: false,
                judicialAssistInfo:[],
                judicialAssistDetails:{}
            }
        },
        methods:{
            handleSizeChange(val) {
                console.log(`每页 ${val} 条`);
            },
            handleCurrentChange(val) {
                console.log(`当前页: ${val}`);
                api.Get('/cloudMap/business/getJudicialAssistInfoList',{
                    "entId": 1,
                    "entName":this.entName,
                    "pageSize":50,
                    "page":val
                }).then((res)=>{
                    console.log(res)
                    this.totalRecord=res.totalRecord;
                    this.judicialAssistInfo =res.data;
                })
            },
            showPopup( ){
                this.dialogVisible = true;
                api.Get('/cloudMap/business/getJudicialAssistDetails',{
                    "entId": 1,
                    "entName":this.entName,
                }).then((res)=>{
                    console.log(res)
                    this.judicialAssistDetails =res.data;
                })
            }
        },
        components:{

        }
    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    @import 'assets/css/common.css';
    @import 'assets/css/tableStyle.css';
    @import 'assets/css/dialog.css';
    .secondTd{
        text-decoration: underline;
        cursor: pointer;
    }
    .dialog-item .item-value{
        text-align: left;
    }
    .writtenVerdict{
        padding:4px 15px 30px;
    }
    p.txtIndent{
        line-height:32px;
        text-indent:2em;
        text-align:left;
        margin-bottom:32px;
    }
</style>



