<template>
    <div class="collection" :class="{active:isActive}" @click="isActive = !isActive">
        <i class="star"></i>
        <span class="collect-flag">{{ isActive ? '已收藏' : '收藏' }}</span>
    </div>
</template>

<script>
    export default {
        name: 'collection',
        data(){
            return{
                isActive: false
            }
        }
    }
</script>

<style scoped>
.collection{
    box-sizing: border-box;
    cursor: pointer;
    padding: 0 10px;
    width: 106px;
    height: 30px;
    border: 1px solid #0765bf;
    border-image: linear-gradient(to bottom, #0765be,#068ec6) 1 1;
    background: linear-gradient(to bottom, rgba(13,65,127,.9),rgba(12,80,137,.9));
    display: flex;
    justify-content: space-around;
    align-items: center;
}
.star{
    width: 21px;
    height: 21px;
    background: url("../../static/img/collection.png") center no-repeat;
    background-position: 0 0;
}
.active .star{
    background-position: 0 -38px;
}
.collect-flag{
    color: #fff;
    font-size: 18px;
    font-family: '微软雅黑';
}
</style>


