<template>
    <div class="boot-page" >
        <!--著作权-->
        <div class="copyright">
            <div class="div-dataList">
            <table>
                <tr>
                    <th class="firstTh">序号</th>
                    <th class="secondTh">作品标题</th>
                    <th class="thirdTh">作品分类</th>
                    <th class="fourthTh">版本号</th>
                    <th class="fifthTh">登记日期</th>
                </tr>
                <tr v-for="(item,index) in copyright">
                    <td class="firstTd">{{index+1}}</td>
                    <td  class="secondTd"  @click="showPopup(item.id)">
                            {{item.worksTitle}}
                    </td>
                    <td>
                            {{item.classificationWork}}
                    </td>
                    <td>
                            {{item.versionNum}}
                    </td>
                    <td  class="lastTd">
                            {{item.regDate}}
                    </td>
                </tr>
            </table>
        </div>
            <div class="div-pager" v-if="this.totalRecord>this.pageSize">
                <el-pagination
                        @size-change="handleSizeChange"
                        @current-change="handleCurrentChange"
                        :current-page.sync="currentPage"
                        :page-size="this.pageSize"
                        layout="prev, pager, next, jumper"
                        :total="this.totalRecord">
                </el-pagination>
            </div>
        </div>

        <!--弹框-->
        <el-dialog  :visible.sync="dialogVisible">
            <p class="title">著作权详情</p>
            <!--这里写自己的内容-->
            <div class="dialog-con">
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">作品标题：</span>
                    </h4>
                    <p class="item-value"><span>{{copyrightList.worksTitle}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">作品分类 ：</span>
                    </h4>
                    <p class="item-value"><span>{{copyrightList.classificationWork}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">版本号：</span>
                    </h4>
                    <p class="item-value"><span>{{copyrightList.versionNum}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">权利人：</span>
                    </h4>
                    <p class="item-value"><span>{{copyrightList.obligee}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">创作性质：</span>
                    </h4>
                    <p class="item-value"><span>{{copyrightList.creativeNature}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">登记日期：</span>
                    </h4>
                    <p class="item-value"><span>{{copyrightList.regDate}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">作者：</span>
                    </h4>
                    <p class="item-value"><span>{{copyrightList.author}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">创作完成时间：</span>
                    </h4>
                    <p class="item-value"><span>{{copyrightList.completionTime}} </span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">权利归属方式：</span>
                    </h4>
                    <p class="item-value"><span>{{copyrightList.ownershipMode}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">首次发表地点：</span>
                    </h4>
                    <p class="item-value"><span>{{copyrightList.publicationPlace}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">首次发表日期：</span>
                    </h4>
                    <p class="item-value"><span>{{copyrightList.publicationDate}}</span></p>
                </div>
            </div>
        </el-dialog>
    </div>
</template>

<script>
    import * as api from '@/assets/js/api.js'
    export default {
        name: 'copyrightList',
        data () {
            return {
                dialogVisible: false,
                totalRecord:100,
                pageSize:50,
                currentPage:1,
                copyright:[
//                    {
//                        "worksTitle":"国内茅台终端设计手册——文化休闲秀效果图",
//                        "worksType":"图形",
//                        "versionNumber":"2017-z-0324",
//                        "regDate":"2017-10-11",
//                    },
//                    {
//                        "worksTitle":"国内茅台终端设计手册——文化休闲秀效果图",
//                        "worksType":"图形",
//                        "versionNumber":"2017-z-0324",
//                        "regDate":"2017-10-11",
//                    },
                ],
                copyrightList:{
//                    "worksTitle":"国酒茅台·终端设计手册——文化休闲区效果图",
//                    "classificationWork" :"图形",
//                    "versionNum":"2010-K-026127",
//                    "obligee":"贵州茅台股份有限公司",
//                    "creativeNature":"--",
//                    "registrationDate":"2010-7-1",
//                    "author":'马大帅',
//                    "completionTime":"2010-7-1",
//                    "ownershipMode":"作品著作权",
//                    "publicationPlace":"北京",
//                    "publicationDate":"2010-7-1"
                }
            }
        },
        mounted(){
//            this.showPopup()
            api.Get('/cloudMap/inAssetsInfo/getCopyrightList',{page:1,pageSize:this.pageSize,entId:1,endtName:""}).then(res=>{
                this.copyright=res.data;
                this.totalRecord=res.totalRecord;
                console.log(res);
            });
        },
        methods:{
            showPopup(id){
                this.dialogVisible = true;
                api.Get('/cloudMap/inAssetsInfo/getCopyrightInfo', {id: id, entId: 1, entName:this.$router.currentRoute.params.entName}).then(res => {
                    this.copyrightList = res.data;
                    //console.log(id);
                    //console.log(res);
                });
            },
            handleSizeChange(val) {
                console.log(`每页 ${val} 条`);
            },
            handleCurrentChange(val) {
                console.log(`当前页: ${val}`);
                api.Get('/cloudMap/inAssetsInfo/getCopyrightList',{entId:1,entName:this.$router.currentRoute.params.entName,page:val,pageSize:this.pageSize}).then(res=>{
                    this.copyright=res.data;
                    this.totalRecord=res.totalRecord;
                    console.log(res);
                });

            },
        },
        components:{

        }
    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    @import 'assets/css/common.css';
    @import 'assets/css/tableStyle.css';
    .copyright .firstTh{
        width:10%;
    }
    .copyright .secondTh{
        width:40%;
    }
    .copyright .thirdTh{
        width:15%;
    }
    .copyright .fourthTh{
        width:20%
    }
    .copyright .fifthTh{
        width:15%
    }
    .secondTd{
        text-decoration: underline;
        cursor: pointer;
    }

</style>



