<template>
    <div class="ball-wrap">
        <ul v-if="list">
            <li v-for="(item,index) in list" @click="toList(item,index)">{{item}}</li>
        </ul>
    </div>
</template>

<script>
    export default {
        name: 'ball',
        props: ['list'],
        data(){
            return {
            }
        },
        mounted(){
        },
        methods: {
            toList(listItemName,i){
                this.$emit('toList',[listItemName,i])
            }
        }
    }
</script>

<style scoped>
    .ball-wrap{
        position: absolute;
        left: 0;
        right: 0;
        top: 0;
        bottom: 0;
        background: url("../../static/img/ball.png") center no-repeat;
        display: flex;
        justify-content: center;
        align-items: center;
    }
    .ball-wrap ul{
        width: 50%;
        display: flex;
        justify-content: space-around;
        align-items: center;
        flex-wrap: wrap
    }
    .ball-wrap li{
        flex: none;
        width: 180px;
        height: 55px;
        color: #fff;
        font-size: 18px;
        cursor: pointer;
        display: flex;
        justify-content: center;
        align-items: center;
        border: 1px solid #E1A41A;
        margin-right: 20px;
        margin-bottom: 10px;
        background: rgba(0,0,0,.3);
    }
    .ball-wrap li:hover{
        color: #fff;
        background: rgba(0,0,0,.5);
    }
</style>



