<template>
    <div class="boot-page">
        <div class="overflowAuto">
            <div class="text">
                <p>{{datalist.articleCon}}</p>
            </div>
        </div>

    </div>
</template>

<script type="es6">
    import * as api from '@/assets/js/api'
    export default {
        name: 'nonSpecificRecord',
        data () {
            return {
                datalist: {
//                    "articleCon":"制造、加工中成药制剂、化妆品；经营中成药、西药制剂、生化药品；普通货运；出租办公用房；出租商业用房；以下项目仅限分公司经营：销售定型包装食品（含乳冷食品）、保健食品、中药材、医疗器械、医疗保健用品；零售中药饮片、图书；制造酒剂、涂膜剂、软胶囊剂、硬胶囊剂、保健酒、营养液（不含医药作用的营养液）；鹿、乌鸡产品的加工；中医科、内科专业、外科专业、妇产科专业、儿科专业、皮肤科专业、老年病科专业诊疗；危险货物运输（3类）；技术咨询、技术服务、技术开发、技术转让、技术培训；药用动植物的饲养、种植；自营和代理各类商品及技术的进出口业务，但国家限定公司经营或禁止进出口的商品及技术除外；零售百货；中西药广告设计；劳务服务；以下项目仅限分公司经营：养殖梅花鹿、乌骨鸡、麝、马鹿；物业服务和供热服务。（企业依法自主选择经营项目，开展经营活动；依法须经批准的项目，经相关部门批准后依批准的内容开展经营活动；不得从事本市产业政策禁止和限制类项目的经营活动。"
                }
            }
        },
        methods:{
            getList(params){//列表
                api.Get('/cloudMap/fda/getNonSpecificRecordDetails',params).then((res)=>{
                    console.log(res);
                    this.datalist =res.data;
                })
                    .catch(err=>{
                        console.log('请求失败返回的错误信息');
                    })
            }
        },
        mounted(){
            this.getList({//列表
                entId:1,
                entName:this.$router.currentRoute.params.entName,
            })
        }
    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    .boot-page{
        width:100%;
        height:100%;
        background:rgba(88,143,255,0.15);
        border:1px solid rgba(99,150,255,0.35);
    }
    .text{
        color:#fff;
        margin:20px 40px;
        line-height:32px;
    }
    .text p{
        text-indent:2em;
        margin-bottom:30px;
    }
</style>



