<template>
    <div class="boot-page">
        <table class="court-table">
            <thead>
            <tr>
                <th class="tc w50">序号</th>
                <th>保护品种编号</th>
                <th>药品名称</th>
                <th class="w100">保护级别</th>
                <th class="w100">保护期限</th>
            </tr>
            </thead>
            <tbody>
            <tr v-for="(list,index) in datalist">
                <td class="tc">{{index+1}}</td>
                <td>
                    <a @click="showPopup(list.id)">{{list.proVarietieNum}}</a>
                </td>
                <td>{{list.drugName}}</td>
                <td>{{list.proLevel}}</td>
                <td>{{list.proTerm}}</td>
            </tr>
            </tbody>
        </table>

        <!--弹框-->
        <el-dialog  :visible.sync="dialogVisible">
            <p class="title">{{title}}</p>
            <!--这里写自己的内容-->
            <div class="dialog-con">
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">保护品种编号：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.proVarietieNum}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">药品名称：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.drugName}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">公告号：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.noticeNo}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">药品批准文号：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.approvalNo}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">保护级别：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.proLevel}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">规格：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.specifications}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">保护起始日：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.proTermStart}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">保护终止日：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.proTermEnd}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">生产企业：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.productionEnt}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">剂型：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.dosageForm}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">保护期限：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.proTerm}}</span></p>
                </div>
            </div>
        </el-dialog>
    </div>
</template>

<script type="es6">
    import * as api from '@/assets/js/api.js'
    export default {
        name: 'cnProtectionVarieties',
        data () {
            return {
                dialogVisible: false,
                title:'中药保护品种',
                datalist: [],
                dataDetails: []
            }
        },
        mounted(){
            api.Get("/cloudMap/fda/getProtectionVarietiesList",{
                "entName": this.$router.currentRoute.params.entName
            })
                .then(res=>{
                    console.log('请求成功返回的结果'+res.code);
                    this.datalist = res.data;
                })
                .catch(err=>{
                    console.log('请求失败返回的错误信息');
                })
        },
        methods:{
            showPopup(id){
                api.Get('/cloudMap/fda/getProtectionVarietiesDetails',{
                    "entName": this.$router.currentRoute.params.entName,
                    "pvId":id
                })
                    .then(res=>{
                        console.log('请求成功返回的结果'+res.code);
                        this.dataDetails = res.data;
                    })
                    .catch(err=>{
                        console.log('请求失败返回的错误信息');
                    })
                this.dialogVisible = true;
            }
        }
    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    @import "../assets/css/tableCon.css";
    @import "../assets/css/dialog.css";
</style>



