<template>
    <div class="boot-page">
        <table class="court-table">
            <thead>
            <tr>
                <th class="tc w50">序号</th>
                <th>公告号</th>
                <th>标称生产企业名称/标称产品名称</th>
                <th>抽检类型</th>
            </tr>
            </thead>
            <tbody>
            <tr v-for="(list,index) in datalist">
                <td class="tc">{{index+1}}</td>
                <td>
                    <a @click="showPopup(list.id,list.samplingType)">{{list.noticeNo}}</a>
                </td>
                <td>{{list.nominalName}}</td>
                <td>{{list.samplingType}}</td>
            </tr>
            </tbody>
        </table>

        <!--弹框-->
        <el-dialog  :visible.sync="dialogVisible">
            <p class="title">{{title}}</p>
            <!--这里写自己的内容-->
            <div class="dialog-con">
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">标称生产企业名称：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.nominalName}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">标称生产企业地址：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.nominalAddress}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">被抽样单位名称：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.unitName}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">被抽样单位所在省份：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.province}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">食品名称：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.foodName}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">规格型号：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.specifications}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">生产日期/批号：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.manufactureDate}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">分类：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.type}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">公告号：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.noticeNo}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">备注：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.remarks}}</span></p>
                </div>
                <div class="dialog-item">
                    <h4 class="clearfix item-name">
                        <i class="fl"></i>
                        <span class="titleBarName fl">抽检项目：</span>
                    </h4>
                    <p class="item-value"><span>{{dataDetails.sampledPrj}}</span></p>
                </div>
            </div>
        </el-dialog>
    </div>
</template>

<script type="es6">
    import * as api from '@/assets/js/api.js'
    export default {
        name: 'foodDetails',
        data () {
            return {
                dialogVisible: false,
                title:'国家食品安全监督抽检（合格产品）',
                datalist: [],
                dataDetails: []
            }
        },
        mounted(){
            api.Get("/cloudMap/fda/getFoodList",{
                "entName": this.$router.currentRoute.params.entName
            })
                .then(res=>{
                    console.log('请求成功返回的结果'+res.code);
                    this.datalist = res.data;
                })
                .catch(err=>{
                    console.log('请求失败返回的错误信息');
                })
        },
        methods:{
            showPopup(id,title){
                api.Get('/cloudMap/fda/getFoodDetails',{
                    "entName": this.$router.currentRoute.params.entName,
                    "foodId":id
                })
                    .then(res=>{
                        console.log('请求成功返回的结果'+res.code);
                        this.dataDetails = res.data;
                        this.title = title;
                    })
                    .catch(err=>{
                        console.log('请求失败返回的错误信息');
                    })
                this.dialogVisible = true;
            }
        }
    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    @import "./assets/css/tableCon.css";
    @import "./assets/css/dialog.css";
</style>



