<template>
    <div class="boot-page">
        <div class="overflowAuto">
            <table class="court-table">
                <thead>
                <tr>
                    <th class="tc w50">序号</th>
                    <th>案号</th>
                    <th>执行标的</th>
                    <th>执行法院</th>
                    <th class="w100">立案时间</th>
                </tr>
                </thead>
                <tbody>
                <tr v-for="(list,index) in datalist">
                    <td class="tc">{{index+1}}</td>
                    <td>{{list.caseNo}}</td>
                    <td>{{list.executionTarget}}</td>
                    <td>{{list.executionCourt}}</td>
                    <td>{{list.filingTime}}</td>
                </tr>
                </tbody>
            </table>
        </div>

        <!--分页-->
        <div class="pageWrap"  v-if="this.totalRecord>this.pageSize">
            <el-pagination
                    @current-change="handleCurrentChange"
                    :current-page.sync="this.currentPage"
                    :page-size="this.pageSize"
                    layout="prev, pager, next, jumper"
                    :total="this.totalRecord">
            </el-pagination>
        </div>
    </div>
</template>

<script type="es6">
    import * as api from '@/assets/js/api.js'
    export default {
        name: 'courtDetails',
        data () {
            return {
                pageSize:50,
                currentPage:1,
                totalRecord:'',
                datalist: []
            }
        },
        methods: {
            handleCurrentChange(val) {//分页
                console.log(`当前页: ${val}`);
                this.getList({
                    entId: 1,
                    entName: this.$router.currentRoute.params.entName,
                    page: val,
                    pageSize: this.pageSize
                })
            },
            getList(params){//列表
                api.Get('/cloudMap/court/getBeingExecutedList',params).then((res)=>{
                    console.log(res);
                    console.log('请求成功返回的结果'+res.code);
                    this.datalist = res.data;
                    this.totalRecord=res.totalRecord;
                })
                .catch(err=>{
                    console.log('请求失败返回的错误信息');
                })
            }
        },
        mounted(){
            this.getList({//列表
                entId: 1,
                entName: this.$router.currentRoute.params.entName,
                page: 1,
                pageSize: this.pageSize
            })
        }
    }
</script>

<!-- Add "scoped" attribute to limit CSS to this component only -->
<style scoped>
    @import "./assets/css/tableCon.css";
</style>



