<template>
    <div class="boot-page" >
        <div class="yearFilterWrapper">
            <ul class="yearFilter clearfix">
                <li class="fl" :class="[current==0 ? 'active' : '']" @click='tab(0)'>2014</li>
                <li class="fl" :class="[current==1 ? 'active' : '']" @click='tab(1)'>2015</li>
                <li class="fl" :class="[current==2 ? 'active' : '']" @click='tab(2)'>2016</li>
            </ul>
        </div>
        <div class="entAnnualReportInfoList overflowAuto">
            <!--企业基本信息-->
            <div  class="baseInfo">
                <h3 class="titleBar clearfix">
                    <i class="fl"></i>
                    <span class="titleBarName fl">
                        企业基本信息
                    </span>
                </h3>
                <table>
                    <tr>
                        <td class="tdname">注册号/统一社会信用代码</td>
                        <td class="tdcon borderRightNone">{{baseInfo.codeOrRegistationNo}}</td>
                        <td class="tdname">企业名称</td>
                        <td class="tdcon">{{baseInfo.entName}}</td>
                    </tr>
                    <tr>
                        <td class="tdname">企业联系电话</td>
                        <td class="tdcon borderRightNone">{{baseInfo.enterprisePhone}}</td>
                        <td class="tdname">邮政编码</td>
                        <td class="tdcon">{{baseInfo.postCode}}</td>
                    </tr>
                    <tr>
                        <td class="tdname">企业通信地址</td>
                        <td class="tdcon" colspan="3">{{baseInfo.address}}</td>
                    </tr>
                    <tr>
                        <td class="tdname">企业电子邮箱</td>
                        <td class="tdcon borderRightNone">{{baseInfo.email}}</td>
                        <td class="tdname">有限责任公司本年度是否发生股东股权转</td>
                        <td class="tdcon">{{baseInfo.ownShareChange}}</td>
                    </tr>
                    <tr>
                        <td class="tdname">企业经营状态</td>
                        <td class="tdcon borderRightNone">{{baseInfo.operateStatus}}</td>
                        <td class="tdname">是否有网站或网店</td>
                        <td class="tdcon ">{{baseInfo.ownWebsite}}</td>
                    </tr>
                    <tr>
                        <td class="tdname">企业是否有投资信息或购买其他公司股权</td>
                        <td class="tdcon borderRightNone">{{baseInfo.ownInvestmentOrMerger}}</td>
                        <td class="tdname">从业人数</td>
                        <td class="tdcon">{{baseInfo.employeeNumber}}</td>
                    </tr>
                </table>
            </div>
            <!--网站或网点信息-->
            <div  class="websiteInfo">
                <h3 class="titleBar clearfix">
                    <i class="fl"></i>
                    <span class="titleBarName fl">
                        网站或网点信息
                    </span>
                </h3>
                <table>
                    <tr>
                        <th class="firstTh">类型</th>
                        <th>名称</th>
                        <th class="lastTh">网站</th>
                    </tr>
                    <tr v-for="(item,index) in website">
                        <td class="firstTd">{{item.websiteCategary}}</td>
                        <td>{{item.websiteName}}</td>
                        <td class="lastTd">{{item.websiteUrl}}</td>
                    </tr>
                </table>
            </div>
            <!--股东（发起人）及出资信息-->
            <div  class="shareholderInfo">
                <h3 class="titleBar clearfix">
                    <i class="fl"></i>
                    <span class="titleBarName fl">
                        股东（发起人）及出资信息
                    </span>
                </h3>
                <table>
                    <tr>
                        <th class="firstTh">股东（发起人）</th>
                        <th>实缴出资额（万元）</th>
                        <th >认缴出资时间</th>
                        <th>认缴出资方式</th>
                        <th>实缴出资额（万元）</th>
                        <th>出资时间</th>
                        <th class="lastTh">出资方式</th>
                    </tr>
                    <tr v-for="(item,index) in shareholder">
                        <td class="firstTd">{{item.shareholderName}}</td>
                        <td>{{item.subscribedAmountSum}}</td>
                        <td>{{item.subscribedDate}}</td>
                        <td>{{item.subscribedType}}</td>
                        <td>{{item.paidAmountSum}}</td>
                        <td>{{item.paidDate}}</td>
                        <td class="lastTd">{{item.paidType}}</td>
                    </tr>
                </table>
            </div>
            <!--对外投资信息-->
            <div  class="investInfo">
                <h3 class="titleBar clearfix">
                    <i class="fl"></i>
                    <span class="titleBarName fl">
                        对外投资信息
                    </span>
                </h3>
                <table>
                    <tr>
                        <th class="firstTh">投资设立企业或购买股权企业名称</th>
                        <th class="lastTh">注册号/统一社会信用代码</th>
                    </tr>
                    <tr v-for="(item,index) in invest">
                        <td class="firstTd">{{item.entName}}</td>
                        <td class="lastTd">{{item.regNum}}</td>
                    </tr>
                </table>
            </div>
            <!--企业资产状况信息-->
            <div  class="entInfo">
                <h3 class="titleBar clearfix">
                    <i class="fl"></i>
                    <span class="titleBarName fl">
                        企业资产状况信息
                    </span>
                </h3>
                <table>
                    <tr>
                        <td class="tdname">资产总额</td>
                        <td class="tdcon borderRightNone">{{entInfo.totalAssets}}</td>
                        <td class="tdname">所有者权益合计</td>
                        <td class="tdcon">{{entInfo.totalOwnersEquity}}</td>
                    </tr>
                    <tr>
                        <td class="tdname">负债总额</td>
                        <td class="tdcon borderRightNone">{{entInfo.totalLiabilities}}</td>
                        <td class="tdname">营业总收入</td>
                        <td class="tdcon">{{entInfo.grossTradingIncome}}</td>
                    </tr>
                    <tr>
                        <td class="tdname">利润总额</td>
                        <td class="tdcon borderRightNone">{{entInfo.totalProfit}}</td>
                        <td class="tdname">营业总收入中主营业务收入</td>
                        <td class="tdcon">{{entInfo.mainBusinessIncome}}</td>
                    </tr>
                    <tr>
                        <td class="tdname">净利润</td>
                        <td class="tdcon borderRightNone">{{entInfo.netProfit}}</td>
                        <td class="tdname">纳税总额</td>
                        <td class="tdcon">{{entInfo.totalTaxAmount}}</td>
                    </tr>
                </table>
            </div>
            <!--对外提供保证担保信息-->
            <div  class="guaranteeInfo">
                <h3 class="titleBar clearfix">
                    <i class="fl"></i>
                    <span class="titleBarName fl">
                        对外提供保证担保信息
                    </span>
                </h3>
                <table>
                    <tr>
                        <th class="firstTh">债权人</th>
                        <th>债务人</th>
                        <th>主债权种类</th>
                        <th>主债权数额</th>
                        <th>履行债务的期限</th>
                        <th>保证的期间</th>
                        <th class="lastTh">保证的方式</th>
                    </tr>
                    <tr v-for="(item,index) in guarantee">
                        <td class="firstTd">{{item.creditor}}</td>
                        <td>{{item.debtor}}</td>
                        <td>{{item.creditorCategary}}</td>
                        <td>{{item.creditorAmount}}</td>
                        <td>{{item.fulfillObligation}}</td>
                        <td>{{item.assuranceDurn}}</td>
                        <td class="lastTd">{{item.assuranceType}}</td>
                    </tr>
                </table>
            </div>
            <!--股权变更信息-->
            <div  class="equityChangeInfo">
                <h3 class="titleBar clearfix">
                    <i class="fl"></i>
                    <span class="titleBarName fl">
                       股权变更信息
                    </span>
                </h3>
                <table>
                    <tr>
                        <th class="firstTh">股东</th>
                        <th>变更前股权比例</th>
                        <th>变更后股权比例</th>
                        <th class="lastTh">股权变更日期</th>
                    </tr>
                    <tr v-for="(item,index) in equityChangeInfo">
                        <td class="firstTd">{{item.shareholderName}}</td>
                        <td>{{item.beforeRadio}}</td>
                        <td>{{item.afterRadio}}</td>
                        <td class="lastTd">{{item.changeDate}}</td>
                    </tr>
                </table>
            </div>
            <!--修改记录-->
